<template>
  <div class="component_example">
    <h1>Vertical-Menu 菜单组件</h1>
    <h2>常用的操作</h2>
    <div class="item">
      <h4>基本用法</h4>
      <div class="item-content">
        <zoehis-vertical-menu v-model="selMenuVal" :data="menuData1"></zoehis-vertical-menu>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>data：数据源,为菜单的所有数据</li>
              <li>v-model的值为当前被选中的值，可以通过v-model设置值的初始化 类型是数据，如：{configId: "220201",menuName: "入科",menuUrl:""}，configId和menuName必填</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;zoehis-vertical-menu v-model=&quot;selMenuVal&quot; :data=&quot;menuData&quot;&gt;&lt;/zoehis-vertical-menu&gt;
           &lt;/template&gt;
           &lt;script&gt;
             export default{
               data: function () {
                 return {
                   selMenuVal: {},
                   menuData: [
                   {configId: &quot;11&quot;,menuName: &quot;床位管理&quot;,items:[
                   {configId: &quot;1101&quot;,menuName: &quot;床位导航&quot;,menuUrl:&quot;&quot;},
                   {configId: &quot;1102&quot;,menuName: &quot;床位管理&quot;,menuUrl:&quot;&quot;},
                   {configId: &quot;1103&quot;,menuName: &quot;床位等级费用维护&quot;,menuUrl:&quot;&quot;}]},
                   {configId: &quot;22&quot;,menuName: &quot;患者管理&quot;,items:[
                   {configId: &quot;2201&quot;,menuName: &quot;入科&quot;,menuUrl:&quot;&quot;},
                   {configId: &quot;2202&quot;,menuName: &quot;转科&quot;,menuUrl:&quot;&quot;}]}
                   ]
                 }
               }
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>

    <div class="item">
      <h4>无收缩的菜单</h4>
      <div class="item-content">
        <zoehis-vertical-menu v-model="selMenuVal" :data="menuData1" :noShrink="false"></zoehis-vertical-menu>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>noShrink: 是否启动菜单收缩功能，默认为true</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;zoehis-vertical-menu
             v-model=&quot;selMenuVal&quot; :data=&quot;menuData&quot; :noShrink=&quot;false&quot;&gt;&lt;/zoehis-vertical-menu&gt;
           &lt;/template&gt;
           &lt;script&gt;
             export default{
               data: function () {
                 return {
                   selMenuVal: {},
                   menuData: [
                   {configId: &quot;11&quot;,menuName: &quot;床位管理&quot;,items:[
                   {configId: &quot;1101&quot;,menuName: &quot;床位导航&quot;,menuUrl:&quot;&quot;},
                   {configId: &quot;1102&quot;,menuName: &quot;床位管理&quot;,menuUrl:&quot;&quot;},
                   {configId: &quot;1103&quot;,menuName: &quot;床位等级费用维护&quot;,menuUrl:&quot;&quot;}]},
                   {configId: &quot;22&quot;,menuName: &quot;患者管理&quot;,items:[
                   {configId: &quot;2201&quot;,menuName: &quot;入科&quot;,menuUrl:&quot;&quot;},
                   {configId: &quot;2202&quot;,menuName: &quot;转科&quot;,menuUrl:&quot;&quot;}]}
                   ]
                 }
               }
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>

    <div class="item">
      <h4>带图标的菜单</h4>
      <div class="item-content">
        <zoehis-vertical-menu v-model="selMenuVal" :data="menuData2"></zoehis-vertical-menu>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>要显示菜单图标，只需源数据data中返回图标字段iconUrl即可显示图标</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;zoehis-vertical-menu
              v-model=&quot;selMenuVal&quot; :data=&quot;menuData&quot; &gt;&lt;/zoehis-vertical-menu&gt;
            &lt;/template&gt;
            &lt;script&gt;
              export default{
                data: function () {
                  return {
                    selMenuVal: {},
                    menuData: [
                    {configId: &quot;11&quot;,menuName: &quot;床位管理&quot;,iconUrl: &quot;z_bedM_normal03&quot;,items:[
                    {configId: &quot;1&quot;,menuName: &quot;床位导航&quot;,iconUrl: &quot;z_bedN_normal&quot;,menuUrl:&quot;&quot;},
                    {configId: &quot;2&quot;,menuName: &quot;床位管理&quot;,iconUrl: &quot;z_bedM_normal03&quot;,menuUrl:&quot;&quot;},
                    {configId: &quot;3&quot;,menuName: &quot;床位等级费用维护&quot;,iconUrl: &quot;z_bedLM_normal&quot;,menuUrl:&quot;&quot;}]},
                    {configId: &quot;22&quot;,menuName: &quot;患者管理&quot;,iconUrl: &quot;z_patientM_normal02&quot;,items:[
                    {configId: &quot;4&quot;,menuName: &quot;入科&quot;,iconUrl:&quot;z_enrollment_normal&quot;,menuUrl:&quot;&quot;},
                    {configId: &quot;5&quot;,menuName: &quot;转科&quot;,iconUrl: &quot;z_transfer_normal03&quot;,menuUrl:&quot;&quot;}]}
                    ]
                  }
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>

    <div class="item">
      <h4>菜单点击事件</h4>
      <div class="item-content">
        <zoehis-vertical-menu ref="menuRef" v-model="selMenuVal" :data="menuData2"  @menu-click="menuClick"></zoehis-vertical-menu>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>@menu-click使用：在zoehis-vertical-menu绑定方法，该菜单栏被点击则触发该方法，该方法会返回对应的selMenuVal，以及对应的configId。</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;zoehis-vertical-menu
             v-model=&quot;selMenuVal&quot; :data=&quot;menuData&quot; @menu-click=&quot;menuClick&quot;&gt;&lt;/zoehis-vertical-menu&gt;
           &lt;/template&gt;
           &lt;script&gt;
             export default{
               data: function () {
                 return {
                   selMenuVal: {},
                   menuData: [
                   {configId: &quot;11&quot;,menuName: &quot;床位管理&quot;,iconUrl: &quot;z_bedM_normal03&quot;,items:[
                   {configId: &quot;1&quot;,menuName: &quot;床位导航&quot;,iconUrl: &quot;z_bedN_normal&quot;,menuUrl:&quot;&quot;},
                   {configId: &quot;2&quot;,menuName: &quot;床位管理&quot;,iconUrl: &quot;z_bedM_normal03&quot;,menuUrl:&quot;&quot;},
                   {configId: &quot;3&quot;,menuName: &quot;床位等级费用维护&quot;,iconUrl: &quot;z_bedLM_normal&quot;,menuUrl:&quot;&quot;}]},
                   {configId: &quot;22&quot;,menuName: &quot;患者管理&quot;,iconUrl: &quot;z_patientM_normal02&quot;,items:[
                   {configId: &quot;4&quot;,menuName: &quot;入科&quot;,iconUrl:&quot;z_enrollment_normal&quot;,menuUrl:&quot;&quot;},
                   {configId: &quot;5&quot;,menuName: &quot;转科&quot;,iconUrl: &quot;z_transfer_normal03&quot;,menuUrl:&quot;&quot;}]}
                   ]
                 }
               },
               methods:{
                 menuClick:function(val,configId){
                   console.log(val,configId)
                 }
               }
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>

    <div class="item">
      <h4>获取/设置菜单栏伸缩属性</h4>
      <div class="item-content">
        <zoehis-vertical-menu ref="menuRef" v-model="selMenuVal" :data="menuData2"  @menu-click="menuClick" @fix-change="fixChange"></zoehis-vertical-menu>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>使用ref属性,在父组件里用this.$refs.menuRef获取子组件的对象，再进行调用</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;zoehis-vertical-menu
              ref=&quot;menuRef&quot; v-model=&quot;selMenuVal&quot; :data=&quot;menuData&quot;&gt;&lt;/zoehis-vertical-menu&gt;
            &lt;/template&gt;
            &lt;script&gt;
              export default{
                data: function () {
                  return {
                    selMenuVal: {},
                    menuData: [
                    {configId: &quot;11&quot;,menuName: &quot;床位管理&quot;,iconUrl: &quot;z_bedM_normal03&quot;,items:[
                    {configId: &quot;1&quot;,menuName: &quot;床位导航&quot;,iconUrl: &quot;z_bedN_normal&quot;,menuUrl:&quot;&quot;},
                    {configId: &quot;2&quot;,menuName: &quot;床位管理&quot;,iconUrl: &quot;z_bedM_normal03&quot;,menuUrl:&quot;&quot;},
                    {configId: &quot;3&quot;,menuName: &quot;床位等级费用维护&quot;,iconUrl: &quot;z_bedLM_normal&quot;,menuUrl:&quot;&quot;}]},
                    {configId: &quot;22&quot;,menuName: &quot;患者管理&quot;,iconUrl: &quot;z_patientM_normal02&quot;,items:[
                    {configId: &quot;4&quot;,menuName: &quot;入科&quot;,iconUrl:&quot;z_enrollment_normal&quot;,menuUrl:&quot;&quot;},
                    {configId: &quot;5&quot;,menuName: &quot;转科&quot;,iconUrl: &quot;z_transfer_normal03&quot;,menuUrl:&quot;&quot;}]}
                    ]
                  }
                },
                methods:{
                  clickGetFixflag:function(){
                    let _flag = this.$refs.menuRef.getFixflag();
                    console.log(_flag)
                  },
                  setMenuFixflag:function(flag){
                    this.$refs.menuRef.setFixflag(flag);
                  },
                },
                mounted:function() {
                  this.setMenuFixflag(true);//设置菜单栏收缩或者展开，true为展开，false为收缩
                  this.clickGetFixflag();//获取菜单栏伸缩值
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>

    <div class="item">
      <h4>属性</h4>
      <zoehis-table keycode="code" :data="propData" :configs="demoConfig1"></zoehis-table>
      <h4>方法</h4>
      <zoehis-table keycode="code" :data="functionData" :configs="demoConfig1"></zoehis-table>
      <h4>事件</h4>
      <zoehis-table keycode="code" :data="eventData" :configs="demoConfig1"></zoehis-table>
    </div>
  </div>
</template>

<script type="text/babel">
  export default {
    data(){
      return {
        menuData1:[],
        menuData2:[],
        selMenuVal:{configId: "220102",iconUrl: "z_transfer_normal03",menuName: "转科",menuUrl:""},
        fixFlag:'',
        propData:[], //属性说明表格数据
        functionData:[], //方法说明表格数据
        eventData:[], //事件说明表格数据
        demoConfig1:[],
      }
    },
    methods:{
      menuClick: function (obj,value) {
//        console.log('我是menuClick事件',obj,value);
      },
      getFixflag: function () {
        this.fixFlag=this.$refs.menuRef.getFixflag();
//        console.log('菜单栏是否伸缩',this.fixFlag);
      },
      setFixflag: function (flag) {
        this.$refs.menuRef.setFixflag(flag);
      },
      handleToggle(e){
        $(e.target).next().find("pre code").toggle()
      },
      fixChange(flag){
//        console.log(11,flag)
      },
    },
    mounted:function() { //用于高亮显示代码
      this.menuData1=[
        {configId: "11",menuName: "床位管理",items:[
          {configId: "1101",menuName: "床位导航",menuUrl:""},
          {configId: "1102",menuName: "床位管理",menuUrl:""},
          {configId: "1103",menuName: "床位等级费用维护",menuUrl:""}]},
        {configId: "22",menuName: "患者管理",items:[
          {configId: "2201",menuName: "入科",menuUrl:""},
          {configId: "2202",menuName: "转科",menuUrl:""}]}
      ];
      this.menuData2=[
        {configId: "11",iconUrl: "z_bedM_normal03",menuName: "床位管理", items:[
            {configId: "1",iconUrl: "z_bedN_normal",menuName: "床位导航",menuUrl:""},
            {configId: "2",iconUrl: "z_bedM_normal03",menuName: "床位管理",menuUrl:""},
            {configId: "3",iconUrl: "z_bedLM_normal",menuName: "床位等级费用维护",menuUrl:""}]},
        {configId: "22",iconUrl: "z_patientM_normal02",menuName: "患者管理", items:[
            {configId: "4",iconUrl:"z_enrollment_normal",menuName: "入科",menuUrl:""},
            {configId: "5",iconUrl: "z_transfer_normal03",menuName: "转科",menuUrl:""}]}
      ];
      this.demoConfig1=[
        {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"parameter",dataItemName:"参数",filterType:"",itemWidth:"120px",fixedFlag:""},
        {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"left",patientItemName:"",dataItemCode:"explain",dataItemName:"说明",filterType:"",itemWidth:"480px",fixedFlag:""},
        {sortNo:2,validFlag:"1", dataItemSort:"1", dataTextAlign:"left",patientItemName:"",dataItemCode:"type",dataItemName:"类型",filterType:"",itemWidth:"240px",fixedFlag:""},
        {sortNo:3,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"optional",dataItemName:"可选值",filterType:"",itemWidth:"",fixedFlag:""},
        {sortNo:4,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"defaultVal",dataItemName:"默认",filterType:"",itemWidth:"",fixedFlag:""},
      ];
      this.propData=[
        {code:1,parameter:"noShrink", explain:"可选属性，是否启动菜单收缩功能", type:"Boolean",optional:"true/false",defaultVal:"true"}
      ];
      this.functionData=[
        {code:1,parameter:"getFixflag()", explain:"获取菜单栏伸缩值的方法", type:"	无传入参数",optional:"-",defaultVal:"-"},
        {code:2,parameter:"setFixflag()", explain:"设置菜单栏伸缩的方法", type:"	传入参数：要设置的值",optional:"true/false",defaultVal:"true"}
      ];
      this.eventData=[
        {code:1,parameter:"@menu-click", explain:"在zoehis-vertical-menu绑定方法，该菜单栏 被点击则触发该方法，该方法会返回对应的selMenuVal，以及对应的configId", type:"	返回参数：1.被点击菜单的selMenuVal值；2.对应的configId值",optional:"-",defaultVal:"-"},
        {code:2,parameter:"@fix-change", explain:"菜单栏伸展收缩时的触发事件", type:"	返回参数说明：参数1：菜单栏伸展收缩参数true/false",optional:"true/false",defaultVal:"true"}
      ];
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>
<style lang="scss" >
  .component_example .zoehis_table{
    .com_table__main{border-bottom: none;
      .com_table__body td{white-space: normal;}
    }
  }
  .component_example .item:last-child{margin-bottom: 20px;border-bottom: none;}
</style>
<style scoped>
  .item-content{  height: 300px;;  }
  ul{margin: 0 0 0 20px; padding: 0;}
  .component_example .clearflex{display: block;}
  .component_example .item-toggle{cursor: pointer; background: #8fcae5; text-align: center; font-weight: bold;}
  .component_example .item-desc{width: 100%;max-width: 100%;}
  .component_example .item-code{ width: 100%;max-width: 100%;}
  .component_example .item-code pre code{display: none;}
</style>



