<template>
  <div class="component_example">
    <h1>DateEditor 手动输入的日期控件</h1>
    <h2>常用的按钮操作</h2>
    <div class="item">
      <h4>基本用法</h4>
      <div class="item-content" >
        <zoehis-date-editor v-model="dateVal"></zoehis-date-editor>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-date-editor v-model=&quot;dateVal&quot;&gt;&lt;/zoehis-date-editor&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          基本用法 什么参数都不用  就一个v-model属性
        </div>
      </div>
    </div>
    <div class="item">
      <h4>各参数的用法</h4>
      <div class="item-content" >
        <zoehis-date-editor v-model="dateVal2" @save="saveTime" :nowtime="false" :autocheck="false" value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd HH:mm" inputformat="yyyy-MM-dd" initfocus="month" endfocus="hour"
        ></zoehis-date-editor>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-date-editor v-model=&quot;dateVal2&quot; value-format=&quot;yyyy-MM-dd HH:mm:ss&quot; :nowtime=&quot;false&quot; :autocheck=&quot;false&quot;
            format=&quot;yyyy-MM-dd HH:mm&quot; inputformat=&quot;yyyy-MM-dd&quot; initfocus=&quot;month&quot; endfocus=&quot;hour&quot;
            &gt;&lt;/zoehis-date-editor&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          yyyy-MM-dd HH:mm:ss 注意要使用大写的 <strong>HH</strong>）；<strong>小写的h是12小时制的</strong> <br>
          value-format：对v-model数据类型的设置 详细见下表 <br>
          format:时间弹窗要显示的格式，比如设置 yyyy-MM-dd 那么就代表只能手工录入 年月日 <br>
          inputformat:跟format属性关联， 若没有设置该属性， 就继承format属性的值。用于显示输入框的值的格式 <br>
          initfocus：弹窗打开时，焦点默认定位到哪个  详细见下表 <br>
          endfocus：要到哪个输入框时就触发 tab-end或者 save事件 <br>
          nowtime：若v-model为空时，弹窗是否要显示当前时间 默认值为：true显示<br>
          autocheck：自动校验输入的值并跳转到下一个输入框 默认值为：true显示<br>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>显示时间范围</h4>
      <div class="item-content" >
        <zoehis-date-editor v-model="dateVal3" mode="range" @save="saveTime" :nowtime="false" value-format="yyyy-MM-dd HH:mm:ss"
                            format="yyyy-MM-dd HH:mm" inputformat="yyyy-MM-dd"></zoehis-date-editor>
      </div>
      <div class="item-detail">
        <div class="item-code">
          <pre><code>
            &lt;zoehis-date-editor v-model=&quot;dateVal3&quot; mode="range" @save="saveTime" :nowtime="false" value-format=&quot;yyyy-MM-dd HH:mm:ss&quot;
            format=&quot;yyyy-MM-dd HH:mm&quot; inputformat=&quot;yyyy-MM-dd&quot;&gt;&lt;/zoehis-date-editor&gt;
          </code></pre>
        </div>
        <div class="item-desc">
          mode:日期控件的模式（正常和显示范围）
        </div>
      </div>
    </div>
    <div class="item">
      <h4>属性</h4>
      <table width="100%">
        <thead>
        <tr>
          <th>参数</th><th>说明</th><th>类型</th><th>可选值</th><th>默认</th>
        </tr>
        </thead>
        <tbody>
        <tr><td>v-model='value'</td><td>当前绑定日期的值</td><td>2019-1-1 / 国际化时间 / 时间戳</td><td>-</td><td>国际化时间</td></tr>
        <tr><td>mode</td><td>日期控件的模式（正常和显示范围,显示范围时v-model传个数组）</td><td>string</td><td>normal/range</td><td>normal</td></tr>
        <tr><td>value-format</td><td>对v-model数据类型的设置。1.空"" 代表国际化格式  2.timestamp:时间戳  3.yyyy-MM-dd HH:mm:ss / yyyy-MM-dd</td><td>string</td><td>""/timestamp/yyyy-MM-dd..</td><td>-</td></tr>
        <tr><td>format</td><td>时间弹窗显示的格式，注意 设置什么就对应显示什么</td><td>String</td><td>yyyy-MM-dd/yyyy-MM-dd HH:mm:ss 等</td><td>yyyy-MM-dd HH:mm:ss</td></tr>
        <tr><td>inputformat</td><td>输入框显示的格式，注意 默认为空，跟随format属性</td><td>String</td><td>yyyy-MM-dd/yyyy-MM-dd HH:mm:ss 等</td><td>-</td></tr>
        <tr><td>initfocus</td><td>弹窗打开时，焦点默认定位到哪个，默认值为:year(定位到年)</td><td>String</td><td>year/month/date/hour/minute/seconds</td><td>year</td></tr>
        <tr><td>endfocus</td><td>按tab/enter键，要到哪个输入框时就触发 tab-end或者 save事件</td><td>String</td><td>year/month/date/hour/minute/seconds</td><td>seconds</td></tr>
        <tr><td>disabled</td><td>是否禁用状态</td><td>boolean</td><td>true/false</td><td>false</td></tr>
        <tr><td>nowtime</td><td>若v-model为空时，弹窗是否要显示当前时间 默认值为：true显示</td><td>Boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>autocheck</td><td>自动校验输入的值并跳转到下一个输入框 默认值为：true显示</td><td>Boolean</td><td>true/false</td><td>true</td></tr>
        <tr><td>width</td><td>输入框的宽度,支持百分百比，auto</td><td>number/string</td><td>-</td><td>180</td></tr>
        <tr><td>focus()</td><td>焦点定位的方法</td><td>无传入参数</td><td>-</td><td>-</td></tr>
        <tr><td>hide()</td><td>隐藏弹窗的方法</td><td>无传入参数</td><td>-</td><td>-</td></tr>
        <tr><td>@focus</td><td>输入框焦点定位的事件</td><td>返回参数：无</td><td>-</td><td>-</td></tr>
        <tr><td>@clear</td><td>按清除键的回调事件(不管值有没有变化，只要按了清除键就会触发)</td><td>返回参数：1.无</td><td>-</td><td>-</td></tr>
        <tr><td>@change</td><td>数值改变事件（只有改变的时候才会触发）</td><td>返回参数：1.当前选中的时间（格式根据valueFormat属性返回），2.type,用于标识是哪个事件引起的 （值：confirm/enter）</td><td>-</td><td>-</td></tr>
        <tr><td>@save</td><td>按enter键/确认键的回调事件</td><td>返回参数：1.当前选中的时间（格式根据valueFormat属性返回），2.type,用于标识是哪个事件引起的 （值：confirm/enter）</td><td>-</td><td>-</td></tr>
        <tr><td>@tab-end</td><td>按tab键焦点要离开当前组件时触发的事件，注意按tab键焦点会先在组件上的许多输入框上跳转（此时不会触发tab-end事件），则到没有地方跳时或者 endfocus属性定义的时 才会触发tab-end</td><td>返回参数：1.无</td><td>-</td><td>-</td></tr>
        <tr><td>@after-prev</td><td>逆向快捷键回调事件 ctrl+tab / ctrl+enter</td><td>返回参数：1.无</td><td>-</td><td>-</td></tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'DateEditor',
    data() {
      return {
        dateVal:new Date(),
        dateVal2:"2018-10-26 10:10:10",
        dateVal3:[new Date(),new Date()],
      }
    },
    watch:{
      dateVal2(val){
        console.log("dateVal2的值：",val)
      }
    },
    methods:{
      changeBtnCt:function(evt){
        this.defaultName="新的图标内容";
        this.featuresName="新的图标内容";
        ++this.keyboard;
      },
      myClickEnterEve:function(evt){
        alert("这是点击事件/或者enter事件");
      },
      //保存时间
      saveTime(value,type){
        console.log('【保存时间====】',value,type);
      },
    },
    mounted:function() { //用于高亮显示代码
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



