<template>
  <div class="zoehis_date_editor"
       :style="{width:width+(!isPercent?'px':'')}"
  >
    <zoehis-input
      ref="reference" readonly :clearable="false" showErrorTip :error-tip="timeErrorTip"
      @focus="focusEvn" :width="mode==='range'?'350px':'100%'" :disabled="disabled"
      v-model="displayValue" :ime-mode="imeMode">
    </zoehis-input>
    <zoehis-date-editor-box
      ref="popper"
      :initfocus="initfocus"
      :endfocus="endfocus"
      :nowtime="nowtime"
      :value="currentValue"
      :format="format"
      :mode="mode"
      :autocheck="autocheck"
      v-if="visible"
      @save="saveEvn"
      @box-blur="boxBlurEvn"
      @tab-end="tabEndEvn"
      @after-prev="afterPrevEvn" :ime-mode="imeMode"
      @change-tip="changeErrorTip"
    ></zoehis-date-editor-box>
  </div>
</template>
<script type="text/babel">
  import Emitter from '../../../src/mixins/emitter';
  import ZoehisDateEditorBox from "./date-editor-box.vue";
  import { isDate } from '../../../src/utils/util';
  import dateUtil from '../../../src/utils/date';
  import {trim} from '../../../src/utils/util.js';
  export default {
    name: 'ZoehisDateEditor',
    componentName: 'ZoehisDateEditor',
    components:{ZoehisDateEditorBox},
    mixins: [Emitter],
    data(){
      return {
        displayVal:"",
        visible:false,
        currentValue:null,
        isPercent:false,
        timeErrorTip:''
      }
    },
    props:{
      disabled:Boolean,
      value: {
        required: true
      },
      //时间弹窗显示的格式
      format: {
        type:String,default:"yyyy-MM-dd HH:mm:ss"
      },
      valueFormat:{//v-model 值的格式 1.空"" 代表国际化格式  2.timestamp:时间戳  3.yyyy-MM-dd HH:mm:ss / yyyy-MM-dd 等
        type:String,default:""
      },
      //输入框input 显示的格式 默认为空，与format一致
      inputformat: {
        type:String,default:""
      },
      mode: {//日期控件的模式（正常和显示范围）
        type:String,
        default:'normal'
      },
      //显示弹窗时默认选中哪一个  year/month/date/hour/minute/seconds
      initfocus:{
        type:String,default:"year"
      },
      endfocus:{
          type:String,
          // default:"seconds"
          default() {
              if(this.mode === 'range') return "seconds_end";
              return "seconds";
          }
      },//按 enter、tab 时，到哪个输入框就跳出来
      //若v-model为空时，弹窗是否要显示当前时间 默认 true显示
      nowtime:{
        type:Boolean,default:true
      },
      //输入内容后 是否自动校验并跳转到下一个输入框 默认 true显示
      autocheck:{
        type:Boolean,default:true
      },
      imeMode:{   //原始属性 ime-mode （输入法引擎）
        type:String,
        default:"disabled"
      },
      width: {      //文本的宽度
        type:[Number,String],
        default: 180
      },

    },
    watch:{
      //监听v-model传进来的值
      value:{
        immediate:true,//第一次的时候会立即执行，而不是value变化的时候才执行
        handler(val){
          if(this.mode === 'range'){
              if(!val || val.length === 0){
                  this.currentValue =''
              }else {
                  let startDate = isDate(val[0]) ? new Date(val[0]) : val[0];
                  let endDate = isDate(val[1]) ? new Date(val[1]) : val[1];
                  this.currentValue = [startDate,endDate];
              }
          }else{
              this.currentValue = isDate(val) ? new Date(val) : val; //国际化单位
          }
        }
      },
      visible(val){
        if(!val){

        }else {
          this.$nextTick(()=>{
            this.broadcast('ZoehisDateEditorBox', 'updatePopper'); //更新下拉框的位置(即创建下拉)
          });
        }
      }
    },
    methods:{
      //逆向快捷键响应事件 ctrl+tab  /  ctrl+enter
      afterPrevEvn(){
        this.$emit("after-prev");
      },
      focusEvn(){
        if(this.disabled) return;
        this.visible = true;
        this.$nextTick(()=>{
          this.$refs.popper.selectInput(this.initfocus);
        });
        this.$emit("focus")
      },
      //弹窗焦点失去事件
      boxBlurEvn(){
        this.visible = false;
        this.timeErrorTip = '';
      },
      //tab 到最后响应时的事件
      tabEndEvn(){
        this.$emit("tab-end");
        this.visible = false;
      },
      //时间更改变事件
      //date 当前返回的时间（国际化）
      saveEvn(date,type){
        this.timeErrorTip='';
        //清除按钮事件
        if(type=="clear"){
          //原先的值不为空
          if(this.currentValue){
            this.$emit("input","",type);
            this.$emit("change","",type);//改变事件
          }
          this.$emit("clear");
          this.visible = false;
          return;
        }

        //确认事件时
        //都为空 ==》代表没有改变
        if(!date && !this.currentValue){
          this.$emit("save","",type);
          this.visible = false;
          return;
        }

        //当前时间为空 此时 this.currentValue肯定不为空（上面已经拦截了），此时值改变了
        if(!date){
          this.$emit("input","",type);
          this.$emit("change","",type);
          this.$emit("save","",type);
          this.visible = false;
          return;
        }

        //接下来 date肯定不为空
        let val = this.mode === 'range'? Object.assign([],date) : date;
        //判断开始时间和结束时间大小
        if(this.mode === 'range'){
            if(this.showTimeErrorTip(val[0],val[1])){
                this.timeErrorTip = '开始时间不能大于结束时间';
                return;
            }
        }
        //先将 date格式转化一下
        if(!this.valueFormat){
          val=date;
        }else if(this.valueFormat=="timestamp"){
          if(this.mode === 'range'){
              val[0] = date[0].getTime();
              val[1] = date[1].getTime();
          }else{
              val = date.getTime();
          }
        }else {//格式化
          if(this.mode === 'range'){
              val[0] = dateUtil.format(date[0],this.valueFormat||"yyyy-MM-dd");
              val[1] = dateUtil.format(date[1],this.valueFormat||"yyyy-MM-dd");
          }else{
              val = dateUtil.format(date,this.valueFormat||"yyyy-MM-dd");
          }
        }

        //相等 则代表没有改变
        if(this.mode === 'range'){
            if(this.currentValue && dateUtil.format(this.currentValue[0],"yyyy-MM-dd HH:mm:ss")==dateUtil.format(date[0],"yyyy-MM-dd HH:mm:ss") && dateUtil.format(this.currentValue[1],"yyyy-MM-dd HH:mm:ss")==dateUtil.format(date[1],"yyyy-MM-dd HH:mm:ss")){
                this.$emit("save",val,type);
                this.visible = false;
                return;
            }
        }else{
            if(this.currentValue && dateUtil.format(this.currentValue,"yyyy-MM-dd HH:mm:ss")==dateUtil.format(date,"yyyy-MM-dd HH:mm:ss")){
                this.$emit("save",val,type);
                this.visible = false;
                return;
            }
        }

        this.$emit("input",val,type);
        this.$emit("change",val,type);
        this.$emit("save",val,type);
        this.visible = false;
      },
      //是否显示时间错误提示
      showTimeErrorTip(startDate,endDate){
          let year = startDate.getFullYear();
          let month = startDate.getMonth()+1;
          let date = startDate.getDate();
          let hour = startDate.getHours();
          let minute = startDate.getMinutes();
          let seconds = startDate.getSeconds();

          let year_end = endDate.getFullYear();
          let month_end = endDate.getMonth()+1;
          let date_end = endDate.getDate();
          let hour_end = endDate.getHours();
          let minute_end = endDate.getMinutes();
          let seconds_end = endDate.getSeconds();
          if(year>year_end) {
              return true;
          }else if(year==year_end && month>month_end) {
              return true;
          }else if(year==year_end && month==month_end && date>date_end) {
              return true;
          }else if(year==year_end && month==month_end && date==date_end && hour>hour_end) {
              return true;
          }else if(year==year_end && month==month_end && date==date_end && hour==hour_end && minute>minute_end) {
              return true;
          }else if(year==year_end && month==month_end && date==date_end && hour==hour_end && minute==minute_end && seconds>seconds_end) {
              return true;
          }
          return false;
      },
      //改变时间错误提示信息 供子组件调用
      changeErrorTip(flag){
        this.timeErrorTip = flag=='1'? '开始时间不能大于结束时间':'';
      },
      //外部调用的focus方法
      focus(){
        this.$refs.reference.focus();
      },
      //外部调用的hide 隐藏弹窗方法
      hide(){
        this.visible = false;
      },
      //判断是否是数字
      isWidthNumber(val){
        var re = /^[0-9]+\.?[0-9]*$/; //匹配是否是数字
        return re.test(val);
      },
    },
    computed:{
      displayValue:{
        //获取显示的值
        get(){
          const value = this.mode === 'range'? Object.assign([],this.currentValue) : this.currentValue;
          if(this.mode === 'range'){
              if(value.length === 0) return "";
              return dateUtil.format(value[0],this.inputformat||this.format||"yyyy-MM-dd") + '-' + dateUtil.format(value[1],this.inputformat||this.format||"yyyy-MM-dd");
          }else{
              if(!value) return "";
              return dateUtil.format(value,this.inputformat||this.format||"yyyy-MM-dd");
          }
        },
        set(value){
          //不为空
//          if(value){
//            const type = this.type;
//            const parser = (
//              TYPE_VALUE_RESOLVER_MAP[type] ||
//              TYPE_VALUE_RESOLVER_MAP['default']
//            ).parser;
//            //将输入框的值转化为标准时间格式
//            const parserValue = parser(value,this.format || DEFAULT_FORMATS[type]);
//            //如果转化的值不为空且picker存在
//            if(parserValue){
//              //保持原始的年月日
//              parserValue.setFullYear(this.currentValue.getFullYear());
//              parserValue.setMonth(this.currentValue.getMonth(),this.currentValue.getDate());
//              if(this.picker){//picker 存在的情况
//                let t1 = isDate(parserValue) ? new Date(parserValue).getTime() : parserValue; //时间戳
//                let t2 = isDate(this.picker.value) ? new Date(this.picker.value).getTime() : this.picker.value; //时间戳
//                if(t1==t2){
//                  this.$refs.reference.setValue(this.displayValue);
//                }else {
//                  this.picker.value = parserValue;
//                }
//              }else {//picker不存在的情况直接更新input里的值
//                this.$emit('input', this.standardtype?parserValue:parserValue.getTime());
//              }
//            }
//
//          }else {//为空
//            this.$emit('input', value);
//            this.picker.value = value;
//          }
        }
      }
    },
    created(){
      //宽度是否是百分比
      this.isPercent = !this.isWidthNumber(this.width);
    }
  };
</script>



