<template>
  <div class="component_example">
    <h1>Func 公共方法</h1>
    <p>目前只有一个导出excel的公共方法 tableToExcel</p>
    <h2>常用的操作</h2>
    <div class="item">
      <h4>基本用法</h4>
      <div class="item-content">
        <zoehis-func ref="zoehisFunc"></zoehis-func>
        <zoehis-button @clickenter="handleDownloadEn">导出</zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li><strong>tableToExcel(excelData,fileName,worksheet,tableConfig)</strong>:导出excel方法。</li>
              <li>1.excelData(必填):要导出表格的数据；</li>
              <li>2.fileName(非必填):文档名称；</li>
              <li>3.worksheet(非必填)：文档里面tab的名称；</li>
              <li>4.tableConfig(必填):自定义表头的信息。(具体例子看代码)</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;zoehis-func ref=&quot;zoehisFunc&quot; &gt;&lt;/zoehis-func&gt;
            &lt;script&gt;
            /**
            * 导出
            */
            handleDownloadEn(){
              /** @param {Array} excelData: 要导出的表格的数据（此处为必填）
                * @param {String} fileName: excel表格的名称
                * @param {String} worksheet: excel表格里面的tab名称
                * @param {Array} tableConfig: 自定义的表格头部（此处为必填）
                **/ 
              //表数据
              let excelData = [];
              for(let i=0;i&lt;1000; i++){
                excelData.push({
                  text1:i+&quot;测试测试测试测试测试测试测试测试列1&quot;,
                  text2:i+&quot;测试测试测试测试测试测试测试测试列2&quot;,
                  text3:i+&quot;测试测试测试测试测试测试测试测试列3&quot;,
                  text4:i+&quot;测试测试测试测试测试测试测试测试列4&quot;,
                  text5:i+&quot;测试测试测试测试测试测试测试测试列5&quot;,
                });
              }
              let fileName = &quot;这个是自定义的文件名&quot;;
              let worksheet = &quot;这个是自定义的excel里tab名&quot;;
              //表头
              let tableConfig = [
                { dataItemCode: &quot;text1&quot;, dataItemName: &quot;列名测试1&quot;},
                { dataItemCode: &quot;text2&quot;, dataItemName: &quot;列名测试2&quot;},
                { dataItemCode: &quot;text3&quot;, dataItemName: &quot;列名测试3&quot;},
                { dataItemCode: &quot;text4&quot;, dataItemName: &quot;列名测试4&quot;},
                { dataItemCode: &quot;text5&quot;, dataItemName: &quot;列名测试5&quot;},
              ];
              this.$refs.zoehisFunc.tableToExcel(excelData,fileName,worksheet,tableConfig);
            },
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>方法</h4>
      <table width="100%">
        <thead>
        <tr>
          <th>参数</th><th>说明</th><th>类型</th><th>可选值</th><th>默认</th>
        </tr>
        </thead>
        <tbody>
        <tr>
            <td>tableToExcel(excelData,fileName,worksheet,tableConfig)</td>
            <td>导出excel方法。</td>
            <td>Function</td>
            <td>参数说明：1.excelData(必填):要导出表格的数据；2.fileName(非必填):文档名称；3.worksheet(非必填)：文档里面tab的名称；4.tableConfig(必填):表头的信息</td>
            <td>-</td>
          </tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'Func',
    data () {
      return {
    
      }
    },
    methods:{
      /**
       * 导出
       */
      handleDownloadEn(){
        /** @param {Array} excelData: 要导出的表格的数据（此处为必填）
          * @param {String} fileName: excel表格的名称
          * @param {String} worksheet: excel表格里面的tab名称
          * @param {Array} tableConfig: 自定义的表格头部（此处为必填）
          **/ 
        //表数据
        let excelData = [];
        for(let i=0;i<1000; i++){
          excelData.push({
            text1:i+"测试测试测试测试测试测试测试测试列1",
            text2:i+"测试测试测试测试测试测试测试测试列2",
            text3:i+"测试测试测试测试测试测试测试测试列3",
            text4:i+"测试测试测试测试测试测试测试测试列4",
            text5:i+"测试测试测试测试测试测试测试测试列5",
          });
        }
        let fileName = "这个是自定义的文件名";
        let worksheet = "这个是自定义的excel里tab名";
        //表头
        let tableConfig = [
          { dataItemCode: "text1", dataItemName: "列名测试1"},
          { dataItemCode: "text2", dataItemName: "列名测试2"},
          { dataItemCode: "text3", dataItemName: "列名测试3"},
          { dataItemCode: "text4", dataItemName: "列名测试4"},
          { dataItemCode: "text5", dataItemName: "列名测试5"}
        ];
        this.$refs.zoehisFunc.tableToExcel(excelData,fileName,worksheet,tableConfig);
      },

      handleToggle(e) {
        $(e.target)
          .next()
          .find("pre code")
          .toggle();
      }
    },
    mounted:function() { //用于高亮显示代码
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>
<style scoped>
.component_example .zoehis_table {
  height: 300px;
}
ul {
  margin: 0 0 0 20px;
  padding: 0;
}
.component_example .clearflex {
  display: block;
}
.component_example .item-toggle {
  cursor: pointer;
  background: #8fcae5;
  text-align: center;
  font-weight: bold;
}
.component_example .item-desc {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code pre code {
  display: none;
}
</style>



