<template>
  <div
    class="zoehis_progress"
    :class="[
      'zoehis_progress_line',
      status ? 'is_' + status : '',
      {
        'zoehis_progress_without_text': !showText,
        'zoehis_progress_text_inside': textInside,
      }
    ]"
    role="progressbar"
    :aria-valuenow="percentage"
    aria-valuemin="0"
    aria-valuemax="100"
  >
    <div class="zoehis_progress_bar">
      <div class="zoehis_progress_bar__outer" :style="{height: strokeWidth + 'px'}">
        <div class="zoehis_progress_bar__inner" :style="barStyle">
          <div class="zoehis_progress_bar__innerText" v-if="showText && textInside">{{percentage}}%</div>
        </div>
      </div>
    </div>
    <div
      class="zoehis_progress__text"
      v-if="showText && !textInside"
      :style="{fontSize: progressTextSize + 'px'}"
    >{{percentage}}%</div>
  </div>
</template>
<script type="text/babel">
  export default {
    name: 'ZoehisProgress',
    props: {
      percentage: {
        type: Number,
        default: 0,
        required: true,
        validator: val => val >= 0 && val <= 100
      },
      status: {
        type: String
      },
      strokeWidth: { //宽度
        type: Number,
        default: 12
      },
      textInside: {
        type: Boolean,
        default: false
      },
      showText: {
        type: Boolean,
        default: true
      },
      color: {
        type: String,
        default: ''
      }
    },
    computed: {
      barStyle() {
        const style = {};
        style.width = this.percentage + '%';
        style.backgroundColor = this.color;
        return style;
      },
      progressTextSize() {
        return 10 + this.strokeWidth * 0.23;
      }
    }
  };
</script>


