<template>
  <div
    :style="{width:width}"
    :class="[
      'zoehis_input_number',
      { 'is_disabled': disabled },
      { 'is_without_controls': !controls },
      { 'is_controls_right': controlsAtRight }
    ]">
    <span
      class="zoehis_input_number__decrease"
      role="button"
      v-if="controls"
      @click="decrease"
      :class="{'is_disabled': minDisabled}"
      @keydown.enter="decrease">
      <i :class="`zoehis_input_number__icon zoeIconfont ${controlsAtRight ? 'z_dropD_form' : 'z_exitES_normal'}`"></i>
    </span>
    <span
      class="zoehis_input_number__increase"
      role="button"
      v-if="controls"
      @click="increase"
      :class="{'is_disabled': maxDisabled}"
      @keydown.enter="increase">
      <i :class="`zoehis_input_number__icon zoeIconfont ${controlsAtRight ? 'z_dropU_form' : 'z_add_normal'}`"></i>
    </span>
    <input
      class="zoehis_input_number__inner"
      :class="{ 'is_disabled': disabled }"
      type="text" ref="input"
      :disabled="disabled"
      :value="currentInputValue"
      @keydown.up.prevent="increase"
      @keydown.down.prevent="decrease"
      @blur="handleBlur"
      @focus="handleFocus"
      @change="handleInputChange"
      />
  </div>
</template>

<script>
  export default {
    name:"zoehisInputNumber",
    componentName:"zoehisInputNumber",
    data(){
      return {
        currentValue: 0
      }
    },
    computed: {
      minDisabled() {
        return this._decrease(this.value, this.step) < this.min;
      },
      maxDisabled() {
        return this._increase(this.value, this.step) > this.max;
      },
      /**
       * 当前输入框显示的值,根据precision 处理一下小数点
       */
      currentInputValue() {
        const currentValue = this.currentValue;
        if (typeof currentValue === 'number' && this.precision !== undefined) {
          return currentValue.toFixed(this.precision);
        } else {
          return currentValue;
        }
      },
      /**
       * 是否显示 按钮在右侧的模式
       */
      controlsAtRight() {
        return this.controlsPosition === 'right';
      },
      /**
       * 获取当前的小数点位数
       */
      numPrecision() {
        const { value, step, getPrecision, precision } = this;
        const stepPrecision = getPrecision(step);
        if (precision !== undefined) {
          //设置的位数 precision 小于 step返回的位数，则以precision为准
          if (stepPrecision > precision) {
            console.warn('[zoehis Warn][InputNumber]precision should not be less than the decimal places of step');
          }
          return precision;
        } else {
          return Math.max(getPrecision(value), stepPrecision);
        }
      },
    },
    props:{
      step: { //每次增加的数值
        type: Number,
        default: 1
      },
      controlsPosition: {
        type: String,
        default: ''
      },
      disabled: Boolean,
      max: {
        type: Number,
        default: Infinity
      },
      min: {
        type: Number,
        default: -Infinity
      },
      controls: {
        type: Boolean,
        default: true
      },
      value: {type:[Number,String],default:""},
      precision: {
        type: Number,
        validator(val) {
          return val >= 0 && val === parseInt(val, 10);
        }
      },
      width:{type: String, default:''}
    },
    watch: {
      value: {
        immediate: true,
        handler(value) {
          let newVal = value === undefined ? value : Number(value);
          if (newVal !== undefined) {
            if (isNaN(newVal)) {
              return;
            }
            if (this.precision !== undefined) {
              newVal = this.toPrecision(newVal, this.precision);
            }
          }
          if (newVal >= this.max) newVal = this.max;
          if (newVal <= this.min) newVal = this.min;
          this.currentValue = newVal;
          this.$emit('input', newVal);
        }
      }
    },
    methods:{
      //==========================
      //==供外部调用方法 start=====
      //==========================
      /**
       * 焦点聚集方法
       */
      focus(){
        this.$refs.input.focus();
      },
      //==========================
      //==供外部调用方法 end=====
      //==========================
      handleInputChange(e) {
        let value = e.target.value;
        const newVal = value === '' ? undefined : Number(value);
        if (!isNaN(newVal) || value === '') {
          this.setCurrentValue(newVal);
        }
      },
      handleBlur(event) {
        this.$emit('blur', event);
        //直接改变input的值
        this.$refs.input.value=this.currentInputValue;
      },
      handleFocus(event) {
        this.$emit('focus', event);
      },
      /**
       * 通过step 获取对应的小数位
       * @param {Number} value
       */
      getPrecision(value) {
        if (value === undefined) return 0;
        const valueString = value.toString();
        const dotPosition = valueString.indexOf('.');
        let precision = 0;
        if (dotPosition !== -1) {
          precision = valueString.length - dotPosition - 1;
        }
        return precision;
      },
      decrease() {
        if (this.disabled || this.minDisabled) return;
        const value = this.value || 0;
        const newVal = this._decrease(value, this.step);
        this.setCurrentValue(newVal);
      },
      increase() {
        if (this.disabled || this.maxDisabled) return;
        const value = this.value || 0;
        const newVal = this._increase(value, this.step);
        this.setCurrentValue(newVal);
      },
      /**
       * @param {Object} val 当前的值
       * @param {Object} step 每步减少的值
       * @return 返回减少后的值
       */
      _decrease(val, step) {
        if (typeof val !== 'number' && val !== undefined) return this.currentValue;

        const precisionFactor = Math.pow(10, this.numPrecision);

        return this.toPrecision((precisionFactor * val - precisionFactor * step) / precisionFactor);
      },
      /**
       * @param {Object} val 当前的值
       * @param {Object} step 每步增加的值
       * @return 返回减少后的值
       */
      _increase(val, step) {
        if (typeof val !== 'number' && val !== undefined) return this.currentValue;

        const precisionFactor = Math.pow(10, this.numPrecision);
        // Solve the accuracy problem of JS decimal calculation by converting the value to integer.
        return this.toPrecision((precisionFactor * val + precisionFactor * step) / precisionFactor);
      },
      toPrecision(num, precision) {
        if (precision === undefined) precision = this.numPrecision;
        return parseFloat(parseFloat(Number(num).toFixed(precision)));
      },
      /**
       * 设置当前正确的值，处理最大值 最小值问题
       * @param {Object} newVal
       */
      setCurrentValue(newVal) {
        const oldVal = this.currentValue;
        //以precision为准
        if (typeof newVal === 'number' && this.precision !== undefined) {
          newVal = this.toPrecision(newVal, this.precision);
        }
        if (newVal >= this.max) newVal = this.max;
        if (newVal <= this.min) newVal = this.min;
        if (oldVal === newVal) {
          this.$refs.input.value=this.currentInputValue;
          return;
        }
        this.$emit('input', newVal);
        this.$emit('change', newVal, oldVal);
        this.currentValue = newVal;
      },
    }
  }
</script>



