              if(t<4 && t_length==1 || t_length==0) return;
              t = t % 31;
              if(t==0) t = 31;
              if(t<10) t = "0" + t;
              newDay = curDay.substring(0,_i2+1) + t;
              if(startFlag){
                this.showInputDate = newDay+"至"+end;
                curIndex1 = newDay.length+1;
                curIndex2 = curIndex1+end.indexOf("-");
              }else{
                this.showInputDate = start+"至"+newDay;
                curIndex1 = this.showInputDate.length;
                curIndex2 = curIndex1;
              }
              this.selectInput(curIndex1,curIndex2,true);

            }else{
              d = d.substring(0,2);
              if(t_length==0){
                newDay = curDay.substring(0,_i2+1) + d;
