<template>
  <div class="zoehis_menu_group clearfix" ref="allWidthId"
       :class="{'zoehis_menu_group_bg':isStyle2,
      'zoehis_menu_group_style3':isStyle3}">
    <div ref="showWidthId" :class="['zoehis_menu_group_content',{'has_replace':hasReplaceMenu}]">
      <slot></slot>
    </div>
    <!--超过显示下拉-->
    <div :class="['zoehis_menu_select',{'isVisibility':!isShowReplaceMenu}]"
         v-if="hasReplaceMenu" ref="replaceMenuId" :id="menuGroupId">
      <zoehis-select
        ref="replaceMenuRef"
        :prevent-over-id="menuGroupId"
        placeholder="其他"
        :itemcode="itemcode"
        :itemtext="itemtext"
        v-model="replaceMenu"
        :selectdata="replaceMenuList"
        :clearable="false"
        :before-select="replaceMenuBefore"
        :render-show-data="selshowdata"
        @visible-change="visibleChange"
        :noselect="noselect"
        width="76"></zoehis-select>
    </div>
    <!--选中的科室-->
    <div :class="['zoehis_selected_con',{'isVisibility':!isShowReplaceMenu},{'zoehis_menu_selectedTxt':selectedTxt!=''}]"
         v-if="hasReplaceMenu">
      {{selectedTxt}}
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default{
    name: "ZoehisMenuGroup",
    componentName: "ZoehisMenuGroup",
    props: {
      activeName: {},
      value: {},
      type: {
        type:String,
        default:"style1"
      },
      disabled: {
        type: Boolean,
        default: false
      },
      hover: {
        type: Boolean,
        default: false
      },
      beforeClick:Function,
      menuList:{
        type:Array,
        default(){
          return [];
        }
      },
      itemtext:{
        type:String,
        default:"itemtext"
      },
      itemcode:{
        type:String,
        default:"itemcode"
      }

    },
    data() {
      return {
        menuGroupId: "menuGroupId"+new Date().getTime(),
        replaceMenu: 1,//可替换菜单
        replaceMenuList:[],//替换下拉框列表
        isShowReplaceMenu: true,//是否显示可替换菜单
        selectedTxt:'', //选中的科室
        noselect:false, //'其他'下拉列表是否可选中
      };
    },
    computed: {
      // 菜单溢出后是否有下拉
      hasReplaceMenu(){
        return this.menuList.length && this.type === "style1";
      },
      isStyle2(){
        return this.type === "style2"
      },
      isStyle3(){
        return this.type === "style3"
      },
    },

    methods:{
      // 替换下拉框选中前事件
      replaceMenuBefore(code){
        for(let [index,item] of this.menuList.entries()){
          if(item[this.itemcode] == code){
            this.$slots.default[index].child.handleClick(1);//传一表示下拉框切换调用
            this.selectedTxt = item[this.itemtext];
            this.$refs.replaceMenuRef.handleClose();
            break;
          }
        }
        return false
      },
      //下拉框显示/隐藏的事件
      visibleChange(){
        this.noselect = false;
        for(let [index,item] of this.menuList.entries()){
          if(this.$slots.default[index].child.disabled){
            this.noselect = true;
          }
        }
      },
      selshowdata(){
        return "其他";
      },
      // 替换下拉框选中后处理  子组件调用
      selectReplaceMenu(label){
        this.replaceMenu = label;
        if(this.$refs.replaceMenuRef && this.$refs.replaceMenuRef.handleClose){
          this.$refs.replaceMenuRef.handleClose();
        }
      },
      // 设置替换下拉框是否显示
      setReplaceMenuState(){
        if(this.menuList.length>6){
          this.replaceMenuList = this.menuList.slice(6);
          this.isShowReplaceMenu = true;
        }else{
          this.replaceMenuList = [];
          this.isShowReplaceMenu = false;
        }
      },
      // 设置replaceMenu值
      setReplaceMenu(val){
        this.replaceMenu = val;
      },
    },

    mounted(){
      if(this.hasReplaceMenu){
        window.onresize = this.setReplaceMenuState;
        setTimeout(()=> {  //初次渲染时，'其他'选项按钮不出现的问题
          this.$nextTick(()=>{
            this.setReplaceMenuState();
          });
        }, 10);
      }
    },

    watch: {
      value(value,oldValue){
        this.$emit('change', value,oldValue);
        if(this.disabled) return false;
      },
      // 可替换下拉数组长度
      'replaceMenuList.length'(num){
        for(let [index,item] of this.replaceMenuList.entries()){
          if(this.value == item[this.itemcode]){
            this.replaceMenu = this.value;
            break;
          }else if(index == this.replaceMenuList.length-1){
            this.replaceMenu = "";
            this.selectedTxt = "";
          }
        }
      }
    },
  }
</script>



