<template>
  <footer class="zoehis_footer" :style="{ height }">
    <slot></slot>
  </footer>
</template>
<script type="text/babel">
export default {
  name:"ZoehisFooter",
  componentName:"ZoehisFooter",
  props:{
    height:{
      type:String,default:"60px"
    }
  }
}
</script>


