<template>
  <div class="component_example">
    <h1>Button 按钮</h1>
    <h2>常用的按钮操作</h2>
    <h4>注意：（表全局）高30px的按钮需要加图标</h4>
    <h4>注意：（表局部）高26px的按钮视为无图标按钮</h4>
    <div class="item">
      <h4>基本用法</h4>
      <div class="item-content" id="testtest">
        <zoehis-button icon="z_delete_normal">有图标的按钮</zoehis-button>

        <zoehis-button type="primary" icon="z_delete_normal">有图标的按钮</zoehis-button>
        <zoehis-button icon="z_delete_normal">默认按钮</zoehis-button><zoehis-button icon="z_delete_normal" type="primary">主要按钮</zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>type属性有2种：default/primary（默认/主要）</li>
              <li>icon属性是来设置有没有图标的</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button icon=&quot;z_delete_normal&quot;&gt;有图标的按钮&lt;/zoehis-button&gt;
               &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot;&gt;有图标的按钮&lt;/zoehis-button&gt;
               &lt;zoehis-button icon=&quot;z_delete_normal&quot;&gt;默认按钮&lt;/zoehis-button&gt;&lt;zoehis-button icon=&quot;z_delete_normal&quot; type=&quot;primary&quot;&gt;主要按钮&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>不可点击状态</h4>
      <div class="item-content">
        <zoehis-button type="primary" :disabled="false" icon="z_delete_normal">图标按钮(可以点击)</zoehis-button>
        <zoehis-button type="primary" :disabled="true" icon="z_delete_normal">图标按钮(不可点击)</zoehis-button>
        <zoehis-button :disabled="false" icon="z_delete_normal">默认按钮</zoehis-button>
        <zoehis-button  icon="z_delete_normal"type="primary" :disabled="true">主要按钮</zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>disabled属性有2种：true/false（不可点击/可以点击）</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button type=&quot;primary&quot; :disabled=&quot;false&quot; icon=&quot;z_delete_normal&quot;&gt;图标按钮(可以点击)&lt;/zoehis-button&gt;
               &lt;zoehis-button type=&quot;primary&quot; :disabled=&quot;true&quot; icon=&quot;z_delete_normal&quot;&gt;图标按钮(不可点击)&lt;/zoehis-button&gt;
               &lt;zoehis-button :disabled=&quot;false&quot; icon=&quot;z_delete_normal&quot;&gt;默认按钮&lt;/zoehis-button&gt;
               &lt;zoehis-button  icon=&quot;z_delete_normal&quot;type=&quot;primary&quot; :disabled=&quot;true&quot;&gt;主要按钮&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>不同尺寸</h4>
      <div class="item-content">
        <zoehis-button type="primary" icon="z_delete_normal">正常按钮</zoehis-button><zoehis-button type="primary" size="small">小型按钮</zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>size属性有2种：normal/small（正常按钮/小型按钮）</li>
              <li>默认为： normal</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot;&gt;正常按钮&lt;/zoehis-button&gt;
               &lt;zoehis-button type=&quot;primary&quot; size=&quot;small&quot;&gt;小型按钮&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>快捷键描述</h4>
      <div class="item-content">
        <zoehis-button type="primary" icon="z_delete_normal" keyboard="0">图标按钮(带快捷键描述)</zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>keyboard属性为String类型：用于显示快捷键描述的</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
              &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot; keyboard=&quot;0&quot;&gt;图标按钮(带快捷键描述)&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                 return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>按钮点击/enter事件</h4>
      <div class="item-content">
        <zoehis-button type="primary" icon="z_delete_normal" @clickenter="myClickEnterEve">图标按钮</zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>@clickenter="myClickEnterEve",组件触发点击事件/或者enter事件（需要焦点在按钮上才会触发enter事件）</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot; @clickenter=&quot;myClickEnterEve&quot;&gt;图标按钮&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                 return {}
               },
               methods:{
                 myClickEnterEve:function(evt){
                   alert(&quot;这是点击事件/或者enter事件&quot;);
                 },
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>按钮组</h4>
      <div class="item-content">
        <zoehis-button-group>
          <zoehis-button type="primary" icon="z_delete_normal">图标按钮</zoehis-button><zoehis-button icon="z_delete_normal" type="default">默认按钮</zoehis-button>
        </zoehis-button-group>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>用zoehis-button-group包起来 </li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button-group&gt;
                 &lt;zoehis-button type=&quot;primary&quot; icon=&quot;z_delete_normal&quot;&gt;图标按钮&lt;/zoehis-button&gt;&lt;zoehis-button icon=&quot;z_delete_normal&quot; type=&quot;default&quot;&gt;默认按钮&lt;/zoehis-button&gt;
               &lt;/zoehis-button-group&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                 return {}
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>改变按钮内容</h4>
      <div class="item-content">
        <zoehis-button type="primary" :keyboard="keyboard" icon="z_delete_normal">{{featuresName}}</zoehis-button>
        <zoehis-button type="default" icon="z_delete_normal">{{defaultName}}</zoehis-button>
        <zoehis-button icon="z_delete_normal" @clickenter="changeBtnCt" type="primary">改变按钮内容</zoehis-button>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>主要去改变keyboard 跟变量defaultName、featuresName的值</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
         <pre><code>
           &lt;template&gt;
             &lt;div class=&quot;component_example&quot;&gt;
               &lt;zoehis-button type=&quot;primary&quot; :keyboard=&quot;keyboard&quot; icon=&quot;z_delete_normal&quot;&gt;{{featuresName}}&lt;/zoehis-button&gt;
               &lt;zoehis-button type=&quot;default&quot; icon=&quot;z_delete_normal&quot;&gt;{{defaultName}}&lt;/zoehis-button&gt;
               &lt;zoehis-button icon=&quot;z_delete_normal&quot; @clickenter=&quot;changeBtnCt&quot; type=&quot;primary&quot;&gt;改变按钮内容&lt;/zoehis-button&gt;
             &lt;/div&gt;
           &lt;/template&gt;

           &lt;script&gt;
             export default {
               data(){
                 return {
                   featuresName:&quot;底部图标&quot;,
                   defaultName:&quot;默认图标&quot;,
                   keyboard:&quot;2&quot;
                 }
               },
               methods:{
                 changeBtnCt:function(evt){
                   this.defaultName=&quot;新的图标内容&quot;;
                   this.featuresName=&quot;新的图标内容&quot;;
                   ++this.keyboard;
                 },
               },
             }
           &lt;/script&gt;
         </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>属性</h4>
      <zoehis-table keycode="code" :data="propData" :configs="demoConfig1"></zoehis-table>
      <h4>方法</h4>
      <zoehis-table keycode="code" :data="functionData" :configs="demoConfig1"></zoehis-table>
      <h4>事件</h4>
      <zoehis-table keycode="code" :data="eventData" :configs="demoConfig1"></zoehis-table>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Button',
    data() {
      return {
        featuresName:"底部图标",
        defaultName:"默认图标",
        keyboard:"2",
        propData:[], //属性说明表格数据
        functionData:[], //方法说明表格数据
        eventData:[], //事件说明表格数据
        demoConfig1:[],
      }
    },
    methods:{
      changeBtnCt:function(evt){
        this.defaultName="新的图标内容";
        this.featuresName="新的图标内容";
        ++this.keyboard;
      },
      myClickEnterEve:function(evt){
        alert("这是点击事件/或者enter事件");
      },
      handleToggle(e){
        $(e.target).next().find("pre code").toggle()
      },
    },
    mounted:function() { //用于高亮显示代码
      this.demoConfig1=[
        {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"parameter",dataItemName:"参数",filterType:"",itemWidth:"120px",fixedFlag:""},
        {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"left",patientItemName:"",dataItemCode:"explain",dataItemName:"说明",filterType:"",itemWidth:"340px",fixedFlag:""},
        {sortNo:2,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"type",dataItemName:"类型",filterType:"",itemWidth:"120px",fixedFlag:""},
        {sortNo:3,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"optional",dataItemName:"可选值",filterType:"",itemWidth:"180px",fixedFlag:""},
        {sortNo:4,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"defaultVal",dataItemName:"默认",filterType:"",itemWidth:"",fixedFlag:""},
      ];
      this.propData=[
        {code:1,parameter:"type", explain:"按钮类型", type:"string",optional:"default/primary（默认/主要）",defaultVal:"default"},
        {code:2,parameter:"icon", explain:"设置字体图标", type:"string",optional:"-",defaultVal:"-"},
        {code:3,parameter:"disabled", explain:"可不用状态", type:"Boolean",optional:"true/false",defaultVal:"false"},
        {code:4,parameter:"size", explain:"按钮大小尺寸", type:"string",optional:"'normal'/'small'",defaultVal:"'normal'"},
        {code:5,parameter:"keyboard", explain:"用于显示快捷键描述的", type:"string",optional:"-",defaultVal:"-"}
      ];
      this.functionData=[
        {code:1,parameter:"focus()", explain:"焦点定位的方法", type:"	无传入参数",optional:"-",defaultVal:"-"}
      ];
      this.eventData=[
        {code:1,parameter:"@clickenter", explain:"点击/enter事件", type:"	返回参数：1.e",optional:"-",defaultVal:"-"}
      ];
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  ul{margin: 0 0 0 20px; padding: 0;}
  .component_example .clearflex{display: block;}
  .component_example .item-toggle{cursor: pointer; background: #8fcae5; text-align: center; font-weight: bold;}
  .component_example .item-code{ width: 100%;max-width: 100%;}
  .component_example .item-code pre code{display: none;}
</style>



