<template>
  <div class="zoehis_tree"
       :class="{
      'zoehis_tree__highlight_current': highlight,
      'is-dragging': !!dragState.draggingNode,
      'is-drop-not-allow': !dragState.allowDrop,
      'is-drop-inner': dragState.dropType === 'inner'
    }"
       role="tree"
  >
    <zoehis-tree-node
      v-for="child in root.childNodes"
      :renderhtml="renderhtml"
      :node="child"
      :props="props"
      :key="getNodeKey(child)">
    </zoehis-tree-node>
    <div class="zoehis_tree__empty_block" v-if="!root.childNodes || root.childNodes.length===0">
      <span class="zoehis_tree__empty_text">{{ emptytext }}</span>
    </div>

    <div
      v-show="dragState.showDropIndicator"
      class="zoehis_tree__drop-indicator"
      ref="dropIndicator">
    </div>
  </div>
</template>

<script type="text/babel">
  import TreeStore from './model/tree-store';
  import emitter from '../../../src/mixins/emitter';
  import { toTree,findNearestComponent } from './model/util';
  import ZoehisTreeNode from './tree-node';
  import { addClass, removeClass } from '../../../src/utils/dom';

  export default {
    name: 'ZoehisTree',

    mixins: [emitter],

    components: {
      ZoehisTreeNode
    },

    props: {

      data: Array,
      highlightcurrent: {
        type: Boolean,
        default: true
      },
      lazy: {
        type: Boolean,
        default: false
      },
      emptytext: {
        type: String,
        default(){
          if(this.$t && (typeof this.$t == "function")){
            return this.$t('zoehisUI.tree.emptyText');
          }else {
            return "暂无数据";
          }
        }
      },
      expandselected: {
        type: Boolean,
        default: false
      },
      expandselectedchild:{
        type: Boolean,
        default: true
      },
      load: Function,
      currentnodekey: [String, Number],//当前选中节点的 key，只写属性
      checkstrictly: Boolean,//在显示复选框的情况下，是否严格的遵循父子不互相关联的做法，默认为 false
      defaultcheckedkeys: Array,//默认勾选的节点的 key 的数组
      defaultexpandedkeys: Array,//默认展开的节点的 key 的数组
      autoexpandparent: {//展开子节点的时候是否自动展开父节点
        type: Boolean,
        default: true
      },
      expandonclicknode: {
        type: Boolean,
        default: false
      },
      defaultexpandall: Boolean,//是否默认展开所有节点
      filternodemethod: Function,
      beforenodeselect: Function,
      showcheckbox: {
        type: Boolean,
        default: false
      },
      props: {
        default() {
          return {
            children: 'children',
            label: '',
            id: 'id',
            pid: 'pid',
            noleaf: ''
          };
        }
      },
      indent: {
        type: Number,
        default: 22
      },
      renderhtml: Function,
      draggable: {//是否允许拖动
        type: Boolean,
        default: false
      },
      editable: {//是否双击编辑
        type: Boolean,
        default: false
      },
      allowDrag: Function,
      allowDrop: Function,
    },

    data() {
      return {
        store: null,
        root: null,
        currentNode: null,
        isShow: true,
        dragState: {
          showDropIndicator: false,
          draggingNode: null,
          dropNode: null,
          allowDrop: true
        }
      };
    },

    watch: {
      data: {
        immediate: true,
        handler: function(data) {
          //append remove 产生的data改变 不执行 newStore
          if(this.updateFlag){
            this.updateFlag=false;
            return;
          }
          this.newStore();
        }
      }
    },

    computed: {
      highlight(){
        return !this.showcheckbox && this.highlightcurrent;
      },
      nodekey(){
        return this.props.id;
      }
    },

    methods: {
      getNodeE() {
        return  $('.is-current')
      },
      getNodeKey(node, index) {
        const nodeKey = this.nodeKey;
        if (nodeKey && node) {
          return node.data[nodeKey];
        }
        return index;
      },
      //获取选中的节点
      getCheckedNodes(leafOnly) {
        return this.store.getCheckedNodes(leafOnly)
      },
      getCheckedKeys(leafOnly) {
        return this.store.getCheckedKeys(leafOnly);
      },
      setCheckedNodes(nodes, leafOnly) {
        if (!this.nodekey) throw new Error('[Tree] nodeKey is required in setCheckedNodes');
        this.store.setCheckedNodes(nodes, leafOnly);
      },
      setCheckedKeys(keys, leafOnly) {
        if (!this.nodekey) throw new Error('[Tree] nodeKey is required in setCheckedNodes');
        this.store.setCheckedKeys(keys, leafOnly);
      },
      appendNode(data, parentData){
        this.store.append(data, parentData)
      },
      removeNode(data,flag){
        this.store.remove(data,flag)
      },
      updateNode(data, newData){
        this.store.update(data, newData)
      },
      setUnselected(){
        this.store.currentNode = null;
      },
      setCurrentSelected(keys){
        let node = this.store.setCurrentNodeKey(keys);
        this.$emit('nodeselect',node.data, node);


        if (this.expandselected){
          if (node.expand && this.expandselectedchild){
            node.expand();
            this.$emit('nodeexpand', node.data, node);
          }
          while (node.parent){

            node.parent.expand();
            node = node.parent;
            this.$emit('nodeexpand', node.data, node);
          }
        }

      },
      /**
       * 通过节点的key值展开对应的节点，展开会触发 $emit("nodeexpand")事件，且第三个参数为 "isExpandFlag"字符串
       * 注意： 已经展开的节点（expanded=true），不会再执行expand方法且不会触发 nodeeepand事件
       * @param keys 要展开的节点 格式为 数组 Array
       */
      expandNodeByKey(keys){
        if(!keys || !(keys instanceof Array)) return;//为空 跟 不是数组 则返回
        for(let i=0,l=keys.length; i<l; i++){
          let node = this.store.getNode(keys[i]);
          if(!node) continue;

          if (node.expand && (!node.expanded)){
            node.expand();
            this.$emit('nodeexpand', node.data, node, "isExpandFlag");
          }
          while (node.parent){

            node = node.parent;
            if(!node.expanded){
              node.expand();
              this.$emit('nodeexpand', node.data, node, "isExpandFlag");
            }
          }
        }

      },
      getCurrentSelected(){
        return this.store.getCurrentNode();
      },
      //更新源数据
      updateTreeData(arr=[]){
        this.updateFlag=true;//append remove方法产生的 源数据的改变
        this.$emit("update:data",arr);
        this.$nextTick(()=>{
          this.updateFlag=false;//解决 tree 没有使用 .sync 造成 updateFlag一直是true的问题
        });
      },
      newStore(){
        this.treedata = toTree(this.data, this.props.id ? this.props.id : 'id', this.props.pid ? this.props.pid : 'pid');

        this.isTree = true;
        this.store = new TreeStore({
          initFlag:"1",//1：初始化   2：新增节点 3：移除节点
          key: this.nodekey,//每个树节点用来作为唯一标识的属性，整颗树应该是唯一的
          data: this.treedata,
          lazy: this.lazy,
          props: {
            children: this.props.children ? this.props.children : 'children',
            label: this.props.label,
            noleaf: this.props.noleaf
          },//配置选项
          load: this.load,//加载子树数据的方法
          currentNodeKey: this.currentnodekey,//当前选中节点的 key，只写属性
          checkStrictly: this.checkstrictly,//在显示复选框的情况下，是否严格的遵循父子不互相关联的做法，默认为 false
          defaultCheckedKeys: this.defaultcheckedkeys,//默认勾选的节点的 key 的数组
          defaultExpandedKeys: this.defaultexpandedkeys,//默认展开的节点的 key 的数组
          autoExpandParent: this.autoexpandparent,//展开子节点的时候是否自动展开父节点
          defaultExpandAll: this.defaultexpandall,//是否默认展开所有节点
          filterNodeMethod: this.filternodemethod,//对树节点进行筛选时执行的方法，返回 true 表示这个节点可以显示，返回 false 则表示这个节点会被隐藏
          beforenodeselect: this.beforenodeselect,
          updateTreeData: this.updateTreeData,//append remove方法后的要更新data数据的回调
        });
        this.root = this.store.root;
      }
    },
    created(){
      let dragState = this.dragState;
      this.$on('tree-node-drag-start', (event, treeNode) => {
        if (typeof this.allowDrag === 'function' && !this.allowDrag(treeNode.node)) {
          event.preventDefault();
          return false;
        }
        event.dataTransfer.effectAllowed = 'move';

        // wrap in try catch to address IE's error when first param is 'text/plain'
        try {
          // setData is required for draggable to work in FireFox
          // the content has to be '' so dragging a node out of the tree won't open a new tab in FireFox
          event.dataTransfer.setData('text/plain', '');
        } catch (e) {}
        dragState.draggingNode = treeNode;
        this.$emit('node-drag-start', treeNode.node, event);
      });

      this.$on('tree-node-drag-over', (event, treeNode) => {
        const dropNode = findNearestComponent(event.target, 'ZoehisTreeNode');
        const oldDropNode = dragState.dropNode;
        if (oldDropNode && oldDropNode !== dropNode) {
          removeClass(oldDropNode.$el, 'is-drop-inner');
        }
        const draggingNode = dragState.draggingNode;
        if (!draggingNode || !dropNode) return;

        let dropPrev = true;
        let dropInner = true;
        let dropNext = true;
        let userAllowDropInner = true;
        if (typeof this.allowDrop === 'function') {
          dropPrev = this.allowDrop(draggingNode.node, dropNode.node, 'prev');
          userAllowDropInner = dropInner = this.allowDrop(draggingNode.node, dropNode.node, 'inner');
          dropNext = this.allowDrop(draggingNode.node, dropNode.node, 'next');
        }
        event.dataTransfer.dropEffect = dropInner ? 'move' : 'none';
        if ((dropPrev || dropInner || dropNext) && oldDropNode !== dropNode) {
          if (oldDropNode) {
            this.$emit('node-drag-leave', draggingNode.node, oldDropNode.node, event);
          }
          this.$emit('node-drag-enter', draggingNode.node, dropNode.node, event);
        }

        if (dropPrev || dropInner || dropNext) {
          dragState.dropNode = dropNode;
        }

        if (dropNode.node.nextSibling === draggingNode.node) {
          dropNext = false;
        }
        if (dropNode.node.previousSibling === draggingNode.node) {
          dropPrev = false;
        }
        if (dropNode.node.contains(draggingNode.node, false)) {
          dropInner = false;
        }
        if (draggingNode.node === dropNode.node || draggingNode.node.contains(dropNode.node)) {
          dropPrev = false;
          dropInner = false;
          dropNext = false;
        }
        const targetPosition = dropNode.$el.getBoundingClientRect();
        const treePosition = this.$el.getBoundingClientRect();

        let dropType;
        const prevPercent = dropPrev ? (dropInner ? 0.25 : (dropNext ? 0.45 : 1)) : -1;
        const nextPercent = dropNext ? (dropInner ? 0.75 : (dropPrev ? 0.55 : 0)) : 1;

        let indicatorTop = -9999;
        const distance = event.clientY - targetPosition.top;
        if (distance < targetPosition.height * prevPercent) {
          dropType = 'before';
        } else if (distance > targetPosition.height * nextPercent) {
          dropType = 'after';
        } else if (dropInner) {
          dropType = 'inner';
        } else {
          dropType = 'none';
        }

        const dropIndicator = this.$refs.dropIndicator;
        if (dropType === 'before') {
          indicatorTop = targetPosition.top - treePosition.top;
        } else if (dropType === 'after') {
          indicatorTop = targetPosition.bottom - treePosition.top;
        }
        dropIndicator.style.top = indicatorTop + 'px';
//        dropIndicator.style.left = (targetPosition.right - treePosition.left) + 'px';
        dropIndicator.style.left = dropNode.node.level * this.indent + 'px';


        if (dropType === 'inner') {
          addClass(dropNode.$el, 'is-drop-inner');
        } else {
          removeClass(dropNode.$el, 'is-drop-inner');
        }

        dragState.showDropIndicator = dropType === 'before' || dropType === 'after';
        dragState.allowDrop = dragState.showDropIndicator || userAllowDropInner;
        dragState.dropType = dropType;
        this.$emit('node-drag-over', draggingNode.node, dropNode.node, event);
      });

      this.$on('tree-node-drag-end', (event) => {

        const { draggingNode, dropType, dropNode } = dragState;
        event.preventDefault();
        event.dataTransfer.dropEffect = 'move';

        if (draggingNode && dropNode) {
          const draggingNodeCopy = { data: draggingNode.node.data };
          if (dropType !== 'none') {
            draggingNode.node.remove();
          }
          if (dropType === 'before') {
            dropNode.node.parent.insertBefore(draggingNodeCopy, dropNode.node);
          } else if (dropType === 'after') {
            dropNode.node.parent.insertAfter(draggingNodeCopy, dropNode.node);
          } else if (dropType === 'inner') {
            dropNode.node.insertChild(draggingNodeCopy);
          }
          if (dropType !== 'none') {
            this.store.registerNode(draggingNodeCopy);
          }

          removeClass(dropNode.$el, 'is-drop-inner');

          this.$emit('node-drag-end', draggingNode.node, dropNode.node, dropType, event);
          if (dropType !== 'none') {
            this.$emit('node-drop', draggingNode.node, dropNode.node, dropType, event);
          }
        }
        if (draggingNode && !dropNode) {
          this.$emit('node-drag-end', draggingNode.node, null, dropType, event);
        }

        dragState.showDropIndicator = false;
        dragState.draggingNode = null;
        dragState.dropNode = null;
        dragState.allowDrop = true;
      });
    }

  };
</script>





