<template>
  <section class="zoehis_container" :class="{is_vertical: isVertical}">
    <slot></slot>
  </section>
</template>
<script type="text/babel">
export default {
  name:"ZoehisContainer",
  componentName:"ZoehisContainer",
  data(){
    return {

    };
  },
  props:{
    direction:String
  },
  computed:{
    isVertical() {
      if(this.direction === "vertical"){
        return true;
      }else if(this.direction === "horizontal"){
        return false;
      }

      return this.$slots && this.$slots.default
        ? this.$slots.default.some(vnode => {
          const tag = vnode.componentOptions && vnode.componentOptions.tag;
          console.log(this)
          return tag === "zoehis-header" || tag === "zoehis-footer";
        })
        : false;
    }
  }
}
</script>


