<template>
  <div>
    <div class="component_example">
      <h1>Progress 进度条</h1>
      <h2>常用的操作</h2>

      <div class="item">
        <h4>基本用法</h4>
        <div class="item-content">
          <span>为零状态：</span><zoehis-progress :percentage="0"></zoehis-progress>
          <br>
          <span>默认状态：</span><zoehis-progress :percentage="25"></zoehis-progress>
          <br>
          <span>完成状态：</span><zoehis-progress :percentage="100" status="success"></zoehis-progress>
          <br>
          <span>异常状态：</span><zoehis-progress :percentage="50" status="exception"></zoehis-progress>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;span&gt;为零状态：&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;0&quot;&gt;&lt;/zoehis-progress&gt;
            &lt;span&gt;默认状态：&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;25&quot;&gt;&lt;/zoehis-progress&gt;
            &lt;span&gt;完成状态：&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;100&quot; status=&quot;success&quot;&gt;&lt;/zoehis-progress&gt;
            &lt;span&gt;异常状态：&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;50&quot; status=&quot;exception&quot;&gt;&lt;/zoehis-progress&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            percentage属性是 <strong>必填</strong>的，表示进度条对应的百分比，必须是Number类型，值在 0-100 之前。 <br>

          </div>
        </div>
      </div>

      <div class="item">
        <h4>常规用法</h4>
        <div class="item-content">
          <span>完成状态：</span><zoehis-progress :percentage="100" status="success"></zoehis-progress>
          <br>
          <span>属性设置：</span><zoehis-progress :percentage="50" :show-text="false" color="#000" :stroke-width="24"></zoehis-progress>
        </div>
        <div class="item-detail">
          <div class="item-code">
          <pre><code>
            &lt;span&gt;完成状态：&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;100&quot; status=&quot;success&quot;&gt;&lt;/zoehis-progress&gt;
            &lt;span&gt;属性设置：&lt;/span&gt;&lt;zoehis-progress :percentage=&quot;50&quot; :show-text=&quot;false&quot; color=&quot;#000&quot; :stroke-width=&quot;24&quot;&gt;&lt;/zoehis-progress&gt;
          </code></pre>
          </div>
          <div class="item-desc">
            status属性：有两种 success（成功）、exception（异常），注意 成功时 percentage设置 100；异常时 percentage设置 50；该属性status主要就是改变了进度条的
            颜色而已，color属性可以对它进行覆盖。<br>
            show-text属性：表示要不要显示描述文本。 <br>
            stroke-width属性：设置进度条的宽度，一般是用默认（12px）
          </div>
        </div>
      </div>

      <div class="item">
        <h4>属性</h4>
        <table width="100%">
          <thead>
          <tr>
            <th>参数</th><th>说明</th><th>类型</th><th>可选值</th><th>默认</th>
          </tr>
          </thead>
          <tbody>
          <tr>
            <td>:percentage</td>
            <td>(必填项*) 设置当前进度条的进度值</td>
            <td>Number</td><td>值为：0 到 100</td><td>-</td>
          </tr>
          <tr>
            <td>status</td><td>进度条当前状态(注意：success设置100%，exception设置50%)</td><td>string</td><td>success(成功)/exception(异常)</td><td>-</td>
          </tr>
          <tr>
            <td>:stroke-width</td><td>进度条的宽度</td><td>Number</td><td>-</td><td>12</td>
          </tr>
          <tr>
            <td>:show-text</td><td>是否显示进度条文字内容</td><td>Boolean</td><td>true/false</td><td>true</td>
          </tr>
          <tr>
            <td>:color</td><td>进度条背景色（会覆盖 status 状态颜色）,如："#ddddd"</td><td>string</td><td>-</td><td>-</td>
          </tr>
          </tbody>
        </table>
        <div style="clear: both"></div>
      </div>

    </div>
  </div>
</template>

<script type="text/babel">
  export default{
    data: function () {
      return {
      }
    },
    mounted:function() { //用于高亮显示代码
      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    },
    methods: {

    }
  }
</script>



