<template>
  <div class="component_example">
    <h1>Table表格组件</h1>
    <h2>常用的操作</h2>
    <p>
      <strong>注意：</strong>表格展示的数据(:data) 和 表格配置数据(:config) 统一写在最底部
    </p>
    <div class="item">
      <h4>基本用法</h4>
      <div class="item-content">
        <zoehis-table
          keycode="drugCode"
          :data="demoData1"
          :configs="demoConfig2"
          checkbox
          checkboxfixed
          operateflag
          operatefixed
          pageflag
          :operate-html="operateHtml"
          @operate-click="operateClickEn"
          checkbox-width="100px"
          operate-width="200px"
          operate-text="自定义操作文本"
          operate-align="center"
        ></zoehis-table>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>data:数据源</li>
              <li>configs:数据配置，参考下面的configs属性说明</li>
              <li>checkbox/checkboxfixed/checkbox-width/@select/@select-all：复选框的相关属性，注意默认值</li>
              <li>index/indexfixed/index-width：序号的相关属性</li>
              <li>operateflag/operatefixed/operate-width/operate-text/operate-align/operate-html/@operate-click：操作列的相关属性，注意默认值，@operate-click注意通过设置 data-id来区别当前点击是哪个</li>
              <li>pageflag/page-param/@change-strip/@change-page：分页组件属性</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;zoehis-table :data=&quot;demoData1&quot; :configs=&quot;demoConfig1&quot; checkbox operateflag pageflag
            :operate-html=&quot;operateHtml&quot; @operate-click=&quot;operateClickEn&quot; checkbox-width=&quot;100px&quot;
            operate-text=&quot;自定义操作文本&quot; operate-width=&quot;200px&quot; operate-align=&quot;center&quot;
            &gt;&lt;/zoehis-table&gt;
            &lt;script&gt;
            //操作列点击事件
            operateClickEn(event,item){
              this.$messageBox.success(item.drugName+"。data-id:"+event.target.dataset.id);
            },
            //构造操作列
            operateHtml(item){
              return &quot;&lt;i data-id=&#x27;1&#x27; class=&#x27;zoeIconfont z_tableR_normal&#x27;&gt;&lt;/i&gt;&lt;i data-id=&#x27;2&#x27; class=&#x27;zoeIconfont z_print_normal&#x27;&gt;&lt;/i&gt;&quot;;
            }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>相关方法</h4>
      <div class="item-content">
        <zoehis-table
          ref="zoeTable"
          checkbox
          keycode="drugCode"
          :data="demoData1"
          :configs="demoConfig1"
          :do-filter-func="doFilterFunc"
          :render-attr="['quantity','drugName']"
          :render-html="renderHtml"
          :sort-attr="['quantity']"
          :do-sort-func="doSortFunc"
        ></zoehis-table>
        <div>
          <zoehis-button @clickenter="inverseSelectionEn">反选</zoehis-button>
          <zoehis-button @clickenter="toggleAllRowSelection">全选/清空全选</zoehis-button>
          <zoehis-button @clickenter="setCurrentRow">选中某一行（取消选中某一行）</zoehis-button>
          <zoehis-button @clickenter="doFilterData">过滤</zoehis-button>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>:do-filter-func:属性filterType="other"时有效</li>
              <li>render-attr（Array）/render-html(Function)：自定义内容</li>
              <li>sort-attr（Array）/do-sort-func(Function)：自定义排序，要以configs属性里的 dataItemSort属性相结合，一般建议不要去配置，采用默认的即可</li>
              <li>toggleAllRowSelection(selected)/toggleRowSelection(row,selected)/inverseSelection()/setCurrentRow:这几个方法主要用于处理 手动选中的一些问题（原理都是根据 keycode的唯一值去设置的）(注意：这几个方法不会触发 @select/@select-all/@row-click事件)</li>
              <li>doFilterData(fn):按一定的条件过滤行的信息 fn 回调函数（该回调函数返回true,代表显示该行）</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;zoehis-table :data=&quot;demoData1&quot; :configs=&quot;demoConfig1&quot;
              :do-filter-func=&quot;doFilterFunc&quot; :render-attr=&quot;[&#x27;quantity&#x27;,&#x27;drugName&#x27;]&quot; :render-html=&quot;renderHtml&quot;
              :sort-attr=&quot;[&#x27;quantity&#x27;]&quot; :do-sort-func=&quot;doSortFunc&quot;
            &gt;&lt;/zoehis-table&gt;
            &lt;div&gt;
              &lt;zoehis-button @clickenter=&quot;inverseSelectionEn&quot;&gt;反选&lt;/zoehis-button&gt;
              &lt;zoehis-button @clickenter=&quot;toggleAllRowSelection&quot;&gt;全选/清空全选&lt;/zoehis-button&gt;
              &lt;zoehis-button @clickenter=&quot;setCurrentRow&quot;&gt;选中某一行（取消选中某一行）&lt;/zoehis-button&gt;
              &lt;zoehis-button @clickenter=&quot;doFilterData&quot;&gt;过滤&lt;/zoehis-button&gt;
            &lt;/div&gt;
            &lt;script&gt;
            //过滤表格信息
            doFilterData(){
              this.$refs.zoeTable.doFilterData((d)=&gt;{
                //d: 每一行的信息{Object}
                //触发table.length次,return true：代表显示当前行
                return d.packageSpec &gt; 5;
              });
            },
            renderHtml(val,item,config){
              if(config.dataItemCode==&#x27;quantity&#x27;){
                return &quot;&lt;span style=&#x27;color: red;&#x27;&gt;render-html加的quantity内容：&lt;/span&gt;&quot;+val;
              }else {
                return &quot;&lt;span style=&#x27;color: red;&#x27;&gt;render-html加的drugName内容：&lt;/span&gt;&quot;+val;
              }
            },
            //自定义排序
            doSortFunc(arr,config,flag){
              //处理数量的自定义排序 根据 :sort-attr属性配置判断
              if(config.dataItemCode==&quot;quantity&quot;){
                if(flag==&quot;up&quot;){
                  console.log(&quot;点击自定义排序,列code值：&quot;,config.dataItemCode,&quot;升降序：&quot;,flag);
                  return arr.sort((a,b)=&gt;{
                    return (&quot;&quot;+a[config.dataItemCode]).localeCompare(b[config.dataItemCode]);
                  });
                }else {
                  console.log(&quot;点击自定义排序,列code值：&quot;,config.dataItemCode,&quot;升降序：&quot;,flag);
                  return arr.sort((a,b)=&gt;{
                    return (&quot;&quot;+b[config.dataItemCode]).localeCompare(a[config.dataItemCode]);
                  });
                }
              }else {//其他属性

              }
            },
            doFilterFunc(val,item,config){
              return val + &quot;岁&quot;;
            },
            //反选
            inverseSelectionEn(){
              this.$refs.zoeTable.inverseSelection();
            },
            //全选/清空全选
            toggleAllRowSelection(){
              this.allSelectFlag=!this.allSelectFlag;
              this.$refs.zoeTable.toggleAllRowSelection(this.allSelectFlag);
            },
            //选中某一行（取消选中某一行）
            setCurrentRow(){
              this.$refs.zoeTable.setCurrentRow();
            },
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>相关事件</h4>
      <div class="item-content">
        <zoehis-table
          keycode="drugCode"
          :data="demoData1"
          :configs="demoConfig1"
          pageflag
          @row-click="rowClickEn"
          @row-dblclick="rowDblClickEn"
          @scrollafter="scrollafterEn"
          @col-click="colClickEn"
          @col-dblclick="colDblClickEn"
        ></zoehis-table>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>@operate-click：操作列事件</li>
              <li>@row-click/@row-dblclick:行单击跟行双击事件</li>
              <li>@col-click/@col-dblclick:列单击跟列双击事件</li>
              <li>@select/@select-all:复选框跟权限事件</li>
              <li>@change-strip/@change-page:分页组件相关事件（主要用于调用后端分页接口，从而达到分页效果功能）</li>
              <li>@scrollafter:滚动条滚动事件（主要用于调用后端分页接口，从而达到分页效果功能）</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;zoehis-table :data=&quot;demoData1&quot; :configs=&quot;demoConfig1&quot; pageflag
              @row-click=&quot;rowClickEn&quot; @row-dblclick=&quot;rowDblClickEn&quot; @scrollafter=&quot;scrollafterEn&quot;
              @col-click=&quot;colClickEn&quot; @col-dblclick=&quot;colDblClickEn&quot;
            &gt;&lt;/zoehis-table&gt;
            &lt;script&gt;
            //单击事件
            rowClickEn(item){
              console.log("当前行：",item);
            },
            //双击事件
            rowDblClickEn(item){
              console.log("当前行：",item);
            },
            //滚动条事件
            scrollafterEn(e){
              let {clientHeight,scrollHeight,scrollTop} = e;
              //主要用于处理数据分页功能
              if(scrollHeight-clientHeight-scrollTop<10){
                console.log("向下滚动")
              }
            },
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>头部自定义插槽功能</h4>
      <div class="item-content">
        <zoehis-table keycode="drugCode" :data="demoData1" :configs="demoConfig1" indexflag>
          <zoehis-input v-model="query" placeholder="插槽1" slot="drugName"></zoehis-input>
        </zoehis-table>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>自定义头部额外的内容操作如下：</li>
              <li>只需要在 table标签里 定义 slot的值，而这个值主要取决于想要在哪列上自定义内容</li>
              <li>比如：左边事例里 取值drugName，分别对应的是 药品名称列 和 厂家名称列的 dataItemCode的值</li>
              <li>
                <strong>插槽说明详见底部表格</strong> (注意跟行内插槽的区别)
              </li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;zoehis-table keycode=&quot;drugCode&quot; :data=&quot;demoData1&quot; :configs=&quot;demoConfig1&quot; indexflag &gt;
              &lt;zoehis-input v-model=&quot;query&quot; placeholder=&quot;插槽1&quot; slot=&quot;drugName&quot;&gt;&lt;/zoehis-input&gt;
            &lt;/zoehis-table&gt;
            &lt;script&gt;
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>过滤弹窗相关方法</h4>
      <div class="item-content">
        <zoehis-table
          keycode="drugCode"
          :data="demoData1"
          :configs="demoConfig1"
          checkbox
          checkboxfixed
          operateflag
          operatefixed
          pageflag
          :operate-html="operateHtml"
          @operate-click="operateClickEn"
          checkbox-width="100px"
          ref="filterRef"
          @confirm-filter="confirmFilterEn"
        ></zoehis-table>
        <zoehis-button @clickenter="filterOpenEn">打开过滤弹窗</zoehis-button>
        <!--<zoehis-button @clickenter="filterClearEn">清空过滤弹窗的数据</zoehis-button>-->
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>方法：openFilter() / closeFilter() / clearFilterList()</li>
              <li>事件：@confirm-filter,具体返回数据F12看控制台</li>
              <li>
                例如：[{dataItemCode:"",dataItemName:"",operatorCode:"",
                operatorName:"",value:"",relateCode:"",relateName:""}]
              </li>
              <li>属性：operator-list / samefilter</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;zoehis-table :data=&quot;demoData1&quot; :configs=&quot;demoConfig1&quot; checkbox operateflag pageflag
            :operate-html=&quot;operateHtml&quot; @operate-click=&quot;operateClickEn&quot; checkbox-width=&quot;100px&quot; ref=&quot;filterRef&quot;
            &gt;&lt;/zoehis-table&gt;
            &lt;script&gt;
            //打开过滤弹窗
            filterOpenEn(){
              this.$refs.filterRef.openFilter();
            },
            //清空过滤弹窗的数据
            filterClearEn(){
              this.$refs.filterRef.clearFilterList();
            },
            //清空按钮确认事件
            confirmFilterEn(arr){
              console.log("过滤弹窗选则的数据",arr);
            },
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>
        表格可编辑功能
        <strong>（无分页的情况）</strong>
      </h4>
      <div class="item-content">
        <zoehis-table
          keycode="drugCode"
          :data="demoData1"
          :configs="demoConfig2"
          @change="handleChange"
        >
          <template slot="td_drugName" slot-scope="{item,index}">
            <zoehis-input v-model="item.drugName" width="100%" @focus="curIndex=index"></zoehis-input>
          </template>
          <template slot="td_packageUnit" slot-scope="{item,index}">
            <zoehis-select
              v-model="item.packageUnitCode"
              :textvalue.sync="item.packageUnit"
              :selectdata="unitList"
              width="100%"
              @focus="curIndex=index"
            ></zoehis-select>
          </template>
        </zoehis-table>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>
                可编辑表格相关属性：
                <strong>slot/slot-scope/@change</strong>
              </li>
              <li>
                <strong>slot/slot-scope:</strong>主要通过vue.js的slot插槽功能实现的。slot的值跟:config配置属性里的dataItemCode值是对应的，
                由于头部由于不跟头部的插槽功能冲突，对于可编辑的插槽补充了个前缀
                <strong>td_</strong>,所以才会看到例子里是
                <strong>< template slot="td_drugName" slot-scope="{item,index}">< /template></strong>
              </li>
              <li>
                <strong>@change：</strong>改变事件，主要用于在编辑改变数据时实时为使用者同步最新的表格数据,也可以调用
                <strong>getData()</strong> 方法获取当前最新的数据
              </li>
              <li>
                <strong>插槽说明详见底部表格</strong> (注意跟表头插槽的区别)
              </li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
        &lt;zoehis-table keycode=&quot;drugCode&quot; :data=&quot;demoData1&quot; :configs=&quot;demoConfig2&quot;
        @change=&quot;handleChange&quot;&gt;
          &lt;template slot=&quot;td_drugName&quot; slot-scope=&quot;{item,index}&quot;&gt;
            &lt;zoehis-input v-model=&quot;item.drugName&quot; width=&quot;100%&quot; @focus=&quot;curIndex=index&quot;&gt;&lt;/zoehis-input&gt;
          &lt;/template&gt;
          &lt;template slot=&quot;td_packageUnit&quot; slot-scope=&quot;{item,index}&quot;&gt;
            &lt;zoehis-select v-model=&quot;item.packageUnitCode&quot; :textvalue.sync=&quot;item.packageUnit&quot; :selectdata=&quot;unitList&quot;
            width=&quot;100%&quot; @focus=&quot;curIndex=index&quot;&gt;&lt;/zoehis-select&gt;
          &lt;/template&gt;
        &lt;/zoehis-table&gt;
        &lt;script&gt;
        /**
        * 编辑表格数据数据改变事件
        * 主要用于获取改变后的数据，
        *同时也可以通过设置某一属性值来标识哪些行数据是改变后的
        */
        handleChange(data){
          //curIndex 代表当前编辑的行
          //直接设置 当前行某一属性标识当前行已修改过
          if(data[this.curIndex]){
            data[this.curIndex].uiRowChangeFlag = true;
          }
          this.nowTableData = data;//设置当前改变后的数据
        },

        &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>
        表格可编辑功能
        <strong>（滚动分页的情况）</strong>
      </h4>
      <div class="item-content">
        <zoehis-table
          keycode="drugCode"
          :data="demoData3"
          :configs="demoConfig2"
          ref="pageTable"
          @scrollafter="editScrollEn"
        >
          <template slot="td_drugName" slot-scope="{item,index}">
            <zoehis-input v-model="item.drugName" width="100%"></zoehis-input>
          </template>
        </zoehis-table>
        <div style="padding: 10px 5px;">
          <zoehis-button @clickenter="handelEditReset">重新设置表格数据data</zoehis-button>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>
                滚动分页主要运用了：
                <strong>insertAfter</strong>方法
              </li>
              <li>详细看例子</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
        &lt;zoehis-table keycode=&quot;drugCode&quot; :data=&quot;demoData3&quot; :configs=&quot;demoConfig2&quot;
          ref=&quot;pageTable&quot; @scrollafter=&quot;editScrollEn&quot;&gt;
          &lt;template slot=&quot;td_drugName&quot; slot-scope=&quot;{item,index}&quot;&gt;
            &lt;zoehis-input v-model=&quot;item.drugName&quot; width=&quot;100%&quot; @focus=&quot;curIndex=index&quot;&gt;&lt;/zoehis-input&gt;
          &lt;/template&gt;
        &lt;/zoehis-table&gt;
        &lt;div style=&quot;padding: 10px 5px;&quot;&gt;
          &lt;zoehis-button @clickenter&quot;handelEditReset&quot;&gt;重新设置表格数据data&lt;/zoehis-button&gt;
        &lt;/div&gt;
        &lt;script&gt;
        //滚动分页
        editScrollEn(e){
          let {clientHeight,scrollHeight,scrollTop} = e;
          if(this.oldScrollTop===scrollTop) return;//垂直top值不变，说明是横向滚动触发的
          this.oldScrollTop=scrollTop;
          //主要用于处理数据分页功能
          if(scrollHeight-clientHeight-scrollTop<10){
            this.getDataByPage();
          }
        },
        //分页获取数据
        getDataByPage(){
          if(this.editTableDoingFlag) return;//阻止多次快速调用
          this.editTableDoingFlag=true;
          let loading = this.$loading({text:&quot;正在拼命的加载中&quot;});
          //模仿ajax异步请求
          new Promise((resolve,reject)=&gt;{
            let l = this.$refs.pageTable.getData().length;//当前表格的长度
            setTimeout(()=&gt;{
              let arr = [];
              for(let i=l;i&lt;l+5;i++){
                let item = {
                  drugCode:&quot;1_&quot;+i,drugName:&quot;药品名称_&quot;+i,packageSpec:1+i,
                  packageUnit:&quot;盒&quot;,packageUnitCode:i,
                  drugSpec:&quot;1ml:50万IU/10瓶/1盒&quot;,modifiedTime:new Date().getTime()+i*1000*60*60*24
                }
                arr.push(item);
              }
              resolve(arr);
            },500);
          }).then(res=&gt;{
            this.$refs.pageTable.insertAfter(res,&quot;&quot;);//在表格最后添加数据
            this.$nextTick(_=&gt;{
              loading &amp;&amp; loading.close();
              this.editTableDoingFlag=false;
            });
          }).catch(e=&gt;{
            this.$nextTick(_=&gt;{
              loading &amp;&amp; loading.close();
              this.editTableDoingFlag=false;
            });
          });
        },
        //重新设置表格的数据
        handelEditReset(){
          let curTime = new Date().getTime();
          let arr = [];
          for(let i=0;i&lt;10;i++){
            let item = {
              drugCode:&quot;1_&quot;+i,drugName:&quot;药品名称_&quot;+i,packageSpec:1+i,
              packageUnit:&quot;盒&quot;,packageUnitCode:i,
              drugSpec:&quot;1ml:50万IU/10瓶/1盒&quot;,modifiedTime:curTime+i*1000*60*60*24
            }
            arr.push(item);
          }
          this.$refs.pageTable.comTableMove({top:0});//将滚动条重置回原位
          this.demoData3=arr;//再设置数据
        },
        &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>
        表格可编辑功能
        <strong>（表格分页、增、删、改的情况）</strong>
      </h4>
      <div class="item-content">
        <zoehis-table
          :keycode="['drugCode','drugName']"
          :data="demoData2"
          :configs="demoConfig2"
          @change="handleChange"
          ref="editTable1"
        >
          <template slot="td_drugName" slot-scope="{item,index}">
            <zoehis-input v-model="item.drugName" width="100%" @focus="curIndex=index"></zoehis-input>
          </template>
        </zoehis-table>
        <div>
          <zoehis-button @clickenter="insertRow(1)">添加向后单条数据</zoehis-button>
          <zoehis-button @clickenter="insertRow(2)">添加向后批量数据</zoehis-button>
          <zoehis-button @clickenter="deleteRow(1)">单条删除</zoehis-button>
          <zoehis-button @clickenter="deleteRow(2)">批量删除</zoehis-button>
          <zoehis-button @clickenter="editRow">修改 药品名称_2</zoehis-button>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>
                注意：
                <strong>增、删、改 操作都会触发 @change 事件。依据是keycode值还有 index索引值</strong>
              </li>
              <li>
                <strong>增加</strong>相关方法：
              </li>
              <li>
                1.在表格数据的某一行
                <strong>前面</strong> 增加数据：insertBefore(data,curRow,isCheckFlag)。参数1.data可以是
                <strong>Array(批量插入)</strong>/
                <strong>Object(单条插入)</strong>；
                参数2.curRow可以是行数据row也可以是行索引值index；参数3(可选).是否为当前要插入的行默认设置选中状态（针对复选框的情况）
              </li>
              <li>
                2.在表格数据的某一行
                <strong>后面</strong> 增加数据：insertAfter(data,curRow,isCheckFlag)。参数跟insertBefore一样
              </li>
              <li>
                <strong>删除</strong>相关方法：
              </li>
              <li>
                1.删除表格某些数据：deleteRow(data)。参数1.data,要删除依据，可以是
                <strong>Array(批量删除)</strong> /
                <strong>Object(单行删除)</strong> /
                <strong>Number/String(索引删除)</strong>
              </li>
              <li>
                <strong>修改</strong>相关方法：
              </li>
              <li>
                1.修改表格某行数据：editRow(row,newRow)。参数1.row,要修改的行，可以是
                <strong>Object(依据keycode)</strong> /
                <strong>Number/String(依据索引值)</strong>；参数2.newRow:要修改的属性值
              </li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
        &lt;zoehis-table keycode=&quot;drugCode&quot; :data=&quot;demoData2&quot; :configs=&quot;demoConfig2&quot;
        @change=&quot;handleChange&quot; ref=&quot;editTable1&quot;&gt;
          &lt;template slot=&quot;td_drugName&quot; slot-scope=&quot;{item,index}&quot;&gt;
            &lt;zoehis-input v-model=&quot;item.drugName&quot; width=&quot;100%&quot; @focus=&quot;curIndex=index&quot;&gt;&lt;/zoehis-input&gt;
          &lt;/template&gt;
        &lt;/zoehis-table&gt;
        &lt;script&gt;
        /**
        * 添加数据
        * @param type: 1代表插入单条，2:代表插入批量
        */
        insertRow(type=1){
          let curTime = new Date().getTime();
          let newRow;
          //参数1.准备添加的数据
          if(type==1){
            //插入单条数据
            newRow = {
            drugCode:curTime,drugName:"阿法骨化醇软胶囊"+curTime,packageSpec:curTime,
            packageUnit:"盒",packageUnitCode:curTime,
            drugSpec:"1ml:10瓶/1盒",modifiedTime:curTime
            }
          }else{
            newRow = [];
            for(let i=0; i<3; i++){
              newRow.push({
                drugCode:curTime+i,drugName:"阿法骨化醇软胶囊"+curTime,packageSpec:curTime,
                packageUnit:"盒",packageUnitCode:curTime+i,
                drugSpec:"1ml:10瓶/1盒",modifiedTime:curTime
              });
            }
          }
          //参数2.以某一条为依据，
          let curRow="";
          //可以是包含 keycode对应的值(此处keycode值是 drugCode与drugName)
          curRow = {drugCode:"1_1",drugName:"药品名称_1",packageUnit:"其他数据没有关系"};//简单点就直接取table表格数据:data内的某一条数据
          //直接取索引值也可以
//        curRow=2;//index为2，即第三条数据

          //参数3.是否要为newRow设置复选框选中状态(默认不选中)
          let isCheckFlag = false;
          this.$refs.editTable1.insertBefore(newRow,curRow,isCheckFlag);
        },
        /**
        * 删除数据
        * @param type: 1代表删除单条，2:代表批量删除
        */
        deleteRow(type){
          //参数1.准备删除的数据（主要要包含 keycode）
          let curRow="";
          if(type==1){
            //对象 Object
            curRow = {drugCode:"1_1",drugName:"药品名称_1",packageUnit:"其他数据没有关系"};//简单点就直接取table表格数据:data内的某一条数据
            //Number 索引
//          curRow = 0;//删除第一条
          }else{
            curRow = [
              {drugCode:"1_0",drugName:"药品名称_0",packageUnit:"其他数据没有关系"},
              {drugCode:"1_1",drugName:"药品名称_1",packageUnit:"其他数据没有关系"},
            ]
          }
          this.$refs.editTable1.deleteRow(curRow);
        },
        //编辑
        editRow(){
          let curRow = {drugCode:"1_2",drugName:"药品名称_2",packageUnit:"其他数据没有关系"};
          this.$refs.editTable1.editRow(curRow,{drugSpec:"规格在变"+new Date().getTime()});
        },

        &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>
        显示条数功能
      </h4>
      <div class="item-content">
        <h5>默认情况</h5>
        <zoehis-table
          :keycode="['drugCode','drugName']"
          :data="demoData2"
          :configs="demoConfig2"
          select-num
          pageflag
          checkbox
          @select-num="selectNumEn"
        >
        </zoehis-table>
        <h5>自定义样式</h5>
        <zoehis-table
          :keycode="['drugCode','drugName']"
          :data="demoData2"
          :configs="demoConfig2"
          select-num
          pageflag
          checkbox
        >
          <template slot="tableSelectNum" slot-scope="{num}">
            自定义显示条数（<strong>{{num}}</strong>）
          </template>
        </zoehis-table>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li>显示条数的前提是 有分页组件(pagefalg)且有复选功能(checkbox)且有select-num属性。</li>
              <li>可使用自定义 slot='tableSelectNum',看例子</li>
              <li>补充 @select-num供使用者参数</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
        &lt;h5&gt;默认情况&lt;/h5&gt;
        &lt;zoehis-table
          :keycode=&quot;[&#x27;drugCode&#x27;,&#x27;drugName&#x27;]&quot;
          :data=&quot;demoData2&quot;
          :configs=&quot;demoConfig2&quot;
          select-num
          pageflag
          checkbox
          @select-num=&quot;selectNumEn&quot;
        &gt;
        &lt;/zoehis-table&gt;
        &lt;h5&gt;自定义样式&lt;/h5&gt;
        &lt;zoehis-table
          :keycode=&quot;[&#x27;drugCode&#x27;,&#x27;drugName&#x27;]&quot;
          :data=&quot;demoData2&quot;
          :configs=&quot;demoConfig2&quot;
          select-num
          pageflag
          checkbox
        &gt;
          &lt;template slot=&quot;tableSelectNum&quot; slot-scope=&quot;{num}&quot;&gt;
            自定义显示条数（&lt;strong&gt;{num}&lt;/strong&gt;）
          &lt;/template&gt;
        &lt;/zoehis-table&gt;
        &lt;script&gt;
        data:{
          return {
            demoData2:[],
            demoConfig2:[]
          };
        },
        methods: {
          /**
          * 已选条数改变事件
          */
          selectNumEn(val){
            console.log("已选：",val,"条");
          },
        }
        &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>隐藏列以及导出excel功能</h4>
      <div class="item-content">
        <zoehis-table
          ref="zoeTableHide"
          checkbox
          keycode="drugCode"
          :data="demoData1"
          :configs="demoConfig1"
          hide-column-flag
        ></zoehis-table>
        <div>
          <zoehis-button @clickenter="handleToExcel">导出当前数据</zoehis-button>
          <router-link to="/func"><zoehis-button>没有使用表格组件可以用导出excel方法</zoehis-button></router-link>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <ul>
              <li><strong>hide-column-flag</strong>:是否启用 隐藏列 功能。默认false,不启用；true代表启用隐藏列功能</li>
              <li><strong>tableToExcel(tableData,fileName,worksheet,tableConfig)</strong>:导出excel方法。1.tableData(必填):要导出表格的数据；2.fileName(非必填):文档名称；3.worksheet(非必填)：文档里面tab的名称；4.tableConfig(非必填):自定义表头的信息，没填代表使用当前表格组件的表头</li>
            </ul>
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;zoehis-table
              ref=&quot;zoeTableHide&quot;
              checkbox
              keycode=&quot;drugCode&quot;
              :data=&quot;demoData1&quot;
              :configs=&quot;demoConfig1&quot;
              hide-column-flag
            &gt;&lt;/zoehis-table&gt;
            &lt;div&gt;
              &lt;zoehis-button @clickenter=&quot;handleToExcel&quot;&gt;导出&lt;/zoehis-button&gt;
            &lt;/div&gt;
            &lt;script&gt;
            /**
            * 导出excel文档
            */
            handleToExcel() {
              //造文档数据
              let tableData = [];
              for (let i = 0; i &lt; 4500; i++) {
                tableData.push({
                  drugCode: &quot;1_&quot; + i,
                  drugName: &quot;药品名称_&quot; + i,
                  packageSpec: 1 + i,
                  packageUnit: &quot;盒&quot;,
                  packageUnitCode: i,
                  drugSpec: &quot;1ml:50万IU/10瓶/1盒&quot;,
                  modifiedTime: i * 1000 * 60 * 60 * 24
                });
              }
              this.$refs.zoeTableHide.tableToExcel(tableData,&quot;自定义文件名&quot;,&quot;自定义tab名称&quot;);
            },
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>table组件属性</h4>
      <table width="100%">
        <thead>
          <tr>
            <th>参数</th>
            <th>说明</th>
            <th>类型</th>
            <th>可选值</th>
            <th>默认</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>:data</td>
            <td>
              表格的源数据(*必填)
              <strong>注意.此属性只是用于重置初始化表格数据，并不能代表操作后的表格数据</strong>
            </td>
            <td>Array</td>
            <td>-</td>
            <td>[]</td>
          </tr>
          <tr>
            <td>:configs</td>
            <td>自定义配置表的信息，按照相应的格式配置(*必填)</td>
            <td>Array</td>
            <td>-</td>
            <td>[]</td>
          </tr>
          <tr>
            <td>:keycode</td>
            <td>表格数据的唯一值，用于v-for对应的key值，达到优化性能的功能(*必填)</td>
            <td>Array/String</td>
            <td>Array类型主要是针对联合主键的，由两个字段组成的唯一值，使用如 :keycode="['code','name']"</td>
            <td>-</td>
          </tr>
          <tr>
            <td>:checkbox</td>
            <td>是否 使用复选框</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:checkboxfixed</td>
            <td>复选框 是否要设置成 左固定 列</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:checkbox-width</td>
            <td>复选框的长度</td>
            <td>String</td>
            <td>比如"50px",主要只支持 px 为单位，不支持百分比</td>
            <td>50px</td>
          </tr>
          <tr>
            <td>:indexflag</td>
            <td>是否 使用序号</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:indexfixed</td>
            <td>序号 是否要设置成 左固定 列</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:index-width</td>
            <td>序号的长度</td>
            <td>String</td>
            <td>比如"50px",主要只支持 px 为单位，不支持百分比</td>
            <td>50px</td>
          </tr>
          <tr>
            <td>:operateflag</td>
            <td>是否 使用操作列</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:operatefixed</td>
            <td>操作列 是否要设置成 右固定 列</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:operate-width</td>
            <td>操作列的长度</td>
            <td>String</td>
            <td>比如"50px",主要只支持 px 为单位，不支持百分比</td>
            <td>100px</td>
          </tr>
          <tr>
            <td>:operate-text</td>
            <td>自定义操作列的文本</td>
            <td>String</td>
            <td>""</td>
            <td>"操作"</td>
          </tr>
          <tr>
            <td>:operate-align</td>
            <td>操作列的长度</td>
            <td>String</td>
            <td>left/center/right</td>
            <td>left</td>
          </tr>
          <tr>
            <td>:pageflag</td>
            <td>是否 使用 分页组件 zoehis-page</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:page-param</td>
            <td>分页组件的属性,包含这三个属性{total:0, page:1, pageSize:30}</td>
            <td>Object</td>
            <td>-</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:strip-arr</td>
            <td>
              同
              <strong>page分页组件</strong> 的stripArr属性
            </td>
            <td>Array</td>
            <td>-</td>
            <td>[10,20,30,40]</td>
          </tr>
          <tr>
            <td>:operate-html="operateHtml(item)"</td>
            <td>自定义操作列的内容（operateflag为ture时有效）</td>
            <td>Function</td>
            <td>参数：1.item 当前行的数据</td>
            <td>-</td>
          </tr>
          <tr>
            <td>:do-filter-func="doFilterFunc(val,item,config)"</td>
            <td>自定义过滤器（配置信息里 filterType为"other"时有效）</td>
            <td>Function</td>
            <td>参数: 1.当前的值 2.当前行的值 3.当前字段的配置信息</td>
            <td>-</td>
          </tr>
          <tr>
            <td>:render-html="renderHtml(val,item,config)"</td>
            <td>自定义所配置的列的内容（根据属性renderAttr,有值的就会触发，一般不建议设置，主要是为了特殊的内容而做的）</td>
            <td>Function</td>
            <td>参数: 1.当前的值 2.当前行的值 3.当前字段的配置信息</td>
            <td>-</td>
          </tr>
          <tr>
            <td>:render-attr="['text']"</td>
            <td>配置前端要自定义内容的列的数据,是个数组</td>
            <td>Array</td>
            <td>-</td>
            <td>[]</td>
          </tr>
          <tr>
            <td>:sort-attr="['text']"</td>
            <td>配置要进行自定义排序的字段集合,是个数组(配置后的列不会再执行前端默认排序的功能)，与事件 :do-sort-func配合使用</td>
            <td>Array</td>
            <td>-</td>
            <td>[]</td>
          </tr>
          <tr>
            <td>:row-class-name</td>
            <td>行的 className 的回调方法，也可以使用字符串为所有行设置一个固定的 className。</td>
            <td>Function({row, rowIndex})/String</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>:do-sort-func</td>
            <td>
              排序的方法（配合 sort-attr 以及 dataItemSort使用）（注意：需要
              <strong>return Array</strong>）
            </td>
            <td>Function</td>
            <td>参数说明：参数1：当前表格的数据Array，参数2：当前列的配置信息，参数3：升序还是降序（up/down）</td>
            <td>-</td>
          </tr>
          <tr>
            <td>:before-row-click</td>
            <td>行点击前方法 return false 将阻止选中当前行（单选）</td>
            <td>Function</td>
            <td>参数说明：参数1：当前行信息； 参数2：event; 参数3：当前行索引index</td>
            <td>-</td>
          </tr>
          <tr>
            <td>:before-select</td>
            <td>点击复选框前方法 return false 将阻止继续执行（包括选中或者不选中）（复选）</td>
            <td>Function</td>
            <td>参数说明：参数1：当前所有选中的行(Array); 参数2：当前行信息; 参数3：当前行索引index</td>
            <td>-</td>
          </tr>
          <tr>
            <td>:before-select-all</td>
            <td>全选前方法 return false 将阻止继续执行（包括选中或者不选中）（复选）</td>
            <td>Function</td>
            <td>参数说明：参数1：当前复选框的状态值（true/false）</td>
            <td>-</td>
          </tr>
          <tr>
            <td>:operator-list</td>
            <td>配置操作符的列表,即：操作符那一列的可选项</td>
            <td>Array</td>
            <td>-</td>
            <td>[{id:"=",text:"模糊查询"}]</td>
          </tr>
          <tr>
            <td>:samefilter</td>
            <td>是否可以选择多个相同的列名</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:hide-column-flag</td>
            <td>是否要启用 隐藏列 功能。默认false不启用,true代表启用隐藏列功能</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
          <tr>
            <td>:select-num</td>
            <td>是否要显示 已选条数（当pageflag,checkbox为true时有效）。默认false不启用</td>
            <td>Boolean</td>
            <td>true/false</td>
            <td>false</td>
          </tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
     <div class="item">
      <h4>table组件方法</h4>
      <table width="100%">
        <thead>
          <tr>
            <th>参数</th>
            <th>说明</th>
            <th>类型</th>
            <th>可选值</th>
            <th>默认</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>toggleAllRowSelection(selected)</td>
            <td>用于多选表格，清空用户的选择 或者 手动全选</td>
            <td>Function</td>
            <td>参数1：selected：true代表全选；false代表清空全选</td>
            <td>-</td>
          </tr>
          <tr>
            <td>toggleRowSelection(row,selected)</td>
            <td>用于多选表格，切换某一行的选中状态，如果使用了第二个参数，则是设置这一行选中与否（selected 为 true 则选中）</td>
            <td>Function</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>inverseSelection()</td>
            <td>用于多选表格，用于反选表格的复选框状态</td>
            <td>Function</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>setCurrentRow(row)</td>
            <td>设定某一行 为选中行（用于单行的选中状态），如果调用时不加参数，则会取消目前高亮行的选中状态。</td>
            <td>Function</td>
            <td>参数说明：参数1：要设置选中行的数据(Object),可为空（代表取消）</td>
            <td>-</td>
          </tr>
          <tr>
            <td>comTableMove(obj={left:0,top:0})</td>
            <td>设置表格滚动条的位置,没有传参数的话 默认将滚动条设置到left:0/top:0</td>
            <td>Function</td>
            <td>参数说明：参数1：滚动条位置属性（Object）</td>
            <td>{left:0,top:0}</td>
          </tr>
          <tr>
            <td>getSelectArr()</td>
            <td>获取表格当前复选框选中行的数据(return {Array})</td>
            <td>Function</td>
            <td>参数说明：无</td>
            <td>-</td>
          </tr>
          <tr>
            <td>doFilterData(fn)</td>
            <td>按一定的条件过滤行的信息 fn 回调函数（该回调函数返回true,代表显示该行）</td>
            <td>Function</td>
            <td>参数说明：无</td>
            <td>-</td>
          </tr>
          <tr>
            <td>tableToExcel(tableData,fileName,worksheet,tableConfig)</td>
            <td>导出excel方法。</td>
            <td>Function</td>
            <td>参数说明：1.tableData(必填):要导出表格的数据；2.fileName(非必填):文档名称；3.worksheet(非必填)：文档里面tab的名称；4.tableConfig(非必填):自定义表头的信息，没填代表使用当前表格组件的表头</td>
            <td>-</td>
          </tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
     <div class="item">
      <h4>table组件事件</h4>
      <table width="100%">
        <thead>
          <tr>
            <th>参数</th>
            <th>说明</th>
            <th>类型</th>
            <th>可选值</th>
            <th>默认</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>@operate-click</td>
            <td>操作列点击事件（operateflag为true有效，重要）</td>
            <td>返回参数说明：参数1：event 参数2：当前行信息; 参数3：当前行索引index</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@row-click</td>
            <td>行单击事件</td>
            <td>返回参数说明：参数1：当前行信息； 参数2：event; 参数3：当前行索引index</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@row-dblclick</td>
            <td>行双击事件</td>
            <td>返回参数说明：参数1：当前行信息； 参数2：event; 参数3：当前行索引index</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@col-click</td>
            <td>列单击事件</td>
            <td>返回参数说明：参数1：{row,column,event,index}。当前行信息/当前列配置信息/event/当前行索引index</td>
            <td>event.stopPropagation()可以阻止@row-click事件的触发</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@col-dblclick</td>
            <td>列双击事件</td>
            <td>返回参数说明：参数1：{row,column,event,index}。当前行信息/当前列配置信息/event/当前行索引index</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@select</td>
            <td>点击复选框事件（checkbox为true有效）</td>
            <td>返回参数说明：参数1：当前所有选中的行(Array); 参数2：当前行信息; 参数3：当前行索引index</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@select-all</td>
            <td>点击全选框事件（checkbox为true有效）</td>
            <td>返回参数说明：参数1：当前所有选中的行(Array);</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@change-strip</td>
            <td>分页的每页显示条数改变事件（pageflag为ture有效）</td>
            <td>返回参数说明：参数1：每页显示的条数</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@change-page</td>
            <td>第几页变化事件</td>
            <td>返回参数说明：参数1：当前第几页</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@scrollafter</td>
            <td>滚动条事件</td>
            <td>返回参数说明：参数1：e(同 zoehis-scrollbar组件的 scrollafter事件)</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@sort-change</td>
            <td>排序的事件（异步，通过调用接口，改变table的数据来达到排序的效果）（配合 sort-attr 以及 dataItemSort使用，只有这两属性有配置才会触发该事件）</td>
            <td>返回参数说明：参数1：当前列的配置信息；参数2：升序还是降序（up/down）</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@confirm-filter</td>
            <td>过滤弹窗点击确认后的事件</td>
            <td>返回参数说明：参数1：当前过滤弹窗上的数据（格式是数组Array）</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>@select-num</td>
            <td>已选条数事件</td>
            <td>返回参数说明：参数1：当前已选的条数</td>
            <td>-</td>
            <td>-</td>
          </tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
    <div class="item">
      <h4>:configs属性 :configs=[{....以下参数},{...}]</h4>
      <table width="100%">
        <thead>
          <tr>
            <th>参数</th>
            <th>说明</th>
            <th>类型</th>
            <th>可选值</th>
            <th>默认</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>dataItemCode</td>
            <td>
              列的显示字段，这个是对应表格数据里要显示的那个字段值
              <strong>(*必填)</strong>
            </td>
            <td>String</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>dataItemName</td>
            <td>
              表头显示的名称
              <strong>(*必填)</strong>
            </td>
            <td>String</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>patientItemName</td>
            <td>表头显示内容需不需要合并，有两个及两以上设置一样的值，将会做合并显示</td>
            <td>String</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>sortNo</td>
            <td>排序号，大于0， 用于设置列的位置(*必填)</td>
            <td>String</td>
            <td>1,2,3...</td>
            <td>-</td>
          </tr>
          <tr>
            <td>validFlag</td>
            <td>是否有效。 “1”: 代表有效; “0”:代表无效。无效则不需在页面显示</td>
            <td>String</td>
            <td>1/0</td>
            <td>-</td>
          </tr>
          <tr>
            <td>dataItemSort</td>
            <td>当前字段是否需要前端进行手工排序功能。"1":需要; "0":不需要。</td>
            <td>String</td>
            <td>1/0</td>
            <td>-</td>
          </tr>
          <tr>
            <td>dataTextAlign</td>
            <td>内容的对齐方式。</td>
            <td>String</td>
            <td>left/right/center。也可以不填，则默认为 center</td>
            <td>center</td>
          </tr>
          <tr>
            <td>itemWidth</td>
            <td>
              列的宽度（单位px）
              <strong>(*必填)</strong>。
            </td>
            <td>String</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>filterType</td>
            <td>前端常用的过滤器类型.</td>
            <td>String</td>
            <td>
              值1:"other".自定义过滤方法(与:do-filter-func结合使用)；值2.为空时默认返回“-”($filterEmpty)；值3.时间格式.用于格式化时间（$filterTime如:yyyy-MM-dd HH:mm:ss,注意要使用大写的
              <strong>HH</strong>）；
              <strong>小写的h是12小时制的</strong>
            </td>
            <td>-</td>
          </tr>
          <tr>
            <td>fixedFlag</td>
            <td>固定列类型。</td>
            <td>String</td>
            <td>""/left/right。分别表示：正常列/左固定列/右固定列</td>
            <td>-</td>
          </tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
    <div class="item">
      <h4>插槽说明</h4>
      <table width="100%">
        <thead>
          <tr>
            <th>参数</th>
            <th>说明</th>
            <th>类型</th>
            <th>可选值</th>
            <th>默认</th>
          </tr>
        </thead>
        <tbody>
          <tr>
            <td>slot="drugName"</td>
            <td>表头插槽使用，drugName为:config配置里dataItemCode的值</td>
            <td>String</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>slot="td_drugName"</td>
            <td>
              行内可编辑功能插槽的使用，drugName为:config配置里dataItemCode的值，与表头插槽不一样的是多了前缀
              <strong>td_</strong>。
            </td>
            <td>String</td>
            <td>-</td>
            <td>-</td>
          </tr>
          <tr>
            <td>slot-scope="{item,index}"</td>
            <td>
              配合行内可编辑功能插槽的使用，
              <strong>item</strong>为表格行的数据，
              <strong>index</strong>为表格的行号
            </td>
            <td>Object</td>
            <td>-</td>
            <td>-</td>
          </tr>
        </tbody>
      </table>
      <div style="clear: both"></div>
    </div>
    <div class="item">
      <h4>表格展示的数据(:data="demoData1") 和 表格配置数据(:config="demoConfig1")</h4>
      <div class="item-content">
        <pre><code>
            &lt;script&gt;
            mounted(){
              //表格配置源数据
              this.demoConfig1=[
                {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"left",patientItemName:"",dataItemCode:"drugName",dataItemName:"药品名称",filterType:"",itemWidth:"260px",fixedFlag:""},
                {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"数量单位",dataItemCode:"packageSpec",dataItemName:"数量",filterType:"",itemWidth:"100px",fixedFlag:""},
                {sortNo:2,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"数量单位",dataItemCode:"packageUnit",dataItemName:"单位",filterType:"",itemWidth:"100px",fixedFlag:""},
                {sortNo:3,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"drugSpec",dataItemName:"规格",filterType:"",itemWidth:"200px",fixedFlag:""},
                {sortNo:4,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"modifiedTime",dataItemName:"创建时间",filterType:"yyyy-MM-dd hh:mm:ss",itemWidth:"300px",fixedFlag:""},
              ];
              //表格配置源数据
              this.demoConfig2=[
                {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"left",patientItemName:"",dataItemCode:"drugName",dataItemName:"药品名称",filterType:"",itemWidth:"260px",fixedFlag:"left"},
                {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"数量单位",dataItemCode:"packageSpec",dataItemName:"数量",filterType:"",itemWidth:"100px",fixedFlag:""},
                {sortNo:2,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"数量单位",dataItemCode:"packageUnit",dataItemName:"单位",filterType:"",itemWidth:"100px",fixedFlag:""},
                {sortNo:3,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"drugSpec",dataItemName:"规格",filterType:"",itemWidth:"200px",fixedFlag:""},
                {sortNo:4,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"modifiedTime",dataItemName:"创建时间",filterType:"yyyy-MM-dd hh:mm:ss",itemWidth:"300px",fixedFlag:""},
              ];

              //表格展示源数据
              let curTime = new Date().getTime();
              for(let i=0;i<10;i++){
                let item = {
                  drugCode:"1_"+i,//药品代码
                  drugName:"药品名称_"+i,//药品名称
                  packageSpec:1+i,//包装数量
                  packageUnit:"盒",//包装单位
                  drugSpec:"1ml:50万IU/10瓶/1盒",//药品规格
                  modifiedTime:curTime+i*1000*60*60*24 //创建时间（时间戳）
                }
                //循环30条
                this.demoData1.push(item);
                this.demoData3.push(item);
              }
              for(let i=0;i<3;i++){
                let item = {
                  drugCode:"1_"+i,drugName:"药品名称_"+i,packageSpec:1+i,
                  packageUnit:"盒",packageUnitCode:i,
                  drugSpec:"1ml:50万IU/10瓶/1盒",modifiedTime:curTime+i*1000*60*60*24
                }
                this.demoData2.push(item);
              }
            }
            &lt;/script&gt;
          </code></pre>
      </div>
    </div>
  </div>
</template>

<script type="text/babel">
export default {
  data() {
    return {
      demoData1: [],
      demoData3: [],
      demoData2: [],
      demoConfig1: [],
      demoConfig2: [],
      query: "",
      curIndex: -1,
      unitList: []
    };
  },
  methods: {
    /**
     * 已选条数改变事件
     */
    selectNumEn(val){
      console.log("已选：",val,"条");
    },
    /**
     * 导出excel文档
     */
    handleToExcel() {
      //造文档数据
      let tem = [];
      for (let i = 0; i < 4500; i++) {
        tem.push({
          drugCode: "1_" + i,
          drugName: "药品名称_" + i,
          packageSpec: 1 + i,
          packageUnit: "盒",
          packageUnitCode: i,
          drugSpec: "1ml:50万IU/10瓶/1盒",
          modifiedTime: i * 1000 * 60 * 60 * 24
        });
      }
      this.$refs.zoeTableHide.tableToExcel(tem,"自定义文件名","自定义tab名称");
    },
    //过滤表格信息
    doFilterData() {
      this.$refs.zoeTable.doFilterData(d => {
        //d: 每一行的信息{Object}
        //触发table.length次,return true：代表显示当前行
        return d.packageSpec > 5;
      });
    },
    //打开过滤弹窗
    filterOpenEn() {
      this.$refs.filterRef.openFilter();
    },
    //      //清空过滤弹窗的数据
    //      filterClearEn(){
    //        this.$refs.filterRef.clearFilterList();
    //      },
    //清空按钮确认事件
    confirmFilterEn(arr) {
      console.log("过滤弹窗选则的数据", arr);
    },

    //反选
    inverseSelectionEn() {
      this.$refs.zoeTable.inverseSelection();
    },
    //全选/清空全选
    toggleAllRowSelection() {
      this.allSelectFlag = !this.allSelectFlag;
      this.$refs.zoeTable.toggleAllRowSelection(this.allSelectFlag);
    },
    //选中某一行（取消选中某一行）
    setCurrentRow() {
      this.$refs.zoeTable.setCurrentRow();
    },

    //操作列点击事件
    operateClickEn(event, item) {
      this.$messageBox.success(
        item.drugName + "。data-id:" + event.target.dataset.id
      );
    },
    //构造操作列
    operateHtml(item) {
      return "<i data-id='1' class='zoeIconfont z_tableR_normal'></i><i data-id='2' class='zoeIconfont z_print_normal'></i>";
    },
    //单击事件
    rowClickEn(item) {
      console.log("当前行：", item);
    },
    //双击事件
    rowDblClickEn(item) {
      console.log("双击当前行：", item);
    },
    //单击事件
    colClickEn(data) {
      console.log("当前列：", data);
    },
    //双击事件
    colDblClickEn(data) {
      console.log("双击当前列：", data);
    },
    //滚动条事件
    scrollafterEn(e) {
      let { clientHeight, scrollHeight, scrollTop } = e;
      //主要用于处理数据分页功能
      if (scrollHeight - clientHeight - scrollTop < 10) {
        console.log("向下滚动");
      }
    },
    renderHtml(val, item, config) {
      if (config.dataItemCode == "quantity") {
        return (
          "<span style='color: red;'>render-html加的quantity内容：</span>" + val
        );
      } else {
        return (
          "<span style='color: red;'>render-html加的drugName内容：</span>" + val
        );
      }
    },
    //自定义排序
    doSortFunc(arr, config, flag) {
      //处理数量的自定义排序 根据 :sort-attr属性配置判断
      if (config.dataItemCode == "quantity") {
        if (flag == "up") {
          console.log(
            "点击自定义排序,列code值：",
            config.dataItemCode,
            "升降序：",
            flag
          );
          return arr.sort((a, b) => {
            return ("" + a[config.dataItemCode]).localeCompare(
              b[config.dataItemCode]
            );
          });
        } else {
          console.log(
            "点击自定义排序,列code值：",
            config.dataItemCode,
            "升降序：",
            flag
          );
          return arr.sort((a, b) => {
            return ("" + b[config.dataItemCode]).localeCompare(
              a[config.dataItemCode]
            );
          });
        }
      } else {
        //其他属性
      }
    },
    doFilterFunc(val, item, config) {
      return val + "岁";
    },
    /**
     * 编辑表格数据数据改变事件
     */
    handleChange(data) {
      if (data[this.curIndex]) {
        data[this.curIndex].uiRowChangeFlag = true;
      }
    },
    /**
     * 添加数据
     * @param type: 1代表插入单条，2:代表插入批量
     */
    insertRow(type = 1) {
      let curTime = new Date().getTime();
      let newRow;
      //参数1.准备添加的数据
      if (type == 1) {
        //插入单条数据
        newRow = {
          drugCode: curTime,
          drugName: "阿法骨化醇软胶囊" + curTime,
          packageSpec: curTime,
          packageUnit: "盒",
          packageUnitCode: curTime,
          drugSpec: "1ml:10瓶/1盒",
          modifiedTime: curTime
        };
      } else {
        newRow = [];
        for (let i = 0; i < 3; i++) {
          newRow.push({
            drugCode: curTime + i,
            drugName: "阿法骨化醇软胶囊" + curTime,
            packageSpec: curTime,
            packageUnit: "盒",
            packageUnitCode: curTime + i,
            drugSpec: "1ml:10瓶/1盒",
            modifiedTime: curTime
          });
        }
      }

      //参数2.以某一条为依据，
      let curRow = "";
      //可以是包含 keycode对应的值(此处keycode值是 drugCode与drugName)
      curRow = {
        drugCode: "1_1",
        drugName: "药品名称_1",
        packageUnit: "其他数据没有关系"
      }; //简单点就直接取table表格数据:data内的某一条数据
      //直接取索引值也可以
      //        curRow=2;//index为2，即第三条数据

      //参数3.是否要为newRow设置复选框选中状态(默认不选中)
      let isCheckFlag = false;
      this.$refs.editTable1.insertBefore(newRow, curRow, isCheckFlag);
    },
    /**
     * 删除数据
     * @param type: 1代表删除单条，2:代表批量删除
     */
    deleteRow(type) {
      //参数1.准备删除的数据（主要要包含 keycode）
      let curRow = "";
      if (type == 1) {
        //对象 Object
        curRow = {
          drugCode: "1_1",
          drugName: "药品名称_1",
          packageUnit: "其他数据没有关系"
        }; //简单点就直接取table表格数据:data内的某一条数据
        //Number 索引
        //          curRow = 0;//删除第一条
      } else {
        curRow = [
          {
            drugCode: "1_0",
            drugName: "药品名称_0",
            packageUnit: "其他数据没有关系"
          },
          {
            drugCode: "1_1",
            drugName: "药品名称_1",
            packageUnit: "其他数据没有关系"
          }
        ];
      }
      this.$refs.editTable1.deleteRow(curRow);
    },
    //编辑
    editRow() {
      let curRow = {
        drugCode: "1_2",
        drugName: "药品名称_2",
        packageUnit: "其他数据没有关系"
      };
      this.$refs.editTable1.editRow(curRow, {
        drugSpec: "规格在变" + new Date().getTime()
      });
    },
    //滚动分页
    editScrollEn(e) {
      let { clientHeight, scrollHeight, scrollTop } = e;
      if (this.oldScrollTop === scrollTop) return; //垂直top值不变，说明是横向滚动触发的
      this.oldScrollTop = scrollTop;
      //主要用于处理数据分页功能
      if (scrollHeight - clientHeight - scrollTop < 10) {
        this.getDataByPage();
      }
    },
    getDataByPage() {
      if (this.editTableDoingFlag) return; //阻止多次快速调用
      this.editTableDoingFlag = true;
      let loading = this.$loading({ text: "正在拼命的加载中" });
      //模仿ajax异步请求
      new Promise((resolve, reject) => {
        let l = this.$refs.pageTable.getData().length; //当前表格的长度
        setTimeout(() => {
          let arr = [];
          for (let i = l; i < l + 5; i++) {
            let item = {
              drugCode: "1_" + i,
              drugName: "药品名称_" + i,
              packageSpec: 1 + i,
              packageUnit: "盒",
              packageUnitCode: i,
              drugSpec: "1ml:50万IU/10瓶/1盒",
              modifiedTime: new Date().getTime() + i * 1000 * 60 * 60 * 24
            };
            arr.push(item);
          }
          resolve(arr);
        }, 500);
      })
        .then(res => {
          this.$refs.pageTable.insertAfter(res, ""); //在表格最后添加数据
          this.$nextTick(_ => {
            loading && loading.close();
            this.editTableDoingFlag = false;
          });
        })
        .catch(e => {
          this.$nextTick(_ => {
            loading && loading.close();
            this.editTableDoingFlag = false;
          });
        });
    },
    //重新设置表格的数据
    handelEditReset() {
      let curTime = new Date().getTime();
