          drugCode: "1_" + i,
          drugName: "药品名称_" + i,
          packageSpec: 1 + i,
          packageUnit: "盒",
          packageUnitCode: i,
          drugSpec: "1ml:50万IU/10瓶/1盒",
          modifiedTime: curTime + i * 1000 * 60 * 60 * 24
        };
        arr.push(item);
      }
      this.$refs.pageTable.comTableMove({ top: 0 }); //将滚动条重置回原位
      this.demoData3 = arr; //再设置数据
    },
    handleToggle(e) {
      $(e.target)
        .next()
        .find("pre code")
        .toggle();
    }
