<template>
    <div
      class="zoehis_multiple_select_dropdown" tabindex="-1" @focus="inputFocusEvn" @blur="inputBlurEvn"
      @keydown.down="navigateOptions('next')" @keydown.up="navigateOptions('prev')"
      @keyup.enter="keyupEnterEvn"
      :style="{minWidth:minWidth,maxWidth:maxWidth}"
    >
      <div style="padding: 5px; border-bottom: 1px solid #ddd;">
        <zoehis-input
          width="100%" :ime-mode="imeMode"
          v-model="selectedLabel" ref="multipleQuery"
          @focus="inputFocusEvn" @blur="inputBlurEvn"
          @keyup.native="handleKeyup" :clickselect="false"
        ></zoehis-input>
      </div>

      <zoehis-select-dropdown
        tabindex="-1" multiple class="zoehis_multiple" @focus.native="inputFocusEvn"
      >
        <zoehis-scrollbar
          tag="ul"
          wrap-class="zoehis_select_dropdown__wrap"
          ref="scrollbar"
          @scrollafter="scrollafter"
        >
          <zoehis-option
            multiple
            v-for="(item, index) in selectdata"
            :item="item"
            :key="item[itemkey]"
            :value="item[itemcode]"
            :label="item[itemtext]"
            :itemindex="index"
          >
            <span v-if="(typeof renderItem=='function')" v-html="renderItem(item)"></span>
          </zoehis-option>
        </zoehis-scrollbar>
        <p class="zoehis_select_dropdown__empty" v-if="isShowNone"  @click="focus">{{ noMatchText }}</p>
      </zoehis-select-dropdown>
    </div>
</template>

<script type="text/ecmascript-6">
  import Popper from '../../../src/utils/vue-popper';
  import Emitter from '../../../src/mixins/emitter';
  import ZoehisInput from '../../input/index.js'
  import ZoehisOption from '../../select/src/option.vue'
  import ZoehisSelectDropdown from '../../select/src/select-dropdown.vue'
  import ZoehisScrollbar from '../../scrollbar/index.js'
  import scrollIntoView from '../../../src/utils/scroll-into-view.js'
  import debounce from '../../../src/utils/throttle-debounce.js';


  export default{
    name: "ZoehisMultipleSelectDropdown",
    componentName: "ZoehisMultipleSelectDropdown",
    mixins: [Emitter,Popper],
    components: {
      ZoehisInput,
      ZoehisScrollbar,
      ZoehisOption,
      ZoehisSelectDropdown,
    },
    created: function () {
      this.selected = [];//存放选中的对象
      //存放所有的option vue对象
      this.options=[];
      //下拉的所有数据
      this.cachedOptions=[];
      //下拉数据的长度
      this.optionsCount=0;


      //后端过滤
      this.debounceFilterMethod = debounce(this.delayTime,(val)=>{
        this.hoverIndex = -1;
        this.filterMethod(val);
      });

      this.$on('handleOptionClick', this.beforeOptionSelect); //监听选中事件
      this.$on('onOptionDestroy', this.onOptionDestroy);
    },
    methods:{
      keyupEnterEvn(){
        this.beforeOptionSelect(this.options[this.hoverIndex])
      },
      //上下快捷键
      navigateOptions(direction) {
        //没有option对象（数据），则返回
        if (this.options.length === 0 || this.filteredOptionsCount === 0) return;
        //获取
        this.optionsAllDisabled = this.options.length === this.options.filter(item => item.disabled === true).length;
        if (!this.optionsAllDisabled) {
          if (direction === 'next') {
            this.hoverIndex++;
            //已经到了最后一个了，则重新定位到第一个 hoverIndex=0
            if (this.hoverIndex === this.options.length) {
              this.hoverIndex = this.options.length-1;
              return;
            }
            //若是不可以点击的、组的、隐藏的，则进行下一个定位
            if (this.options[this.hoverIndex].disabled === true ||
              !this.options[this.hoverIndex].visible) {
              this.navigateOptions('next');
            }
          }
          if (direction === 'prev') {
            this.hoverIndex--;
            //到达第一个了，则跳到最后一个
            if (this.hoverIndex < 0) {
              this.hoverIndex = -1;
            }else {
              //若是不可以点击的、组的、隐藏的，则进行下一个定位
              if (this.options[this.hoverIndex].disabled === true ||
                !this.options[this.hoverIndex].visible) {
                this.navigateOptions('prev');
              }
            }
          }
        }
        this.hoverIndex !== -1 && this.$nextTick(() => this.scrollToOption(this.options[this.hoverIndex]));
      },
      //滚动条定位到相应的位置
      scrollToOption(p) {
        let option = p || this.selected;
        //没有选中项则定位到0
        if(!option) {
          this.$nextTick(()=>{
            this.scrollMove(0);
          });
          return;
        }
        const target = Array.isArray(option) && option[0] ? option[0].$el : option.$el;
        if (this.$refs.scrollbar && target) {
          const menu = this.$refs.scrollbar.$el.querySelector('.zoehis_select_dropdown__wrap');
          scrollIntoView(menu, target);
        }
      },
      //滚动条事件
      scrollafter(wrap){

        const [h,sh,st]=[wrap.clientHeight,wrap.scrollHeight,wrap.scrollTop];
        this.$emit("scroll-after",this.selectedLabel,this);
        //滚动条快滚到底事件
        if(sh-h-st<10){
          this.$emit("scroll-to-bottom",this.selectedLabel,this);
        }
        //滚动条滚快顶了的事件
        if(st<10){
          this.$emit("scroll-to-top",this.selectedLabel,this);
        }
      },
      //滚动条滚动位置
      scrollMove(num=0){
        this.$refs.scrollbar && this.$refs.scrollbar.move({top:num});
      },
      /**
       * 重新设置hoverIndex的值
       */
      resetHoverIndex() {
        if(!this.defaultfirst) return;//不默认选中的话 返回
        let nowIndex=-1;
        let dataLength = this.options.length;
        for(let i=0; i<dataLength; i++){
          if(this.options[i].visible){
            nowIndex=this.options[i].index;
            break;
          }
        }
        this.hoverIndex = nowIndex;
      },
      //输入框焦点事件
      inputFocusEvn(e){
        this.$emit("input-focus",e);
      },
      //输入框失焦事件
      inputBlurEvn(e){
        this.$emit("input-blur",e);
      },
      focus(){
        this.$refs.multipleQuery.focus();
      },
      //option: 当前选中的数据
      beforeOptionSelect(option){
        this.$emit("before-select");//触发点击前事件
        if(option && option.visible){
          if(typeof this.beforeSelect === 'function' && this.beforeSelect(option.value, option.item)===false){

          }else {
            //当前值是选中状态
            if(option.itemSelected){
              this.removeSelectItem(option.value);
            }else {
              this.addSelectItem(option.item);
            }
          }
        }
        this.focus();//焦点定位到输入框
      },
      //移除选中项 通过code的值
      addSelectItem(item={}){
        this.selected.push(Object.assign({},item));
        this.$emit("selectChange",this.selected);
      },
      //移除选中项 通过code的值
      removeSelectItem(code){
        for(let i=0,l=this.selected.length; i<l; i++){
          if(this.selected[i][this.itemcode]==code){
            this.selected.splice(i,1);
            break;
          }
        }
        this.$emit("selectChange",this.selected);
      },
      //清空值
      clearSelectItem(){
        this.selected=[];
        this.$emit("selectChange",this.selected);
      },
      //销毁选项
      onOptionDestroy(option) {
        this.optionsCount--;
        this.filteredOptionsCount--;
        let index = this.options.indexOf(option);
        if (index > -1) {
          this.options.splice(index, 1);
          this.cachedOptions.splice(index, 1);
        }
      },
      handleKeyup(e){
        //阻止向上、向下快捷键的冒泡
        if(e.keyCode==38 || e.keyCode==40){
          e.stopPropagation();
        }
        if(e.keyCode==27){
          return;
        }
        switch (e.keyCode){
          case 13://enter
          case 37://left
          case 38://top
          case 39://right
          case 40://down
          case 9://tab
          case 16: //shift
          case 17: //ctrl
          case 20: //alt
            break;
          default:
            this.onInputChange(e.target.value);
        }
      },
      //模糊查询
      onInputChange(val) {
        //多选支持模糊查询
        if (typeof this.filterMethod === 'function') {
          this.debounceFilterMethod(val);
        } else { //内部自动检索
          this.filteredOptionsCount = this.optionsCount;
          this.broadcast('ZoehisOption', 'queryChange', val);
        }
        this.$nextTick(()=>{
          this.scrollMove();
          this.resetHoverIndex();//重新设置hoverIndex值
        });

      },

      //设置选中的值
      setSelectVal(arr){
        this.selected=arr;
        this.selectcode=[];
        for(let i=0,l=arr.length; i<l; i++){
          this.selectcode.push(arr[i][this.itemcode]);
        }
      },

    },
    mounted(){
      this.referenceElm = this.$parent.$refs.reference; //父组件的input输入框的dom元素
      this.$parent.popperElm = this.popperElm = this.$el;
      this.$on('updatePopper', () => {
        if (this.$parent.visible) this.updatePopper();
      });
      this.$nextTick(()=>{
        this.scrollMove();
        this.resetHoverIndex();

      });

    },
    watch:{
      selectdata:{
        deep:true,
        handler(val){
          //重新计算滚动条的长度 大小
          this.$refs.scrollbar && this.$refs.scrollbar.update();
        }
      }
    },
    computed:{
      isShowNone(){
        return this.filteredOptionsCount===0;
      },
    },
    data() {
      return {
        selectcode:[],//选中code的值 [code,code]

        isSelect: true,//标识这是个Select组件，主要用于option组件的查找
        hoverIndex: -1,//选中的索引值
        selectedLabel:"",  //查询的条件
        filteredOptionsCount: 0,//下拉显示数据的长度
        multiple:true,
      }
    },
    props: {
      selectdata:{//下拉的所有数据
        type:Array,
        default(){
          return  [];
        }
      },
      itemcode:{ //下拉的选项的唯一值
        type:String,
        default:"id"
      },
      itemtext:{//下拉选项的文本值
        type:String,
        default:"text"
      },
      itemkey:{ //下拉的选项的唯一值
        type:String,
        default(){
          return this.itemcode;
        }
      },
      filterField:{//需要过滤的几个字段  默认为：名称、code值、拼音码、五笔码
        type:Array,
        default(){
          return [this.itemtext,this.itemcode,"spellCode","wbzxCode"];
        }
      },
      minWidth: [String,Number],//最小宽度（带单位的）
      maxWidth: [String,Number],//最大宽度（带单位的）
      renderItem:Function,
      noMatchText: {
        default(){
          if(this.$t && (typeof this.$t == "function")){
            return this.$t('zoehisUI.select.noMatchText');
          }else {
            return "查无数据";
          }
        },
        type:String
      },
      defaultfirst:{
        type:Boolean,
        default:true
      },
      delayTime: {    //模糊查询的延时时间（只对filterMethod方法有效）
        type:[String,Number],
        default:200
      },
      filterMethod: Function,//自定模糊查询回调
      preventOverId:{
        type:String,
        default:""
      },
      filterRowData: Function,//自定义显示的内容
      beforeSelect: Function,//选中前钩子
      imeMode:{   //原始属性 ime-mode （输入法引擎）
        type:String,
        default:"auto"
      },



      //定位的参数
      placement: {
        default: 'bottom-start'
      },
      boundariesPadding: {
        default: 0
      },
      popperOptions: {
        default() {
          return {
            forceAbsolute: true,
            gpuAcceleration: false
          };
        }
      },
    },
    beforeDestroy(){
      //如果输入框有值 则要调用模糊查询 filterMethod
      if(this.selectedLabel && typeof this.filterMethod === 'function'){
        this.debounceFilterMethod("");
      }
    }
  }
</script>


