<template>
  <div class="component_example">
    <h1>Slider 滑块</h1>
    <p>能快速选中一定范围值的组件</p>
    <div class="item">
      <h4>基本用法</h4>
      <div class="item-content">
        <div class="t_block">
          <span>默认</span>
          <zoehis-slider v-model="numVal"></zoehis-slider>
        </div>
        <div class="t_block">
          <span>禁用</span>
          <zoehis-slider v-model="numVal2" disabled></zoehis-slider>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>v-model</strong>同步更新滑块的值，可用于设置初始值。<strong>disabled</strong>用于控制是否禁用滑块
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;默认&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;禁用&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal2&quot; disabled&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal:50,
                  numVal2:50,
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>显示右侧内容</h4>
      <div class="item-content">
        <div class="t_block">
          <span>显示右侧内容</span>
          <zoehis-slider v-model="numVal3" show-input></zoehis-slider>
        </div>
        <div class="t_block">
          <span>格式化内容</span>
          <zoehis-slider v-model="numVal4" show-input :format-tooltip="formatTooltip"></zoehis-slider>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>show-input</strong>属性用于控制右侧描述内容的显示。<strong>:format-tooltip</strong>用于格式化显示的内容。
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;显示右侧内容&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal3&quot; show-input&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;格式化内容&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal4&quot; show-input :format-tooltip=&quot;formatTooltip&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal3:50,
                  numVal4:50,
                }
              },
              methods:{
                formatTooltip(val){
                  return val/100;
                },
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>步数控制</h4>
      <div class="item-content">
        <div class="t_block">
          <div class="t_block">
            <span>步数（10）</span>
            <zoehis-slider v-model="numVal5" show-input :step='10'></zoehis-slider>
          </div>
          <div class="t_block">
            <span>步数（0.02）</span>
            <zoehis-slider v-model="numVal6" show-input :step='0.02'></zoehis-slider>
          </div>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>step</strong>控制滑块每次滑动的步长值，类型为 Number。
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;步数（10）&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal5&quot; show-input :step=&quot;10&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;步数（0.02）&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal6&quot; show-input :step=&quot;0.02&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal5:50,
                  numVal6:50,
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>最大最小值</h4>
      <div class="item-content">
        <div class="t_block">
          <div class="t_block">
            <zoehis-slider v-model="numVal7" show-input :min='-20' :max='200'></zoehis-slider>
          </div>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>min</strong>/<strong>max</strong>设置最大最小值，类型为 Number。
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;zoehis-slider v-model=&quot;numVal7&quot; show-input :min=&quot;-20&quot; :max=&quot;200&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal7:50,
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>竖向滑块</h4>
      <div class="item-content">
        <span>外框设置height值</span>
        <div class="t_block" style="height:200px;">
          <zoehis-slider v-model="numVal9" show-input vertical></zoehis-slider>
        </div>
        <div class="t_block">
          <span>组件设置height的值</span>
          <zoehis-slider v-model="numVal9" vertical height="160px"></zoehis-slider>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>vertical</strong>设置是否竖向展示。<strong>height</strong>针对vertical=true有效，也可以不设置，通过外框控制。
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;span&gt;外框设置height值&lt;/span&gt;
              &lt;div class=&quot;t_block&quot; style=&quot;height:200px;&quot;&gt;
                &lt;zoehis-slider v-model=&quot;numVal9&quot; show-input vertical&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;span&gt;组件设置height的值&lt;/span&gt;
                &lt;zoehis-slider v-model=&quot;numVal9&quot; vertical height=&quot;160px&quot;&gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal9:50,
                }
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item">
      <h4>相关事件</h4>
      <div class="item-content">
        <div class="t_block">
          <div class="t_block">
            <zoehis-slider v-model="numVal8" @change="handleChange" @input="handleInput"></zoehis-slider>
          </div>
        </div>
      </div>
      <div class="item-detail clearflex">
        <div class="item-desc">
          <div>
            <strong>@change</strong>/<strong>@input</strong>值改变时触发的事件
          </div>
        </div>
        <div class="item-toggle" @click="handleToggle">展开/收起代码</div>
        <div class="item-code">
          <pre><code>
            &lt;template&gt;
              &lt;div class=&quot;t_block&quot;&gt;
                &lt;zoehis-slider v-model=&quot;numVal8&quot; @change=&quot;handleChange&quot; @input=&quot;handleInput&quot; &gt;&lt;/zoehis-slider&gt;
              &lt;/div&gt;
            &lt;/template&gt;
            &lt;script&gt;
              data () {
                return {
                  numVal8:50,
                }
              },
              methods:{
                handleChange(val){
                  console.log(&quot;拖动结束后触发：&quot;,val);
                },
                handleInput(val){
                  console.log(&quot;拖动过程中实时触发：&quot;,val);
                },
              }
            &lt;/script&gt;
          </code></pre>
        </div>
      </div>
    </div>
    <div class="item item_table">
        <h4>属性</h4>
        <zoehis-table keycode="code" :data="propData" :configs="demoConfig1"></zoehis-table>
        <h4>事件</h4>
        <zoehis-table keycode="code" :data="eventData" :configs="demoConfig2"></zoehis-table>
      </div>
  </div>
</template>
<script>
  export default {
    name: 'Slider',
    data () {
      return {
        numVal:50,
        numVal2:50,
        numVal3:50,
        numVal4:50,
        numVal5:50,
        numVal6:50,
        numVal7:50,
        numVal8:50,
        numVal9:50,
        propData:[],
        demoConfig1:[],
        demoConfig2:[],
        eventData:[],
      }
    },
    methods:{
      
      handleChange(val){
        console.log("拖动结束后触发：",val);
      },
      handleInput(val){
        console.log("拖动过程中实时触发：",val);
      },
      formatTooltip(val){
        return val/100;
      },

      handleToggle(e) {
        $(e.target)
          .next()
          .find("pre code")
          .toggle();
      }
    },
    mounted:function() { //用于高亮显示代码
      this.demoConfig1=[
        {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"parameter",dataItemName:"参数",filterType:"",itemWidth:"120px",fixedFlag:""},
        {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"left",patientItemName:"",dataItemCode:"explain",dataItemName:"说明",filterType:"",itemWidth:"340px",fixedFlag:""},
        {sortNo:2,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"type",dataItemName:"类型",filterType:"",itemWidth:"120px",fixedFlag:""},
        {sortNo:3,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"optional",dataItemName:"可选值",filterType:"",itemWidth:"180px",fixedFlag:""},
        {sortNo:4,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"defaultVal",dataItemName:"默认",filterType:"",itemWidth:"",fixedFlag:""},
      ];
      this.demoConfig2=[
        {sortNo:0,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"parameter",dataItemName:"名称",filterType:"",itemWidth:"80px",fixedFlag:""},
        {sortNo:1,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"explain",dataItemName:"说明",filterType:"",itemWidth:"300px",fixedFlag:""},
        {sortNo:2,validFlag:"1", dataItemSort:"1", dataTextAlign:"",patientItemName:"",dataItemCode:"param",dataItemName:"回调参数",filterType:"",itemWidth:"240px",fixedFlag:""},
      ];
      this.propData=[
        {code:1,parameter:"v-model='value'", explain:"绑定值", type:"number",optional:"-",defaultVal:"0"},
        {code:2,parameter:"min", explain:"最小值", type:"number",optional:"-",defaultVal:"0"},
        {code:9,parameter:"max", explain:"最大值", type:"number",optional:"-",defaultVal:"100"},
        {code:3,parameter:"disabled", explain:"是否禁用状态", type:"boolean",optional:"true/false",defaultVal:"false"},
        {code:4,parameter:"step", explain:"每步的长度", type:"number",optional:"-",defaultVal:"1"},
        {code:5,parameter:"show-input", explain:"是否显示右侧说明内容", type:"boolean",optional:"true/false",defaultVal:"false"},
        {code:6,parameter:"format-tooltip", explain:"格式化说明内容", type:"function(value)",optional:"-",defaultVal:""},
        {code:7,parameter:"vertical", explain:"是否竖向显示", type:"boolean",optional:"true/false",defaultVal:"false"},
        {code:8,parameter:"height", explain:"slider组件的高度（vertical=true有效）", type:"string",optional:"",defaultVal:""},
      ];
      this.eventData=[
        {code:1,parameter:"@change", explain:"值改变时触发（使用鼠标拖曳时，只在松开鼠标后触发）", param:"1.改变后的值(value)"},
        {code:2,parameter:"@input", explain:"值改变时触发（使用鼠标拖曳时，拖动过程中实时触发）", param:"1.改变后的值(value)"},
      ];

      $('pre code').each(function(i, block) {
        hljs.highlightBlock(block);
      });
    }
  }
</script>
<style scoped>
ul {
  margin: 0 0 0 20px;
  padding: 0;
}
.component_example .clearflex {
  display: block;
}
.component_example .item-toggle {
  cursor: pointer;
  background: #8fcae5;
  text-align: center;
  font-weight: bold;
}
.component_example .item-desc {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code {
  width: 100%;
  max-width: 100%;
}
.component_example .item-code pre code {
  display: none;
}
</style>


