<template>
  <div></div>
</template>
<script type="text/babel">
  import { downloadToExcel } from '../../../src/utils/util';
  export default {
    name: 'ZoehisFunc',
    componentName: 'ZoehisFunc',
    props: {},
    data(){
      return{}
    },
    created(){
      this.i18nObj={
        excelHeadNone:"表头数据不能为空！",
        excelDataNone:"表格数据不能为空！",
      };
      //兼容翻译的
      if(this.$t && (typeof this.$t == "function")){
        Object.assign(this.i18nObj,{
          excelHeadNone:this.$t('zoehisUI.table.excelHeadNone'),
          excelDataNone:this.$t('zoehisUI.table.excelDataNone')
        })
      }
    },
    methods:{
      /**
       * 导出excel表格
       * @param {Array} excelData: 要导出的表格的数据（此处为必填）
       * @param {String} fileName: excel表格的名称
       * @param {String} worksheet: excel表格里面的tab名称
       * @param {Array} tableConfig: 自定义的表格头部（此处为必填）
       */
      tableToExcel(excelData,fileName="导出",worksheet="Sheet1",tableConfig=[]){
        if(!tableConfig.length){
          this.$messageBox.warn(this.i18nObj.excelHeadNone);
          return;
        }
        if(!excelData.length){
          this.$messageBox.warn(this.i18nObj.excelDataNone);
          return;
        }
        //处理表头
        let head = [];
        head.push("<tr>");
        tableConfig.forEach(item=>{
          head.push(`<th>${item.dataItemName}</th>`);
        });
        head.push("</tr>");
        //组装内容
        excelData.forEach(item=>{
          head.push("<tr>");
          tableConfig.forEach(item2=>{
            head.push(`<td>${item[item2.dataItemCode]}</td>`);
          });
          head.push("</tr>");
        });
        downloadToExcel(head.join(""),fileName,worksheet);
      },

    }
  };
</script>



