<template>
  <div class="zoehis_slider"
    :class="{ 'is_vertical': vertical, 'zoehis_slider__with_input': showInput }"
     role="slider"
     :aria-valuemin="min"
     :aria-valuemax="max"
     :aria-orientation="vertical ? 'vertical': 'horizontal'"
     :aria-disabled="sliderDisabled"
     :style="runwayStyle"
  >
    <!-- <el-input-number
      v-model="firstValue"
      v-if="showInput && !range"
      class="zoehis_slider__input"
      ref="input"
      @change="$nextTick(emitChange)"
      :step="step"
      :disabled="sliderDisabled"
      :controls="showInputControls"
      :min="min"
      :max="max"
      :debounce="debounce"
      :size="inputSize">
    </el-input-number> -->
    <span class="zoehis_slider__input" v-if="showInput">{{formatValue}}</span>
    <div class="zoehis_slider__runway"
      :class="{ 'show_input': showInput, 'disabled': sliderDisabled }"
      @click="onSliderClick"
      ref="slider">
      <div
        class="zoehis_slider__bar"
        :style="barStyle">
      </div>
      <slider-button
        :vertical="vertical"
        v-model="firstValue"
        :tooltip-class="tooltipClass"
        ref="button1">
      </slider-button>
      <slider-button
        :vertical="vertical"
        v-model="secondValue"
        :tooltip-class="tooltipClass"
        ref="button2"
        v-if="range">
      </slider-button>
      <!-- 显示间断点 -->
      <div
        class="zoehis_slider__stop"
        v-for="(item, key) in stops"
        :key="key"
        :style="vertical ? { 'bottom': item + '%' } : { 'left': item + '%' }"
        v-if="showStops">
      </div>
    </div>
  </div>
</template>

<script type="text/babel">
  // import ElInputNumber from 'element-ui/packages/input-number';
  import SliderButton from './slider-button.vue';
  import Emitter from '../../../src/mixins/emitter';

  export default {
    name: 'ZoehisSlider',

    mixins: [Emitter],
    props: {
      min: {
        type: Number,
        default: 0
      },
      max: {
        type: Number,
        default: 100
      },
      step: {
        type: Number,
        default: 1
      },
      value: {
        type: [Number, Array],
        default: 0
      },
      showInput: {
        type: Boolean,
        default: false
      },
      showInputControls: {
        type: Boolean,
        default: true
      },
      inputSize: {
        type: String,
        default: 'small'
      },
      showStops: {
        type: Boolean,
        default: false
      },
      showTooltip: {
        type: Boolean,
        default: true
      },
      formatTooltip: Function,
      disabled: {
        type: Boolean,
        default: false
      },
      range: {
        type: Boolean,
        default: false
      },
      vertical: {
        type: Boolean,
        default: false
      },
      height: {
        type: String
      },
      debounce: {
        type: Number,
        default: 300
      },
      label: {
        type: String
      },
      tooltipClass: String
    },

    components: {
      // ElInputNumber,
      SliderButton
    },

    data() {
      return {
        firstValue: null,//当前值
        secondValue: null,//第二个值 主要用于范围
        oldValue: null,//上一次的值 主要用于判断值是否改变
        dragging: false,//true 代表鼠标按下，正在移动
        sliderSize: 1
      };
    },

    watch: {
      //更新值
      value(val, oldVal) {
        if (this.dragging ||
          Array.isArray(val) &&
          Array.isArray(oldVal) &&
          val.every((item, index) => item === oldVal[index])) {
          return;
        }
        this.setValues();
      },
      /**
       * 拖动结束值设置值
       */
      dragging(val) {
        if (!val) {
          console.log("val",this.value)
          this.setValues();
        }
      },

      firstValue(val) {
        console.log("vla",val)
        if (this.range) {
          this.$emit('input', [this.minValue, this.maxValue]);
        } else {
          this.$emit('input', val);
        }
      },

      secondValue() {
        if (this.range) {
          this.$emit('input', [this.minValue, this.maxValue]);
        }
      },

      min() {
        this.setValues();
      },

      max() {
        this.setValues();
      }
    },

    methods: {
      /**
       * 判断值是否已经改变。是否更原来的值不一样。
       * @return {Boolean}:true:代表已经改变
       */
      valueChanged() {
        if (this.range) {
          return ![this.minValue, this.maxValue]
            .every((item, index) => item === this.oldValue[index]);
        } else {
          return this.value !== this.oldValue;
        }
      },
      //设置值
      setValues() {
        if (this.min > this.max) {
          console.error('[Zoehis Error][Slider]min should not be greater than max.');
          return;
        }
        const val = this.value;
        //范围处理
        if (this.range && Array.isArray(val)) {
          if (val[1] < this.min) {
            this.$emit('input', [this.min, this.min]);
          } else if (val[0] > this.max) {
            this.$emit('input', [this.max, this.max]);
          } else if (val[0] < this.min) {
            this.$emit('input', [this.min, val[1]]);
          } else if (val[1] > this.max) {
            this.$emit('input', [val[0], this.max]);
          } else {
            this.firstValue = val[0];
            this.secondValue = val[1];
            if (this.valueChanged()) {
              this.oldValue = val.slice();
            }
          }
        //单值处理 val值必须为 number类型
        } else if (!this.range && typeof val === 'number' && !isNaN(val)) {
          if (val < this.min) {
            this.$emit('input', this.min);
          } else if (val > this.max) {
            this.$emit('input', this.max);
          } else {
            this.firstValue = val;
            console.log("wwww",val)
            if (this.valueChanged()) {
              this.oldValue = val;
            }
          }
        }
      },
      /**
       * 设置滑块的位置
       * @param {Number} percent: 0~100 代表当前值在100份中占了多少份
       */
      setPosition(percent) {
        const targetValue = this.min + percent * (this.max - this.min) / 100;
        if (!this.range) {
          this.$refs.button1.setPosition(percent);
          return;
        }
        let button;
        if (Math.abs(this.minValue - targetValue) < Math.abs(this.maxValue - targetValue)) {
          button = this.firstValue < this.secondValue ? 'button1' : 'button2';
        } else {
          button = this.firstValue > this.secondValue ? 'button1' : 'button2';
        }
        this.$refs[button].setPosition(percent);
      },
      /**
       * 点击设置滑块事件
       */
      onSliderClick(event) {
        if (this.sliderDisabled || this.dragging) return;
        this.resetSize();
        if (this.vertical) {
          const sliderOffsetBottom = this.$refs.slider.getBoundingClientRect().bottom;
          this.setPosition((sliderOffsetBottom - event.clientY) / this.sliderSize * 100);
        } else {
          const sliderOffsetLeft = this.$refs.slider.getBoundingClientRect().left;
          this.setPosition((event.clientX - sliderOffsetLeft) / this.sliderSize * 100);
        }
        this.emitChange();
      },
      /**
       * 设置当前滑块的总长度、总宽度
       */
      resetSize() {
        if (this.$refs.slider) {
          this.sliderSize = this.$refs.slider[`client${ this.vertical ? 'Height' : 'Width' }`];
        }
      },
      /**
       * 触发change事件
       */
      emitChange() {
        this.$nextTick(() => {
          this.$emit('change', this.range ? [this.minValue, this.maxValue] : this.value);
        });
      }
    },

    computed: {
       /**
       * 是否可以自定义title提示内容
       */
      enableFormat() {
        return this.formatTooltip instanceof Function;
      },
      /**
       * title提示内容自定义（简单的字符）
       */
      formatValue() {
        return this.enableFormat && this.formatTooltip(this.value) || this.value;
      },
      stops() {
        if (!this.showStops || this.min > this.max) return [];
        if (this.step === 0) {
          process.env.NODE_ENV !== 'production' &&
          console.warn('[Element Warn][Slider]step should not be 0.');
          return [];
        }
        const stopCount = (this.max - this.min) / this.step;
        const stepWidth = 100 * this.step / (this.max - this.min);
        const result = [];
        for (let i = 1; i < stopCount; i++) {
          result.push(i * stepWidth);
        }
        if (this.range) {
          return result.filter(step => {
            return step < 100 * (this.minValue - this.min) / (this.max - this.min) ||
              step > 100 * (this.maxValue - this.min) / (this.max - this.min);
          });
        } else {
          return result.filter(step => step > 100 * (this.firstValue - this.min) / (this.max - this.min));
        }
      },
      /**
       * 最小值（主要针对范围） 
       */
      minValue() {
        return Math.min(this.firstValue, this.secondValue);
      },
      /**
       * 最大值（主要针对范围） 
       */
      maxValue() {
        return Math.max(this.firstValue, this.secondValue);
      },
      /**
       * 当前选中的滑块所占比例
       */
      barSize() {
        return this.range
          ? `${ 100 * (this.maxValue - this.minValue) / (this.max - this.min) }%`
          : `${ 100 * (this.firstValue - this.min) / (this.max - this.min) }%`;
      },
      /**
       * 当前滑块选中的起始位置
       */
      barStart() {
        return this.range
          ? `${ 100 * (this.minValue - this.min) / (this.max - this.min) }%`
          : '0%';
      },
      /**
       * 获取 min/max/step 中小数点最多的位数
       */
      precision() {
        let precisions = [this.min, this.max, this.step].map(item => {
          let decimal = ('' + item).split('.')[1];
          return decimal ? decimal.length : 0;
        });
        return Math.max.apply(null, precisions);
      },

      runwayStyle() {
        return this.vertical ? { height: this.height } : {};
      },
      /**
       * 当前设置的滚动条样式
       */
      barStyle() {
        return this.vertical
          ? {
            height: this.barSize,
            bottom: this.barStart
          } : {
            width: this.barSize,
            left: this.barStart
          };
      },
      /**
       * 禁用状态
       */
      sliderDisabled() {
        return this.disabled;
      }
    },

    mounted() {
      let valuetext;
      if (this.range) {
        if (Array.isArray(this.value)) {
          this.firstValue = Math.max(this.min, this.value[0]);
          this.secondValue = Math.min(this.max, this.value[1]);
        } else {
          this.firstValue = this.min;
          this.secondValue = this.max;
        }
        this.oldValue = [this.firstValue, this.secondValue];
        valuetext = `${this.firstValue}-${this.secondValue}`;
      } else {
        if (typeof this.value !== 'number' || isNaN(this.value)) {
          this.firstValue = this.min;
        } else {
          this.firstValue = Math.min(this.max, Math.max(this.min, this.value));
        }
        this.oldValue = this.firstValue;
        valuetext = this.firstValue;
      }
      this.$el.setAttribute('aria-valuetext', valuetext);

      // label screen reader
      this.$el.setAttribute('aria-label', this.label ? this.label : `slider between ${this.min} and ${this.max}`);

      this.resetSize();
      window.addEventListener('resize', this.resetSize);
    },

    beforeDestroy() {
      window.removeEventListener('resize', this.resetSize);
    }
  };
</script>



