<template>
  <header class="zoehis_header" :style="{ height }">
    <slot></slot>
  </header>
</template>
<script type="text/babel">
export default {
  name:"ZoehisHeader",
  componentName:"ZoehisHeader",
  props:{
    height:{
      type:String,default:"60px"
    }
  }
}
</script>


