<template>
  <div
    class="zoehis_multiple_select" tabindex="-1" @focus="multipleFocus" @blur="multipleBlur"
    :style="{width:width+(isWidthNum?'px':'')}" :class="{zoehis_multiple_disabled:disabled}"
  >
    <div class="zoehis_multiple_input_box" ref="reference" tabindex="-1" @focus="handleFocusBox"
         :title="selectTitle" :class="{zoehis_multiple_line:!selected.length && disabled}"
    >
      <div
        :contenteditable="contenteditable" ref="multipleInput" tabindex="-1" :disabled="disabled"
        v-for="(d,index) in selected" :key="d[itemcode]" :class="{selected:index==focusIndex}"
        class="zoehis_multiple_input_item" unselectable="on" @blur="multipleBlur"
        @focus="handleFocus(d,index)" @keydown="handleKeydown($event,d,index)"
      >{{d[itemtext]}}{{index+1 < selected.length? ";":""}}
      </div>
      <input ref="multipleNoneInput" class="zoehis_multiple_input_none" v-if="!selected.length" type="text"
             @focus="multipleFocus" @blur="multipleBlur" :disabled="disabled"
             @keydown="handleKeydown($event)" :placeholder="placeholder" :ime-mode="imeMode"
      >
    </div>
    <i class="zoehis_multiple_close zoeIconfont z_close_normal" v-if="!disabled && clearable && selected.length" @click="clearEvn"></i>

    <zoehis-multiple-select-dropdown
      :selectdata="selectdata"
      :itemcode="itemcode"
      :itemtext="itemtext"
      :min-width="inputWidth"
      :max-width="dropdownMaxWidth"
      :render-item="renderItem"
      :no-match-text="noMatchText"
      :delay-time="delayTime"
      @scroll-to-top="scrollToTopEvn"
      @scroll-to-bottom="scrollToBottomEvn"
      @scroll-after="scrollAfterEvn"
      :prevent-over-id="preventOverId"
      :filter-row-data="filterRowData"
      @keydown.native="keyupDropdownEvn"
      @keyup.native="keyupDropupEvn"
      tabindex="-1"
      @before-select="focusIndex=-1"
      @input-focus="multipleFocus"
      @input-blur="multipleBlur"
      v-if="visible"
      ref="popper"
      @selectChange="handleSelectChange"
      :before-select="beforeSelect"
      :filter-method="filterMethod"
      :ime-mode="imeMode"
    ></zoehis-multiple-select-dropdown>


  </div>
</template>

<script type="text/ecmascript-6">
  import Emitter from '../../../src/mixins/emitter';
  import ZoehisInput from '../../input/index.js'
  import ZoehisMultipleSelectDropdown from './multiple-select-dropdown.vue'
  import {addResizeListener,removeResizeListener} from '../../../src/utils/resize-event';//resize事件(监听某个元素的)
  export default{
    name: "ZoehisMultipleSelect",
    componentName: "ZoehisMultipleSelect",
    mixins: [Emitter],
    components: {
      ZoehisInput,
      ZoehisMultipleSelectDropdown,
    },
    created: function () {

    },
    methods:{
      //清空事件
      clearEvn(){
        this.selected=[];
        this.setSelectVal([]);
        this.$emit("input",[]);
        this.$emit("change",this.selected);
        this.$nextTick(()=>{
          this.focus();
        });
      },
      //下拉框 输入框事件
      keyupDropdownEvn(){
        this.$emit("drop-keydown",event);
      },
      //下拉框 输入框事件
      keyupDropupEvn(){
        this.$emit("drop-keyup",event);
      },
      //组件聚焦的事件
      multipleFocus(){
        if(this.visibleTimer){
          clearTimeout(this.visibleTimer);
          this.visibleTimer=null;
        }
        if(this.disabled) return;
        if(!this.visible){
          this.visible=true;
          this.$nextTick(()=>{
            this.broadcast('ZoehisMultipleSelectDropdown', 'updatePopper'); //更新下拉框的位置(即创建下拉)
            this.setSelectVal(this.selected);//显示弹窗时设置选中的值
            this.setInputWidth();//设置最小宽度
          });
        }
        this.$emit("focus");//焦点聚集事件
      },
      //组件失焦事件
      multipleBlur(){
        if(this.visibleTimer){
          clearTimeout(this.visibleTimer);
        }
        if(this.visible){
          this.visibleTimer=setTimeout(()=>{
            this.visible=false;
            this.focusIndex = -1;
          },200);
        }
      },
      //隐藏当前下拉组件的弹窗
      hide(){
        this.multipleBlur();
      },
      //滚动条滚到底事件
      scrollToBottomEvn(query){
        this.$emit("scroll-to-bottom",query);
      },
      //滚动条事件
      scrollToTopEvn(query){
        this.$emit("scroll-to-top",query);
      },
      //滚动条事件
      scrollAfterEvn(query){
        this.$emit("scroll-after",query);
      },
      handleFocusBox(){
        if(this.disabled) return;
        //当前有选中的数据
        if(this.selected.length){
          this.focusInput(this.selected.length-1);
        }
      },
      handleKeydown(e,d,index){
        if(this.disabled) return;
        switch (e.keyCode){
          case 8://返回键
          case 46://delete
            if(!d) break;
            this._deleteFlag = true;
            this.deleteItem(d);
            this.$nextTick(()=>{
              this.focusInput(index-1<0?0:index-1);//设置焦点到下一位
            });
            e.preventDefault();
            break;
          case 37://left
            //选到下一位
            if(index-1>=0){
              this.focusInput(index-1);
            }
            break;
          case 39://right
            //选到下一位
            if(index+1<this.selected.length){
              this.focusInput(index+1);
            }
            break;
          case 9://tab
          case 13://enter
          case 40://down
          case 38://up
          case 17://ctrl

            break;
          default:
            //过滤 ctrl&&tab || ctrl&&enter || ctrl||C
            if(e.ctrlKey && (e.keyCode==13 || e.keyCode==9 || e.keyCode==67)){

            }else {
              this.focusIndex = -1;
              this.focusQuery();
            }
        }
      },
      //删除某一选项
      deleteItem(d){
        this.$refs.popper.removeSelectItem(d[this.itemcode]);
      },

      //值选中改变事件
      handleSelectChange(arr){
        let temArr=[];
        for(let i=0,l=arr.length; i<l; i++){
          temArr.push(Object.assign({},arr[i]));
        }
        this.$emit("input",temArr);
        this.$emit("select-change", temArr)
      },
      //输入框焦点定位
      handleFocus(d,index){
        if(this.disabled) return;
        this.focusIndex = index;
        this.multipleFocus();
      },
      //焦点定位到输入区
      focusInput(index){
        this.$refs.multipleInput[index] && this.$refs.multipleInput[index].focus();
      },
      focus(){
        if(this.selected.length){
          this.focusInput(0);
        }else {
          this.$refs.multipleNoneInput.focus();
        }
      },
      //焦点定位到搜索区
      focusQuery(){
        this.$refs.popper && this.$refs.popper.focus();
      },
      //设置弹窗的选中值
      setSelectVal(val=[]){
        let temArr=[];
        for(let i=0,l=val.length; i<l; i++){
          temArr.push(Object.assign({},val[i]));
        }
        this.$refs.popper && this.$refs.popper.setSelectVal(temArr);//显示弹窗时设置选中的值
      },

      //设置当前输入框的长度
      setInputWidth(){
        if (this.$refs.reference) {
          //先用下拉框的长度 || 输入框的长度 || 默认长度
          let temp = this.dropdownWidth || this.$refs.reference.getBoundingClientRect().width || this.width;
          this.inputWidth = this.isNumBool(temp) ? `${temp}px` : temp;
        }
      },
      //判断val 是不是数字
      isNumBool(val){
        var re = /^[0-9]+\.?[0-9]*$/; //匹配是否是数字
        return re.test(val);
      }

    },
    mounted(){
      //是百分比才添加监听元素的变化
      if(!this.isWidthNum){
        addResizeListener(this.$refs.reference,()=>{
          this.setInputWidth();
        });
      }

    },
    beforeDestroy(){
      //是百分比才移除
      if(!this.isWidthNum){
        removeResizeListener(this.$refs.reference);
      }
    },
    watch:{
      value:{
        deep:true,
        immediate:true,
        handler(val){
          if(val.length!=this.selected.length){//长度不相等 说明值变了
            this.selected=val;
            this.setSelectVal(val);
            if(this.isSecondFlag){
              this.$emit("change",this.selected);
            }
            this.isSecondFlag=true;//用于解决初始化时触发change的问题

            if(!val.length && this._deleteFlag){
              this.$nextTick(()=>{
                this._deleteFlag=false;
                this.$refs.multipleNoneInput.focus();
              });
            }
            return;
          }

          let hasFlag = true;//true 代表没有变化
          for(let i=0,l=val.length; i<l; i++){
            let isHad=false;
            for(let j=0,k=this.selected.length; j<k; j++){
              if(val[i][this.itemcode]==this.selected[j][this.itemcode]){
                isHad=true;
                break;
              }
            }
            if(!isHad){//有 没有对应的 说明选中的值变了 则需要重新设置选中值
              hasFlag=false;
              break;
            }
          }

          if(!hasFlag){//选中的值变了 则需要重新设置选中值
            this.selected=val;
            this.setSelectVal(val);
            if(this.isSecondFlag){
              this.$emit("change",this.selected);
            }
            this.isSecondFlag=true;//用于解决初始化时触发change的问题
          }

        }
      },
      inputFocus(val){

//          this.$nextTick(()=>{
//            this.broadcast('ZoehisMultipleSelectDropdown', 'updatePopper'); //更新下拉框的位置(即创建下拉)
//            this.setSelectVal(this.selected);//显示弹窗时设置选中的值
//          });

      },
      visible(val){
        this.$emit("visible-change",val);
      }
    },
    computed:{
      //宽度是否是数字
      isWidthNum(){
        return this.isNumBool(this.width);
      },
      //选中的title
      selectTitle(){
        let title="";
        for(let i=0,l=this.selected.length; i<l; i++){
          title=title+this.selected[i][this.itemtext]+";";
        }
        return title;
      },
      //输入框是否可以编辑
      contenteditable(){
        return !this.disabled;
      },
    },
    data() {
      return {
        selected:[],//当前选中的值
        focusIndex:-1,//焦点的位置
        inputWidth: 0,//输入框的宽度
        visible:false,  //是否显示下拉框

      }
    },
    props: {
      value: {//输入框 v-model的值[]
        required: true
      },
      selectdata:{//下拉的所有数据
        type:Array,
        default(){
          return  [];
        }
      },
      placeholder: {//默认显示的提示文字
        type: String,
        default:""
      },
      itemcode:{ //下拉的选项的唯一值
        type:String,
        default:"id"
      },
      itemtext:{//下拉选项的文本值
        type:String,
        default:"text"
      },
      itemkey:{ //下拉的选项的唯一值
        type:String,
        default(){
          return this.itemcode;
        }
      },
      filterField:{//需要过滤的几个字段  默认为：名称、code值、拼音码、五笔码
        type:Array,
        default(){
          return [this.itemtext,this.itemcode,"spellCode","wbzxCode"];
        }
      },
      dropdownWidth:[Number,String],//下拉框的宽度
      dropdownMaxWidth:{//下拉框的最大宽度
        type:[Number,String],
        default:'500px'
      },
      width:{//下拉的宽度
        type:[Number,String],
        default:165
      },
      renderItem:Function,
      disabled:Boolean,//是否可以编辑 true代表不可以编辑，false代表可以编辑
      noMatchText: String,
      defaultfirst:{
        type:Boolean,
        default:true
      },
      delayTime: {    //模糊查询的延时时间（只对filterMethod方法有效）
        type:[String,Number],
        default:200
      },
      filterMethod: Function,//自定模糊查询回调
      preventOverId:{
        type:String,
        default:""
      },
      clearable: {  //是否支持XX清空功能
        type: Boolean,
        default: true
      },
      filterRowData: Function,//自定义显示的内容
      beforeSelect: Function,//选中前钩子
      imeMode:{   //原始属性 ime-mode （输入法引擎）
        type:String,
        default:"auto"
      },




    }
  }
</script>



