<template>
  <div class="zoehis_date_editor_box" tabindex="-1" @focusin="editorFocusEvn" @focusout="editorFocusoutEvn">
    <div class="zoehis_date_editor_box_inner">
      <div class="zoehis_date_editor_item" v-if="dateobj.year.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_year" check-type="digit" :digit-length="4"
          @keydown.native.tab.prevent
          @keydown.native.left.prevent @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('year')"
          @keydown.native.down.prevent="editorKeyDownEvn('year')"
          @keyup.native="keyupEvn('year')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('year')"
          v-model="dateobj.year.value" :clearable="false" :width="60">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.years}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.month.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_month" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('month')"
          @keydown.native.down.prevent="editorKeyDownEvn('month')"
          @keyup.native="keyupEvn('month')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('month')"
          v-model="dateobj.month.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.months}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.date.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_date" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('date')"
          @keydown.native.down.prevent="editorKeyDownEvn('date')"
          @keyup.native="keyupEvn('date')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('date')"
          v-model="dateobj.date.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.days}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.hour.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_hour" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('hour')"
          @keydown.native.down.prevent="editorKeyDownEvn('hour')"
          @keyup.native="keyupEvn('hour')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('hour')"
          v-model="dateobj.hour.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.hours}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.minute.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_minute" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('minute')"
          @keydown.native.down.prevent="editorKeyDownEvn('minute')"
          @keyup.native="keyupEvn('minute')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('minute')"
          v-model="dateobj.minute.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.minutes}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.seconds.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_seconds" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('seconds')"
          @keydown.native.down.prevent="editorKeyDownEvn('seconds')"
          @keyup.native="keyupEvn('seconds')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('seconds')"
          v-model="dateobj.seconds.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.seconds}}</span>
      </div>
    </div>
    <div v-if="mode==='range'" class="zoehis_date_editor_box_inner">
      <div class="zoehis_date_editor_item" v-if="dateobj.year_end.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_year_end" check-type="digit" :digit-length="4"
          @keydown.native.tab.prevent
          @keydown.native.left.prevent @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('year_end','end')"
          @keydown.native.down.prevent="editorKeyDownEvn('year_end','end')"
          @keyup.native="keyupEvn('year_end','end')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('year_end')"
          v-model="dateobj.year_end.value" :clearable="false" :width="60">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.years}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.month_end.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_month_end" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('month_end','end')"
          @keydown.native.down.prevent="editorKeyDownEvn('month_end','end')"
          @keyup.native="keyupEvn('month_end','end')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('month_end')"
          v-model="dateobj.month_end.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.months}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.date_end.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_date_end" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('date_end','end')"
          @keydown.native.down.prevent="editorKeyDownEvn('date_end','end')"
          @keyup.native="keyupEvn('date_end','end')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('date_end')"
          v-model="dateobj.date_end.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.days}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.hour_end.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_hour_end" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('hour_end','end')"
          @keydown.native.down.prevent="editorKeyDownEvn('hour_end','end')"
          @keyup.native="keyupEvn('hour_end','end')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('hour_end')"
          v-model="dateobj.hour_end.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.hours}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.minute_end.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_minute_end" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('minute_end','end')"
          @keydown.native.down.prevent="editorKeyDownEvn('minute_end','end')"
          @keyup.native="keyupEvn('minute_end','end')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('minute_end')"
          v-model="dateobj.minute_end.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.minutes}}</span>
      </div>
      <div class="zoehis_date_editor_item" v-if="dateobj.seconds_end.visible">
        <zoehis-input
          class="zoehis_date_edit_inner"
          ref="editor_seconds_end" check-type="digit" :digit-length="2"
          @keydown.native.tab.prevent @keydown.native.left.prevent
          @keydown.native.right.prevent
          @keydown.native.up.prevent="editorKeyUpEvn('seconds_end','end')"
          @keydown.native.down.prevent="editorKeyDownEvn('seconds_end','end')"
          @keyup.native="keyupEvn('seconds_end','end')" :ime-mode="imeMode"
          @inputnative="autoCheckEvn('seconds_end')"
          v-model="dateobj.seconds_end.value" :clearable="false" :width="41">
        </zoehis-input>
        <span class="zoehis_date_text">{{i18nAttr.seconds}}</span>
      </div>
    </div>
    <div class="zoehis_date_editor_box_btn">
      <span class="zoehis_date_editor_clear" @click="clearEvn">{{i18nAttr.clearButtonText}}</span>
      <zoehis-button size="small" @clickenter="confirmEvn('confirm')">{{i18nAttr.confirmButtonText}}</zoehis-button>
    </div>
  </div>
</template>
<script type="text/babel">
  import Popper from '../../../src/utils/vue-popper';
  import { getDayCountOfMonth } from '../../../src/utils/util';
  export default {
    name: 'ZoehisDateEditorBox',
    componentName: 'ZoehisDateEditorBox',
    mixins: [Popper],
    data(){
      return {
        dateobj:{
          year:{value:"2018",visible:true,next:"month",prev:""},
          month:{value:"12",visible:true,next:"date",prev:"year"},
          date:{value:"12",visible:true,next:"hour",prev:"month"},
          hour:{value:"12",visible:true,next:"minute",prev:"date"},
          minute:{value:"12",visible:true,next:"seconds",prev:"hour"},
          seconds:{value:"12",visible:true,next:"year_end",prev:"minute"},
          year_end:{value:"2018",visible:true,next:"month_end",prev:"seconds"},
          month_end:{value:"12",visible:true,next:"date_end",prev:"year_end"},
          date_end:{value:"12",visible:true,next:"hour_end",prev:"month_end"},
          hour_end:{value:"12",visible:true,next:"minute_end",prev:"date_end"},
          minute_end:{value:"12",visible:true,next:"seconds_end",prev:"hour_end"},
          seconds_end:{value:"12",visible:true,next:"",prev:"minute_end"}
        },
      }
    },
    props:{
      placement: {
        default: 'bottom-start'
      },
      boundariesPadding: {
        default: 0
      },
      popperOptions: {
        default() {
          return {
            forceAbsolute: true,
            gpuAcceleration: false
          };
        }
      },
      preventOverId:{
        type:String,
        default:""
      },
      format: {
        type:String,default:"yyyy-MM-dd HH:mm:ss"
      },
      value: {
        required: true
      },
      initfocus:String,
      endfocus:{type:String,default:"seconds"},//按 enter、tab 时，到哪个输入框就跳出来
      nowtime:Boolean,
      autocheck:Boolean,
      imeMode:{   //原始属性 ime-mode （输入法引擎）
        type:String,
        default:"disabled"
      },
      mode: {//日期控件的模式（正常和显示范围）
        type:String,
        default:'normal'
      },
    },
    methods:{
      //keyup 事件
      keyupEvn(type){
        let keyCode = event.keyCode;
        if(event.ctrlKey && (keyCode==9 || keyCode==13)){
          this.$emit('after-prev');
          return;
        }

        switch (keyCode){
          case 9://tab
            this.tabEvn(type);
            break;
          case 13://enter
            this.enterEvn(type);
            break;
          case 39://right
            this.rightNextEvn(type);
            break;
          case 37://left
            this.leftPrevEvn(type);
            break;
        }
      },
      //设置每一项的属性
      setDateItemAttr(){

        this.dateobj.year.visible = this.format.indexOf("yyyy")!=-1;
        this.dateobj.month.visible = this.format.indexOf("MM")!=-1;
        this.dateobj.date.visible = this.format.indexOf("dd")!=-1;
        this.dateobj.hour.visible = this.format.indexOf("HH")!=-1;
        this.dateobj.minute.visible = this.format.indexOf("mm")!=-1;
        this.dateobj.seconds.visible = this.format.indexOf("ss")!=-1;
        this.dateobj.year_end.visible = this.format.indexOf("yyyy")!=-1;
        this.dateobj.month_end.visible = this.format.indexOf("MM")!=-1;
        this.dateobj.date_end.visible = this.format.indexOf("dd")!=-1;
        this.dateobj.hour_end.visible = this.format.indexOf("HH")!=-1;
        this.dateobj.minute_end.visible = this.format.indexOf("mm")!=-1;
        this.dateobj.seconds_end.visible = this.format.indexOf("ss")!=-1;
      },
      //确认按钮事件
      confirmEvn(type='confirm'){
        let curDate = new Date();
        let year = curDate.getFullYear(),month = curDate.getMonth()+1,date = curDate.getDate(),
          hour = curDate.getHours(),minute = curDate.getMinutes(),seconds = curDate.getSeconds();
        //处理年
        if((""+this.dateobj.year.value).length>2){
          year = this.dateobj.year.value;
        }
        this.dateobj.year.value = ""+year;

        //处理月
        if(this.dateobj.month.value){
          month = parseInt(this.dateobj.month.value)%12 || 12;
        }
        this.dateobj.month.value = month>=10? month : "0"+month;

        //处理日
        if(this.dateobj.date.value){
          let dayCount = getDayCountOfMonth(parseInt(year),parseInt(month)-1);//根据年月 获取 一个月共有多少天
          date = parseInt(this.dateobj.date.value)%dayCount || dayCount;
        }
        this.dateobj.date.value = date>=10? date : "0"+date;

        //处理小时
        if(this.dateobj.hour.value){
          hour = parseInt(this.dateobj.hour.value)%24;
        }
        this.dateobj.hour.value = hour>=10? hour : "0"+hour;
        //处理分钟
        if(this.dateobj.minute.value){
          minute = parseInt(this.dateobj.minute.value)%60;
        }
        this.dateobj.minute.value = minute>=10? minute : "0"+minute;
        //处理秒
        if(this.dateobj.seconds.value){
          seconds = parseInt(this.dateobj.seconds.value)%60;
        }
        this.dateobj.seconds.value = seconds>=10? seconds : "0"+seconds;

        if(this.mode === 'range'){
            let year_end = curDate.getFullYear(),month_end = curDate.getMonth()+1,date_end = curDate.getDate(),
                hour_end = curDate.getHours(),minute_end = curDate.getMinutes(),seconds_end = curDate.getSeconds();
            //处理年
            if((""+this.dateobj.year_end.value).length>2){
                year_end = this.dateobj.year_end.value;
            }
            this.dateobj.year_end.value = ""+year_end;

            //处理月
            if(this.dateobj.month_end.value){
                month_end = parseInt(this.dateobj.month_end.value)%12 || 12;
            }
            this.dateobj.month_end.value = month_end>=10? month_end : "0"+month_end;

            //处理日
            if(this.dateobj.date_end.value){
                let dayCount_end = getDayCountOfMonth(parseInt(year_end),parseInt(month_end)-1);//根据年月 获取 一个月共有多少天
                date_end = parseInt(this.dateobj.date_end.value)%dayCount_end || dayCount_end;
            }
            this.dateobj.date_end.value = date_end>=10? date_end : "0"+date_end;

            //处理小时
            if(this.dateobj.hour_end.value){
                hour_end = parseInt(this.dateobj.hour_end.value)%24;
            }
            this.dateobj.hour_end.value = hour_end>=10? hour_end : "0"+hour_end;
            //处理分钟
            if(this.dateobj.minute_end.value){
                minute_end = parseInt(this.dateobj.minute_end.value)%60;
            }
            this.dateobj.minute_end.value = minute_end>=10? minute_end : "0"+minute_end;
            //处理秒
            if(this.dateobj.seconds_end.value){
                seconds_end = parseInt(this.dateobj.seconds_end.value)%60;
            }
            this.dateobj.seconds_end.value = seconds_end>=10? seconds_end : "0"+seconds_end;

            let dateStart = (this.dateobj.year.value) + "-" + (this.dateobj.month.value) +"-"+
                (this.dateobj.date.value)+ " " +(this.dateobj.hour.value) + ":" +
                (this.dateobj.minute.value)+ ":" +(this.dateobj.seconds.value);
            let dateEnd = (this.dateobj.year_end.value) + "-" + (this.dateobj.month_end.value) +"-"+
                (this.dateobj.date_end.value)+ " " +(this.dateobj.hour_end.value) + ":" +
                (this.dateobj.minute_end.value)+ ":" +(this.dateobj.seconds_end.value);
            dateStart = new Date(dateStart);
            dateEnd = new Date(dateEnd);
            let dateArr = [dateStart,dateEnd];
            this.$emit("save",dateArr,type);
            return;
        }
        let dateStr = (this.dateobj.year.value) + "-" + (this.dateobj.month.value) +"-"+
          (this.dateobj.date.value)+ " " +(this.dateobj.hour.value) + ":" +
          (this.dateobj.minute.value)+ ":" +(this.dateobj.seconds.value);
        this.$emit("save",new Date(dateStr),type);
      },
      clearEvn(){
        this.$emit("save","","clear");
      },

      //keydown事件 up
      editorKeyUpEvn(type){
        switch (type){
          case "year"://年
          case "year_end":
            let year = parseInt(this.dateobj[type].value||0);
            if(year>=9999){
                this.dateobj[type].value = "9999";
            }else {
                this.dateobj[type].value = ""+(++year);
            }
            break;
          case "month"://月
          case "month_end":
            let month = parseInt(this.dateobj[type].value||0);
            ++month;
            if(month<1 || month>12){
                month="01";
            }else if(month<10){
                month = "0" + month;
            }
            this.dateobj[type].value = ""+month;
            break;
          case "date"://天
          case "date_end":
            let date = parseInt(this.dateobj[type].value||0);
            ++date;
            if(date<1 || date>31){
                date="01";
            }else if(date<10){
                date = "0" + date;
            }
            this.dateobj[type].value = ""+date;
            break;

          case "hour"://时
          case "hour_end":
            let hour = parseInt(this.dateobj[type].value||0);
            ++hour;
            if(hour<1 || hour>23){
                hour="00";
            }else if(hour<10){
                hour = "0" + hour;
            }
            this.dateobj[type].value = ""+hour;
            break;
          case "minute"://分
          case "minute_end":
            let minute = parseInt(this.dateobj[type].value||0);
            ++minute;
            if(minute<1 || minute>59){
                minute="00";
            }else if(minute<10){
                minute = "0" + minute;
            }
            this.dateobj[type].value = ""+minute;
            break;
          case "seconds"://秒
          case "seconds_end":
            let seconds = parseInt(this.dateobj[type].value||0);
            ++seconds;
            if(seconds<1 || seconds>59){
                seconds="00";
            }else if(seconds<10){
                seconds = "0" + seconds;
            }
            this.dateobj[type].value = ""+seconds;
            break;
        }
        this.selectInput(type);
      },
      //keydown事件 down
      editorKeyDownEvn(type){
        switch (type){
          case "year"://年
          case "year_end":
            let year = parseInt(this.dateobj[type].value||0);
            if(year<=100){
                this.dateobj[type].value = "100";
            }else {
                this.dateobj[type].value = ""+(--year);
            }
            break;
          case "month"://月
          case "month_end":
            let month = parseInt(this.dateobj[type].value||0);
            --month;
            if(month<1 || month>12){
                month="12";
            }else if(month<10){
                month = "0" + month;
            }
            this.dateobj[type].value = ""+month;
            break;
          case "date"://天
          case "date_end":
            let date = parseInt(this.dateobj[type].value||0);
            --date;
            if(date<1 || date>31){
                date="31";
            }else if(date<10){
                date = "0" + date;
            }
            this.dateobj[type].value = ""+date;
            break;
          case "hour"://时
          case "hour_end":
            let hour = parseInt(this.dateobj[type].value||0);
            --hour;
            if(hour<0 || hour>23){
                hour="23";
            }else if(hour<10){
                hour = "0" + hour;
            }
            this.dateobj[type].value = ""+hour;
            break;
          case "minute"://分
          case "minute_end":
            let minute = parseInt(this.dateobj[type].value||0);
            --minute;
            if(minute<0 || minute>59){
                minute="59";
            }else if(minute<10){
                minute = "0" + minute;
            }
            this.dateobj[type].value = ""+minute;
            break;
          case "seconds"://秒
          case "seconds_end":
            let seconds = parseInt(this.dateobj[type].value||0);
            --seconds;
            if(seconds<0 || seconds>59){
                seconds="59";
            }else if(seconds<10){
                seconds = "0" + seconds;
            }
            this.dateobj[type].value = ""+seconds;
            break;
        }
        this.selectInput(type);
      },
      //全选某个输入框
      //type:类型
      selectInput(type="year"){

        this.$nextTick(()=>{

          let curVm = this.findTheRefVm(type);
          if(!curVm) return;
          curVm.inputSelect();
        });
      },
      //找到根据type 找到 对应的vm对象，当前找不到 则找下一个
      findTheRefVm(type){
        if(!type) return;
        let curObj = this.dateobj[type];
        if(!curObj) return;
        if(curObj.visible && this.$refs[`editor_${type}`]) return this.$refs[`editor_${type}`];
        return this.findTheRefVm(curObj.next);
      },
      editorFocusEvn(){
        if(this.blurtimer){
          clearTimeout(this.blurtimer);
          this.blurtimer=null;
        }
      },
      editorFocusoutEvn(){
        if(this.blurtimer){
          clearTimeout(this.blurtimer);
        }
        this.blurtimer=setTimeout(()=>{
          this.$emit("box-blur");
        },20);
      },
      //tab 事件
      tabEvn(type){
        let curVm = this.findTheRefVm(this.dateobj[type].next);
        if(type==this.endfocus || !curVm){
          this.$emit("tab-end");
          return;
        }
        curVm.inputSelect();
      },
      //按左键 事件
      leftPrevEvn(type){
        let curVm = this.findTheRefVm(this.dateobj[type].prev);
        if(!curVm){
          this.selectInput(type);
          return;
        }
        curVm.inputSelect();
      },
      //按右键 事件
      rightNextEvn(type){
        let curVm = this.findTheRefVm(this.dateobj[type].next);
        if(!curVm){
          this.selectInput(type);
          return;
        }
        curVm.inputSelect();
      },
      //enter 事件
      enterEvn(type){
        let curVm = this.findTheRefVm(this.dateobj[type].next);
        if(type==this.endfocus || !curVm){
          this.confirmEvn("enter");
          return;
        }
        curVm.inputSelect();
      },
      //自动校验并跳转到下一个输入框
      autoCheckEvn(type){
        if(!this.autocheck)return;
        let skipFlag=false;
        switch (type){
          case "year"://年
            if((""+this.dateobj.year.value).length>3){
              skipFlag=true;
            }else{
              this.$emit("change-tip");
            }
            break;
          case "year_end"://年
            if((""+this.dateobj.year_end.value).length>3){
              skipFlag=true;
            }else{
              this.$emit("change-tip");
            }
            break;
          case "month"://月
            if(this.dateobj.month.value>12){this.dateobj.month.value=12}
            if((""+this.dateobj.month.value).length==1&&this.dateobj.month.value>=2){
              this.dateobj.month.value='0'+this.dateobj.month.value;
              skipFlag=true;
            }
            if((""+this.dateobj.month.value).length>1){
              if(this.dateobj.month.value>0){skipFlag=true;}
            }else{
              this.$emit("change-tip");
            }
            break;
          case "month_end"://月
            if(this.dateobj.month_end.value>12){this.dateobj.month_end.value=12}
            if((""+this.dateobj.month_end.value).length==1&&this.dateobj.month_end.value>=2){
              this.dateobj.month_end.value='0'+this.dateobj.month_end.value;
              skipFlag=true;
            }
            if((""+this.dateobj.month_end.value).length>1){
              if(this.dateobj.month_end.value>0){skipFlag=true;}
            }else{
              this.$emit("change-tip");
            }
            break;
          case "date"://日
            if((""+this.dateobj.date.value).length==1&&this.dateobj.date.value>3){
              this.dateobj.date.value='0'+this.dateobj.date.value;
              skipFlag=true;
            }
            var countDayOfMonth = this.getDayCountOfMonth(this.dateobj.year.value,this.dateobj.month.value-1);//得到该年月有几天
            if(this.dateobj.date.value>countDayOfMonth){this.dateobj.date.value=countDayOfMonth}
            if((""+this.dateobj.date.value).length>1){
              if(this.dateobj.date.value>0){skipFlag=true;}
            }else{
              this.$emit("change-tip");
            }
            break;
          case "date_end"://日
            if((""+this.dateobj.date_end.value).length==1&&this.dateobj.date_end.value>3){
              this.dateobj.date_end.value='0'+this.dateobj.date_end.value;
              skipFlag=true;
            }
            var countDayOfMonth = this.getDayCountOfMonth(this.dateobj.year_end.value,this.dateobj.month_end.value-1);//得到该年月有几天
            if(this.dateobj.date_end.value>countDayOfMonth){this.dateobj.date_end.value=countDayOfMonth}
            if((""+this.dateobj.date_end.value).length>1){
              if(this.dateobj.date_end.value>0){skipFlag=true;}
            }else{
              this.$emit("change-tip");
            }
            break;
          case "hour"://时
            if((""+this.dateobj.hour.value).length==1&&this.dateobj.hour.value>2){
              this.dateobj.hour.value='0'+this.dateobj.hour.value;
              skipFlag=true;
            }
            if(this.dateobj.hour.value>24){this.dateobj.hour.value=24}
            if((""+this.dateobj.hour.value).length>1){
              skipFlag=true;
            }else{
              this.$emit("change-tip");
            }
            break;
          case "hour_end"://时
            if((""+this.dateobj.hour_end.value).length==1&&this.dateobj.hour_end.value>2){
              this.dateobj.hour_end.value='0'+this.dateobj.hour_end.value;
              skipFlag=true;
            }
            if(this.dateobj.hour_end.value>24){this.dateobj.hour_end.value=24}
            if((""+this.dateobj.hour_end.value).length>1){
              skipFlag=true;
            }else{
              this.$emit("change-tip");
            }
            break;
          case "minute"://分
            if((""+this.dateobj.minute.value).length==1&&this.dateobj.minute.value>6){
              this.dateobj.minute.value='0'+this.dateobj.minute.value;
              skipFlag=true;
            }
            if(this.dateobj.minute.value>60){this.dateobj.minute.value=60}
            if((""+this.dateobj.minute.value).length>1){
              skipFlag=true;
            }else{
              this.$emit("change-tip");
            }
            break;
          case "minute_end"://分
            if((""+this.dateobj.minute_end.value).length==1&&this.dateobj.minute_end.value>6){
              this.dateobj.minute_end.value='0'+this.dateobj.minute_end.value;
              skipFlag=true;
            }
            if(this.dateobj.minute_end.value>60){this.dateobj.minute_end.value=60}
            if((""+this.dateobj.minute_end.value).length>1){
              skipFlag=true;
            }else{
              this.$emit("change-tip");
            }
            break;
          case "seconds"://秒
            if((""+this.dateobj.seconds.value).length==1&&this.dateobj.seconds.value>6){
              this.dateobj.seconds.value='0'+this.dateobj.seconds.value;
              skipFlag=true;
            }
            if(this.dateobj.seconds.value>60){
              this.dateobj.seconds.value=60
            }
            if((""+this.dateobj.seconds.value).length>1){
              skipFlag=true;
            }else{
              this.$emit("change-tip");
            }
            break;
          case "seconds_end"://秒
            if((""+this.dateobj.seconds_end.value).length==1&&this.dateobj.seconds_end.value>6){
              this.dateobj.seconds_end.value='0'+this.dateobj.seconds_end.value;
              skipFlag=true;
            }
            if(this.dateobj.seconds_end.value>60){
              this.dateobj.seconds_end.value=60
            }
            if((""+this.dateobj.seconds_end.value).length>1){
              skipFlag=true;
            }else{
              this.$emit("change-tip");
            }
            break;
        }
        let curVm = this.findTheRefVm(this.dateobj[type].next);
        if(!skipFlag)return;
        if(type==this.endfocus || !curVm){
          this.confirmEvn("enter");
          return;
        }
        curVm.inputSelect();
      },
      //得到该月有几天（传入年份如1999，月份如11--传月份要减1）
      getDayCountOfMonth: function(year, month) {
        if (month === 3 || month === 5 || month === 8 || month === 10) {
          return 30;
        }
        if (month === 1) {
          if (year % 4 === 0 && year % 100 !== 0 || year % 400 === 0) {
            return 29;
          } else {
            return 28;
          }
        }
        return 31;
      },
    },
    computed:{

    },
    watch:{
      //监听v-model传进来的值
      value:{
        immediate:true,//第一次的时候会立即执行，而不是value变化的时候才执行
        handler(val){
          if(this.nowtime && !val) {//是否要初始化为当前时间
              this.mode==='range'? val=[new Date(),new Date()] : val=new Date();
          }
          if(!val){
            this.dateobj.year.value = "";
            this.dateobj.month.value = "";
            this.dateobj.date.value = "";
            this.dateobj.hour.value = "";
            this.dateobj.minute.value = "";
            this.dateobj.seconds.value = "";
            this.dateobj.year_end.value = "";
            this.dateobj.month_end.value = "";
            this.dateobj.date_end.value = "";
            this.dateobj.hour_end.value = "";
            this.dateobj.minute_end.value = "";
            this.dateobj.seconds_end.value = "";
          }else {
            if(this.mode === 'range'){
                let year = val[0].getFullYear();
                let month = String(1+val[0].getMonth()).length<2 ? "0"+String(val[0].getMonth()+1) : val[0].getMonth()+1;
                let date = (""+val[0].getDate()).length<2 ? ("0"+val[0].getDate()) : val[0].getDate();
                let hour = (""+val[0].getHours()).length<2 ? ("0"+val[0].getHours()) : val[0].getHours();
                let minute = (""+val[0].getMinutes()).length<2 ? ("0"+val[0].getMinutes()) : val[0].getMinutes();
                let seconds = (""+val[0].getSeconds()).length<2 ? ("0"+val[0].getSeconds()) : val[0].getSeconds();

                let year_end = val[1].getFullYear();
                let month_end = String(1+val[1].getMonth()).length<2 ? "0"+String(val[1].getMonth()+1) : val[1].getMonth()+1;
                let date_end = (""+val[1].getDate()).length<2 ? ("0"+val[1].getDate()) : val[1].getDate();
                let hour_end = (""+val[1].getHours()).length<2 ? ("0"+val[1].getHours()) : val[1].getHours();
                let minute_end = (""+val[1].getMinutes()).length<2 ? ("0"+val[1].getMinutes()) : val[1].getMinutes();
                let seconds_end = (""+val[1].getSeconds()).length<2 ? ("0"+val[1].getSeconds()) : val[1].getSeconds();

                this.dateobj.year.value = ""+year;
                this.dateobj.month.value = ""+month;
                this.dateobj.date.value = ""+date;
                this.dateobj.hour.value = ""+hour;
                this.dateobj.minute.value = ""+minute;
                this.dateobj.seconds.value = ""+seconds;
                this.dateobj.year_end.value = ""+year_end;
                this.dateobj.month_end.value = ""+month_end;
                this.dateobj.date_end.value = ""+date_end;
                this.dateobj.hour_end.value = ""+hour_end;
                this.dateobj.minute_end.value = ""+minute_end;
                this.dateobj.seconds_end.value = ""+seconds_end;
            }else {
                let year = val.getFullYear();
                let month = String(1 + val.getMonth()).length < 2 ? "0" + String(val.getMonth() + 1) : val.getMonth() + 1;
                let date = ("" + val.getDate()).length < 2 ? ("0" + val.getDate()) : val.getDate();
                let hour = ("" + val.getHours()).length < 2 ? ("0" + val.getHours()) : val.getHours();
                let minute = ("" + val.getMinutes()).length < 2 ? ("0" + val.getMinutes()) : val.getMinutes();
                let seconds = ("" + val.getSeconds()).length < 2 ? ("0" + val.getSeconds()) : val.getSeconds();

                this.dateobj.year.value = "" + year;
                this.dateobj.month.value = "" + month;
                this.dateobj.date.value = "" + date;
                this.dateobj.hour.value = "" + hour;
                this.dateobj.minute.value = "" + minute;
                this.dateobj.seconds.value = "" + seconds;
            }
          }
        }
      },
    },
    created(){
      if(this.$t && (typeof this.$t == "function")){
        this.i18nAttr={
          confirmButtonText:this.$t("zoehisUI.dataEditor.confirmButtonText"),
          clearButtonText:this.$t("zoehisUI.dataEditor.clearButtonText"),
          seconds:this.$t("zoehisUI.dataEditor.seconds"),
          minutes:this.$t("zoehisUI.dataEditor.minutes"),
          hours:this.$t("zoehisUI.dataEditor.hours"),
          days:this.$t("zoehisUI.dataEditor.days"),
          months:this.$t("zoehisUI.dataEditor.months"),
          years:this.$t("zoehisUI.dataEditor.years"),
        }
      }else {
        this.i18nAttr={
          confirmButtonText:"确认",
          clearButtonText:"清除",
          seconds:"秒",
          minutes:"分",
          hours:"时",
          days:"日",
          months:"月",
          years:"年",
        }
      }
    },
    mounted() {
      this.setDateItemAttr();
      this.referenceElm = this.$parent.$refs.reference.$el; //父组件的input输入框的dom元素
      this.$parent.popperElm = this.popperElm = this.$el;
      this.$on('updatePopper', () => {
        if (this.$parent.visible) this.updatePopper();
      });
    },
  };
</script>



